<template>
    <div>
        <el-form :inline="true" :model="form">
            <el-button type="warning" size="small" @click="exportTeamInfos" v-if="isTeamDetail" :loading="loading">导出全部查询数据</el-button>
            <div style="margin-top: 15px">
                <el-form-item label="所属机构" prop="bankid">
                    <div style="display: flex;">
                        <el-input readonly="" size="small" v-model="bankname"></el-input>
                        <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
                    </div>
                </el-form-item>

                <el-form-item label="团队/片区编号">
                    <el-input
                            v-model="form.branch_id"
                            clearable
                            placeholder="请输入团队/片区编号"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="团队/片区名称">
                    <el-input
                            v-model="form.branch_name"
                            clearable
                            placeholder="请输入团队/片区名称"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="团队/片区类型" prop="team_flag">
                    <el-select v-model="form.team_flag" clearable size="small">
                        <el-option
                                key="1"
                                label="团队"
                                value="1"
                        ></el-option>
                        <el-option
                                key="2"
                                label="片区"
                                value="2"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button
                            type="primary"
                            @click="getTeamList"
                            size="small"
                            icon="el-icon-search"
                    >搜索
                    </el-button
                    >
                </el-form-item>
            </div>
        </el-form>
        <base-table :list="dataList" :data="teamList">
            <el-table-column fixed="right" label="操作" align="center">
                <template slot-scope="scope">
                    <template>

                    <el-dropdown placement="bottom" v-if="!isTeamDetail">
                        <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="beforeEdit(scope.row.approval_status, scope.row.team_show)"
                    >操作
                    </el-button
                    ><i  class="el-icon-arrow-down el-icon--right"></i>
                       <el-dropdown-menu slot="dropdown">
                           <el-dropdown-item @click.native="editTeam(scope.row.id, false, type010)" v-if="scope.row.approval_status=='2' && scope.row.team_show=='0' "
                           >团队基本信息调整</el-dropdown-item
                           >
                           <el-dropdown-item @click.native="editTeam(scope.row.id, false, type011)" v-if="scope.row.approval_status=='2' && scope.row.team_show=='0' "
                           >团队类型调整</el-dropdown-item
                         >
                           <el-dropdown-item @click.native="editTeam(scope.row.id, false, type012)" v-if="scope.row.approval_status=='2' && scope.row.team_show=='0' "
                           >团队所属机构调整</el-dropdown-item
                         >
                          <el-dropdown-item @click.native="editTeam(scope.row.id, false, type013)" v-if="scope.row.approval_status=='2' && scope.row.team_show=='0' "
                           >团队负责人调整</el-dropdown-item
                         >
                           <el-dropdown-item @click.native="deleteTeam(scope.row.id, false, type020)" v-if="scope.row.approval_status=='2' && scope.row.team_show=='0' "
                           >删除团队</el-dropdown-item
                           >
                       </el-dropdown-menu>
                     </el-dropdown>
                    </template>
                    <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="editTeam(scope.row.id,true)"
                            v-if="isTeamDetail"
                    >查看
                    </el-button
                    >
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";

    export default {
        components: {
            baseTable,
            basePage,
            BranchTreePicker
        },
        name: "team",
        data() {
            return {
                user: JSON.parse(sessionStorage.getItem("user")),
                role_id: sessionStorage.getItem("role_id"),
                role: sessionStorage.getItem("role"),
                form: {},
                bankid:"",
                bankname:"",
                dataList: [
                    {
                        label: "团队/片区编号",
                        prop: "branch_id",
                        sort: true,
                    },
                    {
                        label: "团队/片区名称",
                        prop: "branch_name",
                        sort: true,
                    },
                    {
                        label: "团队/片区类型",
                        prop: "team_flag",
                    },
                    {
                        label: "资产余额（元）",
                        prop: "amt1",
                    },
                    {
                        label: "资产年日均（元）",
                        prop: "amt2",
                    },
                    {
                        label: "存款余额（元）",
                        prop: "amt3",
                    },
                    {
                        label: "存款年日均（元）",
                        prop: "amt4",
                    },
                    {
                        label: "试营业服务点数",
                        prop: "site_num1",
                    },
                    {
                        label: "开业服务点数",
                        prop: "site_num2",
                    },
                    {
                        label: "客户数",
                        prop: "cus_num1",
                    },
                ],
                teamList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                flag: "",
                type010: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"010").code,
                type011: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"011").code,
                type012: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"012").code,
                type013: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"013").code,
                type020: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"020").code,
                loading: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
            isTeamDetail() {
                return this.$route.path == '/teamDetail'
            },
        },
        mounted() {
        },
        methods: {
            // 机构列表
            nodeselectForSerch(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },

            beforeEdit(approval_status, is_show){
                if(approval_status !== '2' || is_show !== '0'){
                    this.$message({
                        message: "暂无可做操作",
                        type: "warning",
                    });
                }
            },
            // 编辑团队
            editTeam(id, flag, approval_type) {
                this.$router.push({
                    path: "/teamAddDetail",
                    // name: "团队/片区信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },

            // 删除团队
            deleteTeam(id, flag, approval_type) {
                this.$router.push({
                    path: "/teamAddDetail",
                    // name: "团队/片区信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },

            // 获取团队列表
            getTeamList() {
                this.$api
                    .getTeamList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        is_approval : 0,
                        bankid: this.bankid,
                        ...this.form,
                    })
                    .then((res) => {
                        res.data.teamList.map((item) => {
                            return (item.team_flag = item.team_flag == "1" ? "团队" : "片区");
                        });
                        this.teamList = res.data.teamList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },

            // 导出
            exportTeamInfos() {
                this.loading = true;
                this.$api.exportTeamInfos({
                    bankid: this.bankid,
                    ...this.form
                }).then((res) => {
                    this.loading = false;
                    var fileName = res.data.fileName;
                    var filePath = res.data.filePath;
                    if ( null==fileName || null==filePath ){
                        this.$message({
                            message: "导出失败，系统异常",
                            type: "error",
                        });
                        return false;
                    }

                    var objSelector = document.createElement("form");
                    document.body.appendChild(objSelector);
                    objSelector.method = "post";
                    objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
                    //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
                    objSelector.enctype = "multipart/form-data";
                    var input01 = document.createElement("input");
                    var input02 = document.createElement("input");
                    var input03 = document.createElement("input");
                    var input04 = document.createElement("input");
                    input01.setAttribute("name", "filePath");
                    input01.setAttribute("type", "hidden");
                    input01.setAttribute("value", filePath);
                    input02.setAttribute("name", "fileName");
                    input02.setAttribute("type", "hidden");
                    input02.setAttribute("value", fileName);
                    input03.setAttribute("name", "isDownload");
                    input03.setAttribute("type", "hidden");
                    input03.setAttribute("value", "1");
                    input04.setAttribute("name", "isDelete");
                    input04.setAttribute("type", "hidden");
                    input04.setAttribute("value", "1");
                    objSelector.appendChild(input01);
                    objSelector.appendChild(input02);
                    objSelector.appendChild(input03);
                    objSelector.appendChild(input04);
                    objSelector.submit();

                    this.$message({
                        message: "导出成功",
                        type: "success",
                    });
                });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getTeamList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getTeamList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getTeamList();
            });
        },
    };
</script>



