<template>
  <div>
    <el-descriptions title="风险信息">
      <el-descriptions-item label="工单流水号">{{ risk.id }}</el-descriptions-item>
      <el-descriptions-item label="项目名称">{{ risk.project_name }}</el-descriptions-item>
      <el-descriptions-item label="分行名称">{{ risk.porgid }}</el-descriptions-item>
      <el-descriptions-item label="支行名称">{{ risk.branch_id }}</el-descriptions-item>
      <el-descriptions-item label="整改机构或站点">{{ risk.site_no }}</el-descriptions-item>
      <el-descriptions-item label="整改要求">{{ risk.requirement }}</el-descriptions-item>
      <el-descriptions-item label="问题描述">{{ risk.content_text }}</el-descriptions-item>
      <el-descriptions-item label="风险等级">{{ risk.risk_level }}</el-descriptions-item>
      <el-descriptions-item label="问题发现日期">{{ risk.created }}</el-descriptions-item>
      <el-descriptions-item label="问题发现人">{{ risk.discoverer }}</el-descriptions-item>
      <el-descriptions-item label="整改预计完成日期">{{ risk.end_date }}</el-descriptions-item>
      <el-descriptions-item label="问题整改人">{{ risk.name }}</el-descriptions-item>
    </el-descriptions>
    <el-divider></el-divider>
    <el-form ref="form" :model="risk" :rules="rules" label-width="90px" :disabled="check">
      <el-row>
        <el-col :span="7">
          <el-form-item
              prop="schedule"
              label="整改完成情况"
          >
            <el-input type="textarea" size="small"
                      v-model="risk.schedule" label="整改完成情况">
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item
              prop="written"
              label="反馈人姓名"
              style="width: 360px" disabled="true">
            <el-select v-model="risk.written" filterable clearable style="width: 150px" @click.native="getAccountList(true)">
              <el-option v-for="item in userList" :key="item.account_id" :label="item.name"
                         :value="item.account_id">
                <span>{{ item.name }}({{ item.account_id }})</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item
              prop="status"
              label="整改状态"
              style="width: 360px"
          >
            <el-select
                v-model="risk.status"
                style="width: 150px"
            >
              <el-option
                  v-for="item in items"
                  :label="item.label"
                  :key="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="整改证明" prop="risk_photo">
            <el-upload
                :class="{disUoloadSty:check}"
                action="/hnm/txCtrl?txcode=upload001"
                list-type="picture-card"
                ref="upload"
                :before-upload="beforeAvatarUpload"
                :file-list="fileList"
                :data="{business: 'risk',name: '123'}"
                :headers='{Accept:"application/json"}'
                :on-success="uploadReturn"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove"
                :limit="10">
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" style="text-align: center" v-if="!check">
      <el-button size="small" @click="handleClose">取 消</el-button>
      <el-button type="primary" size="small" @click="addOrEditRisk">确 定
      </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "riskDetail",

  data() {
    return {
      rules: {
        schedule: [{
          required: true,
          message: "整改进度不能为空",
          trigger: "change"
        },],
        status: [{
          required: true,
          message: "请选择整改状态",
          trigger: "change"
        },],
        written: [{
          required: true,
          message: "请选择反馈人姓名",
          trigger: "change"
        },],
      },
      items: [
        {
          label: '已整改',
          value: 1
        }, {
          label: '无需整改',
          value: 2
        },
        {
          label: '无法整改',
          value: 3
        },
      ],
      apply_id:'',
      req_place:'',
      check: false,
      risk: [],
      dialogVisible: false,
      dialogImageUrl: '',
      fileList:[],
      userList: [],
    }
  },
  mounted() {
    this.$api.getRiskDetail({
      apply_id: this.$route.query.apply_id,
    }).then((res) => {
      this.risk = res.data;
      if (this.risk.risk_photo) {
        this.$api.getPhotoList({"ids": this.risk.risk_photo}).then((res) => {
          this.fileList = res.data.photoList;
          this.risk.risk_photo = '';
          this.fileList.map((item) => {
            item.response = {data: [{id: item.name}]};
            this.risk.risk_photo += "'" + item.response.data[0].id + "',";
          })
          this.risk.risk_photo = this.risk.risk_photo.substr(0, this.risk.risk_photo.length - 1);

        });
      }
    });
    this.check = this.$route.query.check === 'false' ? false : Boolean(this.$route.query.check);
    this.req_place = this.$route.query.req_place ;
    this.apply_id = this.$route.query.apply_id ;
    this.getAccountList();
    this. getApprovalQY();
  },
  methods: {
    // 查询是否启用
    getApprovalQY() {
      this.$api
          .getApprovalQY({
            approval_type: '023',
          })
          .then((res) => {
            this.use_up = res.data.use_up;
          });
    },
    // 机构下用户列表
    getAccountList(flag) {
      if (flag){
        this.$api.getAccountList({"deleted": 0}).then((res) => {
          this.userList = res.data;
        })
      }else {
        this.$api.getAccountList({"all":""}).then((res) => {
          this.userList = res.data;
        })};

    },
    addOrEditRisk() {
      this.$refs.form.validate((valid) => {
        if (this.flag) {
          this.handleClose();
        } else {
          if (valid) {

            this.risk.use_up = this.use_up;
            this.risk.req_place = this.req_place;
            this.risk.apply_id = this.apply_id;
            this.risk.approval_type = '023';
            if (this.risk.status === 1) {
              if (this.risk.risk_photo === null || this.risk.risk_photo === undefined || this.risk.risk_photo.length <= 0) {
                this.$message.error("整改图片必需上传");
                return
              }
            }
            this.$api.addOrEditRisk(this.risk).then(() => {
              this.$message.success("整改成功");
              this.$router.push({
                path: "/approvalApply",
                name: "我的申请",
                query: {},
              });
            });
          }
        }
      });
    },
    handleClose() {
      this.visible = false;
      this.risk = {};
      this.fileList = [];
      this.flag = false;
      // this.getRisk()
    },
    //删除上传图片
    handleRemove(file, fileList) {
      this.form.risk_photo = '';
      fileList.map((item) => {
        this.form.risk_photo += "'" + item.response.data[0].id + "',";
      })
      this.form.risk_photo = this.form.risk_photo.substr(0, this.form.risk_photo.length - 1);
      console.log(this.form.risk_photo);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    //图片上传成功回调
    uploadReturn(response, file, fileList) {
      console.log(file)
      this.risk.risk_photo = '';
      fileList.map((item) => {
        this.risk.risk_photo += "'" + item.response.data[0].id + "',";
      })
      this.risk.risk_photo = this.risk.risk_photo.substr(0, this.risk.risk_photo.length - 1);
      console.log(this.risk.risk_photo);
    },
    beforeAvatarUpload(file) {
      if (file.type !== 'image/png' && file.type !== 'image/jpg' && file.type !== 'image/jpeg') {
        this.$message.error('不支持png、jpg、jpeg以外格式的图片')
        return false
      }
      return true
    },

  }
}
</script>

<style >
.disUoloadSty .el-upload--picture-card {
  display: none; /* 上传按钮隐藏 */
}
</style>


