<template>

  <div>
    <el-button type="warning" @click="exportExpandRiskInfos" size="small">导出数据</el-button>
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-form-item label="整改人姓名">
        <el-select
            v-model="form.responsible"
            clearable
            placeholder="请输入姓名"
            size="small"
            filterable
        >
          <el-option v-for="user in userList"
                     :value="user.account_id"
                     :key="user.account_id"
                     :label="user.name"
          >
            <span>{{ user.name }}({{ user.account_id }})</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="工单流水号">
        <el-input v-model="form.id" clearable size="small"></el-input>
      </el-form-item>
      <el-form-item label="机构或站点名称">
        <el-cascader v-model="form.site_no" :options="options"
                     filterable clearable
                     :show-all-levels="false" @change="handleChange" size="small">
        </el-cascader>
      </el-form-item>
      <el-form-item label="整改状态" prop="status">
        <el-select readonly size="small" v-model="status" clearable multiple >
          <el-option v-for="item in items"
                     :value="item.value"
                     :key="item.value"
                     :label="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
          prop="pj_classify"
          label="巡查项目"
          style="width: 360px" size="small">
        <el-select
            v-model="form.pj_classify" clearable>
          <el-option
              v-for="item in projectList"
              :label="item.project_name"
              :key="item.pj_classify"
              :value="item.pj_classify">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="问题描述" style="width: 300px">
        <el-input v-model="form.content_text" filterable clearable size="small">
        </el-input>
      </el-form-item>
      <el-form-item label="整改要求" prop="requirement" style="width: 300px">
        <el-input v-model="form.requirement" filterable clearable size="small">
        </el-input>
      </el-form-item>
      <el-form-item
          prop="risk_level"
          label="风险等级"
          style="width: 300px">
        <el-select
            v-model="form.risk_level" size="small">
          <el-option
              v-for="item in items2"
              :label="item.label"
              :key="item.label"
              :value="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="整改预计完成日期" prop="rangeDate">
        <el-date-picker v-model="dateRange" clearable type="daterange" size="small"
                        range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"
                        unlink-panels  :picker-options="pickerOptions" value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getRisk(true)"
            size="small"
            icon="el-icon-search"
        >搜索
        </el-button
        >
      </el-form-item>
    </el-form>
    <el-table style="table-layout: fixed" :data="showData" :row-class-name="tableRowClassName" border
              :row-style="{height: '80px'}" :cell-style="{padding: '0'}">
      <el-table-column label="序号" prop="index" align="center" type="index" width="50">
      </el-table-column>
      <el-table-column label="工单流水号" prop="id" align="center">
      </el-table-column>
      <el-table-column label="机构或站点名称" prop="site_no" align="center">
      </el-table-column>
      <el-table-column label="巡查项目" prop="project_name" align="center" width="110">
      </el-table-column>
      <el-table-column label="问题描述" prop="content_text" align="center" resizable>
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.content_text" placement="top">
            <div class="resAndsug" style="height: 70px">{{ scope.row.content_text }}</div>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="风险等级" prop="risk_level" align="center" width="60">
      </el-table-column>
      <el-table-column label="整改预计完成日期" prop="end_date" align="center" width="100" sortable>
      </el-table-column>
      <el-table-column label="整改要求" prop="requirement" align="center" resizable>
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.requirement" placement="top">
            <div class="resAndsug" style="height: 70px">{{ scope.row.requirement }}</div>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="整改人" prop="name" align="center" width="80">
      </el-table-column>
      <el-table-column label="整改状态" prop="status" align="center" width="80">
        <template slot-scope="scope" v-if="">
          <span>
            {{ computedStatus(scope.row.status) + '(' + scope.row.approval_status_name + ')' }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="80">
        <template slot-scope="scope">
          <template>
            <el-button
                type="text"
                icon="el-icon-edit-outline"
                @click="editRisk(scope.row)"
            >整改
            </el-button>
            <!--            <el-button-->
            <!--                type="text"-->
            <!--                icon="el-icon-edit-outline"-->
            <!--                @click="editRisk(scope.row,true)"-->
            <!--                v-else-->
            <!--            >查看-->
            <!--            </el-button>-->
          </template>
        </template>
      </el-table-column>
    </el-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog title="整改进度填写" :visible.sync="visible" width="75%"
               :before-close="handleClose">

      <el-descriptions title="风险信息">
        <el-descriptions-item label="工单流水号">{{ risk.id }}</el-descriptions-item>
        <el-descriptions-item label="项目名称">{{ risk.project_name }}</el-descriptions-item>
        <el-descriptions-item label="分行名称">{{ risk.porgid }}</el-descriptions-item>
        <el-descriptions-item label="支行名称">{{ risk.branch_id }}</el-descriptions-item>
        <el-descriptions-item label="整改机构或站点">{{ risk.site_no }}</el-descriptions-item>
        <el-descriptions-item label="整改要求">{{ risk.requirement }}</el-descriptions-item>
        <el-descriptions-item label="问题描述">{{ risk.content_text }}</el-descriptions-item>
        <el-descriptions-item label="风险等级">{{ risk.risk_level }}</el-descriptions-item>
        <el-descriptions-item label="问题发现日期">{{ risk.created }}</el-descriptions-item>
        <el-descriptions-item label="问题发现人">{{ risk.discoverer }}</el-descriptions-item>
        <el-descriptions-item label="整改预计完成日期">{{ risk.end_date }}</el-descriptions-item>
        <el-descriptions-item label="问题整改人">{{ risk.name }}</el-descriptions-item>
        <el-descriptions-item label="审核工单号">{{ approvalApply.id }}</el-descriptions-item>
        <el-descriptions-item label="审核状态">{{ approvalApply.approval_status_name }}</el-descriptions-item>
        <el-descriptions-item label="当前审核人">{{ approvalApply.name }}</el-descriptions-item>
      </el-descriptions>
      <el-divider></el-divider>
      <el-form ref="form" :model="risk" :rules="rules" label-width="90px" :disabled="flag">
        <el-row>
          <el-col :span="7">
            <el-form-item
                prop="schedule"
                label="整改完成情况"
            >
              <el-input type="textarea" size="small"
                        v-model="risk.schedule" label="整改完成情况">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
                prop="written"
                label="反馈人姓名"
                style="width: 360px" disabled="true">
              <el-select v-model="risk.written" filterable clearable style="width: 150px"
                         @click.native="getAccountList(true)">
                <el-option v-for="item in userList" :key="item.account_id" :label="item.name"
                           :value="item.account_id">
                  <span>{{ item.name }}({{ item.account_id }})</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item
                prop="status"
                label="整改状态"
                style="width: 360px"
            >
              <el-select
                  v-model="risk.status"
                  @change="changeRequired"
                  style="width: 150px"
              >
                <el-option
                    v-for="item in items"
                    :label="item.label"
                    :value="item.value"
                    :key="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="整改证明" prop="risk_photo">
              <el-upload
                  :class="{disUoloadSty:flag}"
                  action="/hnm/txCtrl?txcode=upload001"
                  list-type="picture-card"
                  ref="upload"
                  :before-upload="beforeAvatarUpload"
                  :file-list="fileList"
                  :data="{business: 'risk',name: '123'}"
                  :headers='{Accept:"application/json"}'
                  :on-success="uploadReturn"
                  :on-preview="handlePictureCardPreview"
                  :on-remove="handleRemove"
                  :limit="10">
                <i class="el-icon-plus"></i>
              </el-upload>
              <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
              </el-dialog>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-form-item label="审批人" prop="approval_user" v-if="risk.status !== 0 && use_up === '1'&& risk.risk_level !== '低'" style="width: 360px">
            <el-select v-model="risk.approval_user" clearable filterable
                       @click.native="getApprovalUser">
              <el-option v-for="item in accountList" :key="item.account_id" :label="item.name"
                         :value="item.account_id">
                <span>{{ item.name }}({{ item.account_id }})</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-row>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addOrEditRisk">确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import BranchTreePicker from "../../components/BranchTreePicker";
import approvalHis from "../flow/approvalHis";

export default {
  components: {
    baseTable,
    basePage,
    BranchTreePicker,
    approvalHis,
  },
  name:'risk',
  data() {
    return {
      user: JSON.parse(sessionStorage.getItem("user")),
      rules: {
        schedule: [{
          required: true,
          message: "整改进度不能为空",
          trigger: "blur"
        },],
        status: [{
          required: true,
          message: "请选择整改状态",
          trigger: "blur"
        },],
        written: [{
          required: true,
          message: "请选择反馈人姓名",
          trigger: "blur"
        },],
        approval_user: [
          {
            required: true,
            message: "请选择审批人",
            trigger: "blur"
          }
        ]
      },
      dateRange:[],
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '未来一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '未来一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '未来三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      status: [0],
      required: false,
      dialogImageUrl: '',
      dialogVisible: false,
      visible: false,
      risk: {},
      fileList: [],
      showData: [],
      form: {},
      flag: false,
      disabled: false,
      items: [
        {
          label: '未整改',
          value: 0
        }, {
          label: '已整改',
          value: 1
        }, {
          label: '无需整改',
          value: 2
        },
        {
          label: '无法整改',
          value: 3
        },
      ],
      options: [{
        label: '机构',
        value: '1',
        children: []
      },
        {
          label: '服务点',
          value: '2',
          children: []
        }],
      items2: [
        {
          label: '高',
        }, {
          label: '中',
        }, {
          label: '低',
        },
      ],
      use_up: "",
      delList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      userList: [],
      accountList: [],
      bankname: "",
      bankid: "",
      projectList: [],
      approvalApply: {
        id: "",
        approval_status_name: "",
        approval_user: "",
      },
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      }
      ,
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
    computedStatus() {
      return function (status) {
        return this.items[status]["label"]
      }

    },
  },
  mounted() {
    this.getRisk();
    this.geBankList();
    this.getSiteSelectList();
    this.getAccountList(true);
    this.getApprovalQY();
    this.$api.getProjectList({}).then((res) => {
      this.projectList = res.data;
    });
  }
  ,
  methods: {
    getApprovalUser() {
      if (this.accountList === null || this.accountList.length <= 0) {
        this.$api.getApprovalUser({approval_type:"023",risk_level:this.risk.risk_level}).then((res) => {
          this.accountList = res.data
        })
      }

    },
    // 查询是否启用
    getApprovalQY() {
      this.$api
          .getApprovalQY({
            approval_type: '023',
          })
          .then((res) => {
            this.use_up = res.data.use_up;
          });
    },
    changeRequired(status) {

      status === 1 ? this.required = true : this.required = false;

    },
    //删除上传图片
    handleRemove(file, fileList) {
      this.form.risk_photo = '';
      fileList.map((item) => {
        this.form.risk_photo += "'" + item.response.data[0].id + "',";
      })
      this.form.risk_photo = this.form.risk_photo.substr(0, this.form.risk_photo.length - 1);
      console.log(this.form.risk_photo);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    //图片上传成功回调
    uploadReturn(response, file, fileList) {
      console.log(file)
      this.risk.risk_photo = '';
      fileList.map((item) => {
        this.risk.risk_photo += "'" + item.response.data[0].id + "',";
      })
      this.risk.risk_photo = this.risk.risk_photo.substr(0, this.risk.risk_photo.length - 1);
      console.log(this.risk.risk_photo);
    },
    beforeAvatarUpload(file) {
      if (file.type !== 'image/png' && file.type !== 'image/jpg' && file.type !== 'image/jpeg') {
        this.$message.error('不支持png、jpg、jpeg以外格式的图片')
        return false
      }
      return true
    },
    tableRowClassName({row}) {
      if (row.status === 1) {
        return 'success-row';
      }
      return '';

    },
    // 机构下用户列表
    getAccountList(flag) {
      // if (this.userList.length != 0) return;
      if (flag) {
        this.$api.getAccountList({"deleted": 0}).then((res) => {
          this.userList = res.data;
        })
      } else {
        this.$api.getAccountList({"all":""}).then((res) => {
          this.userList = res.data;
        })
      }
    },
    getSiteSelectList() {
      this.$api.getSiteSelectList().then((res) => {
        let showlist = res.data.siteList;
        if (showlist != null) {
          for (let i = 0; i < showlist.length; i++) {
            let defaultProps = {};
            defaultProps.label = showlist[i].site_name;
            defaultProps.value = showlist[i].site_no;
            this.options[1].children.push(defaultProps)
          }
        }
      })
    },
    handleChange(itemIdArr) {
      //获取id
      this.form.site_no = itemIdArr[itemIdArr.length - 1]
    },
    geBankList() {
      this.$api.getBranch({}).then((res) => {
        let showlist = res.data;
        if (showlist) {
          for (let i = 0; i < showlist.length; i++) {
            let defaultProps = {};
            defaultProps.label = showlist[i].branch_name;
            defaultProps.value = showlist[i].branch_id;
            this.options[0].children.push(defaultProps)
          }
        }
      });
    },
    getRisk(flag) {
      if (flag) {
        this.pageNum = 1;
      }
      console.log( sessionStorage.getItem("role"))
      if (this.dateRange !== null && this.dateRange.length > 0 ){
        this.form.start_time = this.dateRange[0];
        this.form.end_time = this.dateRange[1];
      }else {
        delete this.form.start_time
        delete this.form.end_time
      }
      //获取风险信息
      this.form.page = this.pageNum - 1
      this.form.number = this.pageSize
      if (this.bankid) {
        this.form.site_no = this.bankid
      }
      this.form.status = this.status
      this.$api.getRisk(this.form).then((res) => {
        this.showData = res.data.resultList;
        this.total = res.data.total;
      });
    },
    handleClose() {
      this.visible = false;
      this.approvalApply = {};
      this.risk = {};
      this.fileList = [];
      this.flag = false;
    },
    addOrEditRisk() {
      this.$refs.form.validate((valid) => {
        if (this.flag) {
          this.handleClose();
        } else {
          if (valid) {
            let obj = {}
            obj.risk_level = this.risk.risk_level;
            obj.status = this.risk.status;
            obj.id = this.risk.id;
            obj.schedule = this.risk.schedule;
            obj.use_up = this.use_up;
            obj.approval_user = this.risk.approval_user;
            obj.approval_type = '023';
            obj.relation_id = this.risk.relation_id;
            obj.written = this.risk.written;
            if (this.risk.status === 1) {
              if (this.risk.risk_photo === null || this.risk.risk_photo === undefined || this.risk.risk_photo.length <= 0) {
                this.$message.error("整改图片必需上传");
                return
              }
            }
            if (this.risk.risk_photo) {
              obj.risk_photo = this.risk.risk_photo;
            }
            this.$api.addOrEditRisk(obj).then(() => {
              this.$message.success("提交整改任务成功");
              this.handleClose();
              this.getRisk();
              this.risk = {};
            });
          }
        }
      });
    },
    // 机构列表
    nodeselectForSerch(node) {
      console.log(node);
      this.bankname = node.branch_name;
      this.bankid = node.branch_id;
    }
    ,
    editRisk(obj, flag) {
      this.risk = {}
      if (flag) {
        this.flag = true;
      }
      if (obj) {
        this.risk = {}
        this.disabled = true;
        this.visible = true;
        this.fileList = [];
        this.risk = JSON.parse(JSON.stringify(obj))

        if (this.risk.written === null || this.risk.written === undefined) {
          this.risk.written = this.user.account_id
        }

        if (this.risk.risk_photo) {
          this.$api.getPhotoList({"ids": this.risk.risk_photo}).then((res) => {
            this.fileList = res.data.photoList;
            this.risk.risk_photo = '';
            this.fileList.map((item) => {
              item.response = {data: [{id: item.name}]};
              this.risk.risk_photo += "'" + item.response.data[0].id + "',";
            })
            this.risk.risk_photo = this.risk.risk_photo.substr(0, this.risk.risk_photo.length - 1);

          });
        }
        // 获取我的申请列表
        if (this.risk.relation_id){
          this.getApprovalApplyList(this.risk.relation_id);
        }
        this.getAccountList()

      }

    },
    // 获取我的申请列表
    getApprovalApplyList(id) {
      let approvalList = [];
      this.$api
          .getApprovalApplyList({
            page: this.pageNum - 1,
            number: this.pageSize,
            relation_id: id,
          })
          .then((res) => {
            approvalList = res.data.approvalApplyList;
            if (approvalList !== null && approvalList.length > 0) {
              this.approvalApply = approvalList[0];
            }
          });
    },
    // 修改页面大小
    changeSize(size) {
      this.pageNum = 1;
      this.pageSize = size;
      this.getRisk();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getRisk();
    },
    // 导出
    exportExpandRiskInfos() {
      //this.loading = true
      delete this.form.page;
      delete this.form.number;
      if (this.form.status == null || this.form.status.length === 0) {
        delete this.form.status;
      }
      if (this.dateRange !== null && this.dateRange.length > 0 ){
        this.form.start_time = this.dateRange[0];
        this.form.end_time = this.dateRange[1];
      }else {
        delete this.form.start_time
        delete this.form.end_time
      }
      this.$api.exportExpandRiskInfos(this.form).then((res) => {
        //this.loading = false
        var fileName = res.data.fileName;
        var filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "导出失败，系统异常",
            type: "error",
          });
          return false;
        }

        var objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        var input01 = document.createElement("input");
        var input02 = document.createElement("input");
        var input03 = document.createElement("input");
        var input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "导出成功",
          type: "success",
        });
      });
    },
  }
}
</script>
<style scoped class="risk">
.el-table .success-row {
  color: lightgray;
  /*background: #cb2e6d;*/
}

.disUoloadSty .el-upload--picture-card {
  display: none; /* 上传按钮隐藏 */
}

.resAndsug {
  text-align: left;
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  /*这里是3行*/
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>


