<template>
    <div>
        <el-button
                type="primary"
                size="small"
                @click="visible = true"
                style="margin-bottom: 10px"
        >新增
        </el-button
        >
        <el-form :inline="true" :model="form" style="margin-top: 15px">
            <el-form-item label="机构" prop="bankid">
                <div style="display: flex;">
                    <el-input readonly="" size="small" v-model="bankname"></el-input>
                    <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
                </div>
            </el-form-item>
            <el-form-item>
                <el-button
                        type="primary"
                        @click="getSiteWarnTimeList"
                        size="small"
                        icon="el-icon-search"
                >搜索
                </el-button
                >
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="siteWarnTimeList">
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="editSiteWarnTime(scope.row.id)"
                    >编辑
                    </el-button
                    >
                    <el-button
                            type="text"
                            icon="el-icon-delete"
                            style="color: red"
                            @click="deleteSiteWarnTime(scope.row.id)"
                    >删除
                    </el-button
                    >
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
        <el-dialog
                :title="form.id ? '修改' : '新增'"
                :visible.sync="visible"
                width="45%"
                :before-close="handleClose"
        >
            <el-form ref="form" :model="form" :rules="rules" label-width="90px">
                <el-form-item label="机构" prop="branch_id">
                    <div style="display: flex;">
                        <el-input :disabled="true" v-model="this.form.branch_name"></el-input>
                        <BranchTreePickerFH @nodeselect="nodeselectForShow" :dfks="this.form.branch_id" v-show="!form.id" :appendToBody="true"></BranchTreePickerFH>
                    </div>
                </el-form-item>
                <el-form-item label="站点试营业-开业提醒时间（天）" prop="warn_time">
                    <el-input size="small" v-model="form.warn_time"></el-input>
                </el-form-item>
                <el-form-item label="站点试营业-开业提醒间隔时间（天）" prop="warn_ac_time">
                    <el-input size="small" v-model="form.warn_ac_time"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" style="text-align: center">
                <el-button size="small" @click="handleClose">取 消</el-button>
                <el-button type="primary" size="small" @click="addSiteWarnTime"
                >确 定
                </el-button
                >
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";
    import BranchTreePickerFH from "../../components/BranchTreePickerFH";

    export default {
        components: {
            baseTable,
            basePage,
            BranchTreePicker,
            BranchTreePickerFH
        },
        name: "siteWarnTime",
        data() {
            var checkTime = (rule, value, callback) => {
                if (value === undefined || value == "" || value == null) {
                    callback();
                }
                var teamNum = /^[1-9]\d*$/; // 提醒时间正则
                if (teamNum.test(value)) {
                    callback();
                } else {
                    return callback(new Error("提醒时间格式非法"));
                }
            };
            return {
                role: sessionStorage.getItem("role"),
                form: {},
                bankid:"",
                bankname:"",
                showlist: [],
                dataList: [
                    {
                        label: "机构号",
                        prop: "branch_id",
                        sort: true,
                    },
                    {
                        label: "机构名称",
                        prop: "branch_name",
                        sort: true,
                    },
                    {
                        label: "站点试营业-开业提醒时间（天）",
                        prop: "warn_time",
                    },
                    {
                        label: "站点试营业-开业提醒间隔时间（天）",
                        prop: "warn_ac_time",
                    },
                ],
                rules: {
                    branch_id: [
                        {required: true, message: "请选择机构", trigger: "blur"},
                    ],
                    warn_time: [
                        {required: true, message: "请输入站点试营业-开业提醒时间", trigger: "blur"},
                        {validator: checkTime, trigger: "blur"},
                    ],
                    warn_ac_time: [
                        {required: true, message: "请站点试营业-开业提醒间隔时间", trigger: "blur"},
                        {validator: checkTime, trigger: "blur"},
                    ],
                },
                siteWarnTimeList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
        mounted() {
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            // 机构列表
            nodeselectForSerch(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },
            nodeselectForShow(node) {
                console.log(node);
                this.$set(this.form, "branch_name", node.branch_name);
                this.$set(this.form, "branch_id", node.branch_id);
            },
            // 新增
            addSiteWarnTime() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.form.id) {
                            this.$api.updateSiteWarnTime(this.form).then((res) => {
                                this.handleClose();
                                this.getSiteWarnTimeList();
                            });
                        } else {
                            this.$api.addSiteWarnTime(this.form).then((res) => {
                                this.handleClose();
                                this.getSiteWarnTimeList();
                            });
                        }
                    }
                });
            },
            // 编辑
            editSiteWarnTime(id) {
                this.$api
                    .getWarnTimeDetail({
                        id,
                    })
                    .then((res) => {
                        console.log(res);
                        this.visible = true;
                        this.form = res.data.msg[0];
                    });
            },
            // 获取列表
            getSiteWarnTimeList() {
                this.$api
                    .getSiteWarnTimeList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        bankid: this.bankid,
                        ...this.form,
                    })
                    .then((res) => {
                        this.siteWarnTimeList = res.data.siteWarnTimeList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getSiteWarnTimeList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getSiteWarnTimeList();
            },
            // 删除
            deleteSiteWarnTime(id) {
                this.$confirm("确定删除吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .deleteSiteWarnTime({
                                id: id,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getSiteWarnTimeList();
                            });
                    })
                    .catch(() => {
                    });
            },

            handleClose() {
                this.visible = false;
                this.form = {};
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getSiteWarnTimeList();
            });
        },
    };
</script>


