<template>
  <div>
    <el-button
        type="warning"
        size="small"
        @click="exportOperateInfos"
    >批量导出
    </el-button
    >
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-form-item label="规则编号">
        <el-input
            v-model="form.operate_no"
            clearable
            placeholder="请输入规则编号"
            size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="规则名称">
        <el-input
            v-model="form.operate_name"
            clearable
            placeholder="请输入规则名称"
            size="small"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getOperateList"
            size="small"
            icon="el-icon-search"
        >搜索
        </el-button
        >
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="operateList">
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editOperate(scope.row.id)"
          >编辑
          </el-button
          >
        </template>
      </el-table-column>
    </base-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog
        :title="'修改规则'"
        :visible.sync="visible"
        width="45%"
        :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="规则编号" prop="operate_no">
          <el-input size="small" v-model="form.operate_no" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="规则名称" prop="operate_name">
          <el-input size="small" v-model="form.operate_name"></el-input>
        </el-form-item>
        <el-form-item label="触发时间" prop="operate_time">
          <el-input size="small" v-model="form.operate_time"></el-input>
          <div v-if="form.operate_no == '1' || form.operate_no == '2'" style="color: #cccccc;font-size: small">
            *为可自定义参数："试营业生效后<span style="color: red">*</span>个月后"
          </div>
          <div v-else-if="form.operate_no == '12'" style="color: #cccccc;font-size: small">*为可自定义参数："开业转退出或转试营业后<span
              style="color: red">*</span>天"
          </div>
          <div v-else style="color: #cccccc;font-size: small">*为可自定义参数："开业生效后<span style="color: red">*</span>个月后"</div>
        </el-form-item>
        <el-form-item label="触发频率" prop="operate_rate">
          <el-input size="small" v-model="form.operate_rate"></el-input>
          <div
              v-if="form.operate_no == '1' || form.operate_no == '2' || form.operate_no == '3' || form.operate_no == '4' || form.operate_no == '7' || form.operate_no == '8' || form.operate_no == '9' || form.operate_no == '10'"
              style="color: #cccccc;font-size: small">*为可自定义参数："每月末检查 或 每月<span style="color: red">*</span>日检查"
          </div>
        </el-form-item>
        <el-form-item label="触发条件" prop="operate_condition">
          <el-input size="small" v-model="form.operate_condition"></el-input>
          <div v-if="form.operate_no == '1'" style="color: #cccccc;font-size: small">*为可自定义参数："服务点名下账户零售AUM存款余额低于<span
              style="color: red">*</span>万"
          </div>
          <div v-if="form.operate_no == '2'" style="color: #cccccc;font-size: small">*为可自定义参数："服务点已准入<span
              style="color: red">*</span>个月以上，未开业或退出"
          </div>
          <div v-if="form.operate_no == '3'" style="color: #cccccc;font-size: small">*为可自定义参数："服务点名下账户零售AUM存款余额低于<span
              style="color: red">*</span>万"
          </div>
          <div v-if="form.operate_no == '4'" style="color: #cccccc;font-size: small">*为可自定义参数："近<span
              style="color: red">*</span>天未发生<span style="color: red">*</span>笔助农取款业务（不含查询）"
          </div>
          <div v-if="form.operate_no == '5'" style="color: #cccccc;font-size: small">*为可自定义参数："助农取款登记簿，季度出现<span
              style="color: red">*</span>笔以上登记不符合要求"
          </div>
          <div v-if="form.operate_no == '6'" style="color: #cccccc;font-size: small">*为可自定义参数："季度出现<span
              style="color: red">*</span>次以上（含）巡检不合格"
          </div>
          <div v-if="form.operate_no == '7'" style="color: #cccccc;font-size: small">*为可自定义参数："T-<span
              style="color: red">*</span>月，助农取款交易没有勾兑"
          </div>

          <div v-if="form.operate_no == '10'" style="color: #cccccc;font-size: small">*为可自定义参数："涉及<span
              style="color: red">*</span>个及以上账户，向同一个账户转账汇款或现金转账，金额<span style="color: red">*</span>万及以上"
          </div>
          <div v-if="form.operate_no == '11'" style="color: #cccccc;font-size: small">*为可自定义参数："夜间<span
              style="color: red">*</span>点到次日<span style="color: red">*</span>点内发生<span style="color: red">*</span>笔以上资金变动交易"
          </div>
<!--          <div v-if="form.operate_no == '12'" style="color: #cccccc;font-size: small">-->
<!--          </div>-->
          <div v-if="form.operate_no === '13'||form.operate_no === '14' ||form.operate_no === '15'"
               style="color: #cccccc;font-size: small">
            *为可自定义参数："偏离度<span style="color: red">*</span>%---<span style="color: red">*</span>%且交易基数大于或等于<span style="color: red">*</span>笔"
          </div>
          <div v-if="form.operate_no === '16'" style="color: #cccccc;font-size: small">
            *为可自定义参数："偏离度<span style="color: red">*</span>%以上且交易基数大于或等于<span style="color: red">*</span>笔"
          </div>
        </el-form-item>
        <el-form-item label="整改范围" prop="operate_area">
          <el-select v-model="form.operate_area" multiple>
            <el-option key="0" label="试营业站点" value="0"></el-option>
            <el-option key="1" label="试营业(已申请pos)站点" value="1"></el-option>
            <el-option key="2" label="开业站点" value="2"></el-option>
            <el-option key="3" label="退出站点" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="风险等级" prop="operate_risk">
          <el-select v-model="form.operate_risk" clearable>
            <el-option key="0" label="低" value="0"></el-option>
            <el-option key="1" label="中" value="1"></el-option>
            <el-option key="2" label="高" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="整改时间" prop="operate_day">
          <el-input size="small" v-model="form.operate_day"></el-input>
          <div style="color: #cccccc;font-size: small">*为可自定义参数："本月末 或 <span style="color: red">*</span>天内"</div>
        </el-form-item>
        <el-form-item label="问题描述" prop="operate_content">
          <el-input size="small" v-model="form.operate_content"></el-input>
        </el-form-item>
        <el-form-item label="整改要求" prop="operate_requirement">
          <el-input size="small" v-model="form.operate_requirement"></el-input>
        </el-form-item>
        <el-form-item label="处理人" prop="operate_operno">
          <el-select v-model="form.operate_operno" clearable>
            <el-option key="0" label="分行管理员" value="0"></el-option>
            <el-option key="1" label="站长对口惠农客户经理" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否启用" prop="use_up">
          <el-radio v-model="form.use_up" label="0">否</el-radio>
          <el-radio v-model="form.use_up" label="1">是</el-radio>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              placeholder="请输入内容"
              v-model="form.remarks"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addOperate"
        >确 定
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";

export default {
  components: {
    baseTable,
    basePage,
  },
  name: "operate",
  data() {
    return {
      form: {},
      dataList: [
        {
          label: "规则编号",
          prop: "operate_no",
          sort: true,
        },
        {
          label: "规则名称",
          prop: "operate_name",
        },
        {
          label: "是否启用",
          prop: "use_up",
        },
      ],
      operateList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
    };
  },
  computed: {
    rules() {
      var checkIntNum = (rule, value, callback) => {
        if (value === undefined || value == "" || value == null) {
          callback();
        }
        let numReg = /^(0|\+?[1-9][0-9]*)$/; // 整数
        if (numReg.test(value)) {
          callback();
        } else {
          return callback(new Error("数值非法"));
        }
      };
      return {
        operate_name: [
          {required: true, message: "请输入规则名称", trigger: "blur"},
        ],
        operate_time: [
          {required: true, message: "请输入触发时间", trigger: "blur"},
        ],
        operate_rate: [
          {required: true, message: "请输入触发频率", trigger: "blur"},
        ],
        operate_condition: [
          {required: true, message: "请输入触发条件", trigger: "blur"},
        ],
        operate_area: [
          {required: true, message: "请选择整改范围", trigger: "change"},
        ],
        operate_risk: [
          {required: true, message: "请选择风险等级", trigger: "change"},
        ],
        operate_day: [
          {required: true, message: "请输入整改时间", trigger: "blur"},
        ],
        operate_content: [
          {required: true, message: "请输入问题描述", trigger: "blur"},
        ],
        operate_requirement: [
          {required: true, message: "请输入整改要求", trigger: "blur"},
        ],
        operate_operno: [
          {required: true, message: "请选择处理人", trigger: "change"},
        ],
        use_up: [
          {required: true, message: "请选择是否启用", trigger: "change"},
        ],
      }
    }
  },

  mounted() {
    //this.getOperateList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {
    // 修改合规操作规则
    addOperate() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          let operate_no = this.form.operate_no;//规则编号
          let operate_time = this.form.operate_time;//触发时间
          let operate_rate = this.form.operate_rate;//触发频率
          let operate_condition = this.form.operate_condition;//触发条件
          let operate_content = this.form.operate_content;//问题描述
          let operate_requirement = this.form.operate_requirement;//整改要求
          let operate_day = this.form.operate_day;//整改时间
          if (operate_no === '1') {
            let operate_time_rule = /^(试营业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每月[1-9][0-9]*日检查|每月末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(服务点名下账户零售AUM存款余额低于(0|[1-9][0-9]*)万)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule = /^(【服务点】【月份】零售AUM存款余额低于(0|[1-9][0-9]*)万)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // };
            let operate_content_rule = "【服务点】【月份】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            ;
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
              this.$message.error("整改时间格式不正确");
              return;
            }
            ;
          } else if (operate_no === '2') {
            let operate_time_rule = /^(试营业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每月[1-9][0-9]*日检查|每月末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(服务点已准入(0|[1-9][0-9]*)个月以上，未开业或退出)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule = /^(【服务点】已准入(0|[1-9][0-9]*)个月以上，未开业或退出)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // }
            let operate_content_rule = "【服务点】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
              this.$message.error("整改时间格式不正确");
              return;
            }
            ;
          } else if (operate_no === '3') {

            let operate_time_rule = /^(开业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每月[1-9][0-9]*日检查|每月末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(服务点名下账户零售AUM存款余额低于(0|[1-9][0-9]*)万)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule =  /^(【服务点】【月份】名下账户零售AUM存款余额低于(0|[1-9][0-9]*)万)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // }
            let operate_content_rule = "【服务点】【月份】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
              this.$message.error("整改时间格式不正确");
              return;
            }
            ;
          } else if (operate_no === '4') {
            let operate_time_rule = /^(开业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每月[1-9][0-9]*日检查|每月末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(近(0|[1-9][0-9]*)天未发生(0|[1-9][0-9]*)笔助农取款业务（不含查询）)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule = /^(【服务点】近(0|[1-9][0-9]*)天未发生(0|[1-9][0-9]*)笔助农取款业务（不含查询）)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // }
            let operate_content_rule = "【服务点】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
              this.$message.error("整改时间格式不正确");
              return;
            }
            ;
          } else if (operate_no === '5') {
            let operate_time_rule = /^(开业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每季末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(助农取款登记簿，季度出现(0|[1-9][0-9]*)笔以上登记不符合要求)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule = /^(【服务点】【季度】出现(0|[1-9][0-9]*)笔以上助农取款登记簿登记不符合要求)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // }
            let operate_content_rule = "【服务点】【季度】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
              this.$message.error("整改时间格式不正确");
              return;
            }
            ;
          } else if (operate_no === '6') {
            let operate_time_rule = /^(开业生效后(0|[1-9][0-9]*)个月后)$/;
            if (!operate_time_rule.test(operate_time)) {
              this.$message.error("触发时间格式不正确");
              return;
            }
            ;
            let operate_rate_rule = /^(每季末检查)$/;
            if (!operate_rate_rule.test(operate_rate)) {
              this.$message.error("触发频率格式不正确");
              return;
            }
            ;
            let operate_condition_rule = /^(季度出现(0|[1-9][0-9]*)次以上（含）巡检不合格)$/;
            if (!operate_condition_rule.test(operate_condition)) {
              this.$message.error("触发条件格式不正确");
              return;
            }
            ;
            // let operate_content_rule = /^(【服务点】【季度】出现(0|[1-9][0-9]*)次以上（含）巡检不合格)$/;
            // if(!operate_content_rule.test(operate_content)){
            //     this.$message.error("问题描述格式不正确");
            //     return;
            // }
            let operate_content_rule = "【服务点】【季度】";
            if (!operate_content.startsWith(operate_content_rule)) {
              this.$message.error("问题描述格式不正确");
              return;
            }
            let operate_day_rule = /^((0|[1-9][0-9]*)天内|本月末)$/;
            if (!operate_day_rule.test(operate_day)) {
