<template>
  <div style="margin-left: 1px;">
    <el-button size="small" type="primary" @click="getMeasures()">管控措施</el-button>
    <el-dialog title="风险客户管控" :visible.sync="dialogVisible" width="65%">
      <el-table
          border fit stripe
          :data="tableData"
          element-loading-text="Loading"
          :header-cell-style="{'text-align':'center'}"
      >
        <slot name="head">
          <el-table-column align="center" label="序号" prop="id" width="50"></el-table-column>
          <el-table-column align="left" label="管控措施" prop="measures"></el-table-column>
          <el-table-column align="center" label="风险客户管控" width="220">
            <template slot-scope="scope">
              <el-checkbox-button label="高" true-label="0" false-label="1" v-model="scope.row.h_risk_level"></el-checkbox-button>
              <el-checkbox-button label="中" true-label="0" false-label="1" v-model="scope.row.m_risk_level"></el-checkbox-button>
              <el-checkbox-button label="低" true-label="0" false-label="1" v-model="scope.row.l_risk_level"></el-checkbox-button>
            </template>
          </el-table-column>
        </slot>
      </el-table>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addOrEditMeasure">确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "measure",

  data() {
    return {
      items: [
        {
          label: '高',
          value: 'hRishLevel'
        }, {
          label: '中',
          value: 'mRishLevel'
        }, {
          label: '低',
          value: 'lRishLevel'
        },
      ],
      dialogVisible: false,
      tableData: [],
    }
  },
  methods: {
    addOrEditMeasure() {
      let obj = {}
      obj.tableData = this.tableData
      this.$api.addOrEditMeasure(obj).then(() => {
        this.$message.success("修改成功");
        this.handleClose()
      })
    },
    handleClose() {
      this.dialogVisible = false;
    },
    getMeasures() {
      this.dialogVisible = true;
      this.$api.getMeasures({}).then((res) => {
        this.tableData = res.data;

      });
    },
  },
};
</script>


