<template>
  <div ref="charts" style="height: 400px; margin: 0 auto;"></div>
</template>

<script>
  import echarts from 'echarts'

  export default {
    name: 'chart',
    data() {
      return {
        chart: ''
      }
    },
    methods: {
      drawCharts() {
        this.chart = echarts.init(this.$refs.charts);
        this.chart.setOption(this.option);
        window.addEventListener('resize', this.chart.resize);
      }
    },
    mounted() {
      this.drawCharts();
    },
    props: ['option'],
    watch: {
      option: {
        handler(newVal, oldVal) {
          if (this.chart) {
            if (newVal) {
              this.chart.setOption(newVal);
            } else {
              this.chart.setOption(oldVal);
            }
          } else {
            this.init();
          }
        },
        deep: true
      }
    }
  }
</script>



