<template>
  <div style="margin-left: 1px;">
    <el-button size="small" type="primary" @click="getProjectList()">巡查项目管理</el-button>
    <el-dialog title="巡查项目管理" :visible.sync="dialogVisible">
      <div>
        <el-button>项目名称</el-button>
        <el-col span="230">
          <el-input id="input"
                    placeholder="请输入项目名称"
                    v-model="project_name"
                    style="width: 100%"
                    maxlength="15"
          >
          </el-input>
        </el-col>
        <el-button type="primary" @click="addOrDeleteProjec">新增</el-button>
      </div>

      <el-table
          :data="tableData"
          element-loading-text="Loading"
          border
          fit
          highlight-current-row
      >
        <slot name="begin"></slot>
        <slot name="head">
          <el-table-column
              fixed
              align="center"
              type="index"
              prop="index"
              label="序号"
              width="50">
          </el-table-column>
          <el-table-column align="center" label="项目名称" prop="project_name">
          </el-table-column>
          <el-table-column
              align="center"
              fixed="right"
              label="操作"
              width="60">
            <template slot-scope="scope">
              <el-button
                  @click.native.prevent="addOrDeleteProjec(scope.row.pj_classify,scope.row.project_name)"
                  type="text"
                  size="small">
                删除
              </el-button>
            </template>
          </el-table-column>
        </slot>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "project",

  data() {
    return {
      visible: false,
      dialogVisible: false,
      project_name: "",
      tableData: [],
      defaultProps: {
        children: 'children',
        label: 'project_name',

      },
      dfks: []
    }
  },
  methods: {

    addOrDeleteProjec(id, name) {
      let form = {}
      form.project_name = this.project_name
      if (typeof id === "number") {
        form.pj_classify = id
        form.project_name = name
      }

      this.$api.addOrDeleteProjec(form).then(() => {
        this.tableData = {}
        this.getProjectList()
      })
    },
    getProjectList() {

      if (this.tableData.length > 1) {
        this.dialogVisible = true;
        return;
      }
      this.$api.getProjectList({}).then((res) => {
        this.tableData = res.data;
        this.dialogVisible = true;
      });
      this.project_name = "";
    },
  },
};
</script>


