export default {
  data() {
    return {
      labelList: {},
      option: {
        title: {
          text: "默认",
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 30,
            end: 70,
            xAxisIndex: [0, 0],
          },
          {
            type: "inside",
            realtime: true,
            start: 30,
            end: 70,
            xAxisIndex: [0, 0],
          },
        ],
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [],
        },
        tooltip: {
          formatter: (params) => {

            if (
              params.name !== this.name
            ) {
              this.tooltip = "";
              this.name = params.name;
              this.getThanData(params);
            } else {
              if (this.tooltip) {
                return this.tooltip;
              } else {
                return `<div  v-loading="${true}">请稍等</div>`;
              }
            }
          },
        },
        yAxis: {
          type: "value",
        },
        grid: {
          left: 40,
          top: 40,
            bottom: 40,
          height: '70%'
        },
        series: [
          {
            name: "日期",
            data: [],
            type: "line",
            itemStyle: {
              normal: {
                color: "#289df5", // 折线条的颜色
                borderColor: "#289df5", // 拐点边框颜色
                areaStyle: {
                  type: "default",
                  opacity: 0.5,
                },
              },
            },
          },
        ],
      },
      // chartShow: false
    };
  },
  mounted() {
    let label = document.querySelectorAll(
      "label"
    );
    label.forEach((item) => {
      if (item.getAttribute("for")) {
        this.$set(
          this.labelList,
          item.getAttribute("for"),
          item.innerText
        );
      }
    });
  },
  methods: {
    showChart(parms) {
      // this.chartShow = true;
      this.$refs.chartShow.chartShow = true;
      this.getData(parms);
    },
  },
};



