<template>
  <el-dialog
  class="chart-dialog"
    title=""
    :visible.sync="chartShow"
    width="50%"
  >
    <el-form :model="form" inline>
      <el-form-item label="选择日期">
        <el-date-picker
          v-model="form.query_date"
          size="small"
          type="date"
          value-format="yyyyMMdd"
          placeholder="选择日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click="showChart">查询</el-button>
      </el-form-item>
    </el-form>
    <chart :option="option"></chart>
    <span slot="footer">

     
    </span>
  </el-dialog>
</template>

<script>
import chart from "@/views/components/chart/index";
export default {
  props: {
    title: '默认',
    option: {
        type: Object,
        default: function() {
            return {}
        }
    }
  },
  components: {
    chart,
  },
  data() {
    return {
      chartShow: false,
      name: '',
      tooltip: '',

      form: {
        query_date: "",
      },
    };
  },
  methods: {
    showChart() {
      this.$emit('get-data', this.form)
    },
  },
};
</script>

<style lang="scss">
.chart-dialog{
  .el-dialog__body {
    height: 500px;
  }
}
</style>



