    rules() {
      var checkPhone = (rule, value, callback) => {
        if (value === undefined || value == "" || value == null) {
          callback();
        }

        let phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
        if (phoneReg.test(value)) {
          callback();
        } else {
          return callback(new Error("手机号码非法"));
        }
      };
      const checkCarNo = (rule, value, callback) => {
        if (value === undefined || value === "" || value === null) {
          callback();
        }
        //服务点卡号
        let carNo = /^[0-9]\d{0,31}$/;

        if (carNo.test(value)) {
          callback();
        } else {
          return callback(new Error("服务点主卡卡号非法"));
        }
      };

      var checkID = (rule, value, callback) => {
        if (value === undefined || value == "" || value == null) {
          callback();
