        // 身份证号正则
        let idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
        if (idcardReg.test(value)) {
          callback();
        } else {
          return callback(new Error("身份证非法"));
        }
      };
      var checkNum = (
          rule,
          value,
          callback
      ) => {
        if (
            value === undefined ||
            value == "" ||
            value == null
        ) {
          callback();
        }
        let numReg = /^(?:[1-9][0-9]?|100)$/; // 数值正则
        if (numReg.test(value)) {
          callback();
        } else {
          return callback(
              new Error("数值非法")
          );
        }
      };

      var checkIntNum = (rule, value, callback) => {
        if (value === undefined || value == "" || value == null) {
          callback();
        }
        let numReg = /^(0|\+?[1-9][0-9]*)$/; // 整数
        if (numReg.test(value)) {
          callback();
        } else {
          return callback(new Error("数值非法"));
        }
      };

      var checkDoubleNum = (rule, value, callback) => {
        if (value === undefined || value == "" || value == null) {
          callback();
        }
        let numReg = /^(0|([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/; // 小数
        if (numReg.test(value)) {
          callback();
        } else {
          return callback(new Error("数值非法"));
        }
      };

