  mounted() {
    this.approval_type = this.$route.query.approval_type || this.type004;
    this.check = this.$route.query.check === 'false' ? false : Boolean(this.$route.query.check);
    console.log(this.check)
    this.id = this.$route.query.id;
    this.req_place = this.$route.query.req_place || '0';
    if (this.id) {
      this.isload = true;
      this.$api
          .getSite({
            id: this.id,
          })
          .then((res) => {
            this.form = res.data.siteDetail;
            this.baseForm = JSON.parse(JSON.stringify(res.data.siteDetail));
            this.site_status = res.data.siteDetail.status;
            if (this.form.p_site_adr) {
              this.form.p_site_adr_arry = this.form.p_site_adr.split("/");
            }
            if (this.form.p_master_adr) {
              this.form.p_master_adr_arry = this.form.p_master_adr.split("/");
            }

            if (res.data.fileList != null && res.data.fileList.length > 0) {
              this.fileList = this.fileList.concat(res.data.fileList)
              //this.fileList = res.data.fileList;
              for (let i = 0; i < this.fileList.length; i++) {
                if (this.fileList[i].id) {
                  this.fileList[i].file_id = this.fileList[i].id;
                  this.fileList[i].filelastname = this.fileList[i].local_file_path.substring(this.fileList[i]
                      .local_file_path.lastIndexOf('.') + 1);
                }
                if (this.fileList[i].code === "site01") {
                  this.fileList[i].name = "广东南粤银行银行卡助农取款业务申请表";
                }
                if (this.fileList[i].code === "site02") {
                  this.fileList[i].name = "站长身份证正面";
                }
                if (this.fileList[i].code === "site03") {
                  this.fileList[i].name = "站长身份证反面";
                }
                if (this.fileList[i].code === "site04") {
                  this.fileList[i].name = "站长征信报告";
                }
                if (this.fileList[i].code === "site05") {
                  this.fileList[i].name = "站长无犯罪证明";
                }
