                  this.fileList[i].name = "其他材料";
                }
                if (this.fileList[i].code === "site07") {
                  this.fileList[i].name = "主卡正面照片";
                }
                if (this.fileList[i].code === "site08") {
                  this.fileList[i].name = "普惠金融支付服务点受理银联卡业务协议书";
                }
                if (this.fileList[i].code === "site09") {
                  this.fileList[i].name = "服务点工商营业执照";
                }
                if (this.fileList[i].code === "site10") {
                  this.fileList[i].name = "服务点装修照片";
                }
                if (this.fileList[i].code === "site11") {
                  this.fileList[i].name = "其他资料";
                }
                if (this.fileList[i].code === "site12") {
                  this.fileList[i].name = "普惠金融支付服务点助农终端撤销申请表";
                }
                if (this.fileList[i].code === "site13") {
                  this.fileList[i].name = "站长退出资料";
                }
                if (this.fileList[i].code === "site21") {
                    this.fileList[i].name = "站长学历证明";
                }
              }
            } else {
              if (this.site_status === "1") {//试营业（已申请pos）
                this.fileList = this.fileList.concat(this.fileList1);
              } else if (this.site_status === "2") {//已开业
                if (this.approval_type !== this.type022) {
                  this.fileList = this.fileList.concat(this.fileList1).concat(this.fileList2);
                }
              } else if (this.site_status === "3") {//已退出
                this.fileList = this.fileList.concat(this.fileList3);
              }
