                  }
                  if (this.fileList[i].code === "site05") {
                    this.fileList[i].name = "站长无犯罪证明";
                  }
                  if (this.fileList[i].code === "site06") {
                    this.fileList[i].name = "其他材料";
                  }
                  if (this.fileList[i].code === "site07") {
                    this.fileList[i].name = "主卡正面照片";
                  }
                  if (this.fileList[i].code === "site08") {
                    this.fileList[i].name = "普惠金融支付服务点受理银联卡业务协议书";
                  }
                  if (this.fileList[i].code === "site09") {
                    this.fileList[i].name = "服务点工商营业执照";
                  }
                  if (this.fileList[i].code === "site10") {
                    this.fileList[i].name = "服务点装修照片";
                  }
                  if (this.fileList[i].code === "site11") {
                    this.fileList[i].name = "其他资料";
                  }
                  if (this.fileList[i].code === "site12") {
                    this.fileList[i].name = "普惠金融支付服务点助农终端撤销申请表";
                  }
                  if (this.fileList[i].code === "site13") {
                    this.fileList[i].name = "站长退出资料";
                  }
                  if (this.fileList[i].code === "site21") {
                    this.fileList[i].name = "站长学历证明";
                  }
                }
              }
            }
            this.handleShowList();
            this.isload = false;
          }).catch(() => {
        this.isload = false;
      });

    }
    //客户经理回显
    this.getMgrList();
    //保存（首次提交）时查询审批角色对应审批人员
    // if (!this.check) {
    //   this.getApprovalAccountList();
    // }

    //查询是否启用
    if (!this.check) {
      this.getApprovalQY();
    }

    if (this.check && this.req_place === '0') {
      this.getApprovalHisLogList(this.id);
    }

  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {

    // 审批记录列表
    getApprovalHisLogList(id) {
      this.$api.getApprovalHisLogList({
        "req_type": "0",
        "id": id,
      }).then((res) => {
        this.approvalHisLogList = res.data;
      });
    },
    showHisDetail(id) {
      this.approvalHisList = [];
      this.$api.getApprovalHisList({
        apply_id: id
      }).then((res) => {
        this.his_visible = true;
        this.approvalHisList = res.data;
      });
    },
    handleHisClose() {
      this.his_visible = false;
    },


    open() {
      this.$prompt('', '申请说明', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputType: 'textarea',
        inputPattern: /^(.|\n){1,300}$/,
        inputErrorMessage: '申请说明不能为空且不能超过300字',
        inputPlaceholder: '输入内容不能超过300字',
      }).then(({value}) => {
        this.form.suggestion = value
        this.onSubmit()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消提交'
        });
      });

    },
    getThanData(params) {
      this.parms.query_date = params.name;
      let parms = this.parms;
      let query_item = this.parms.query_item;
      if ("amt1" === query_item || "amt2" === query_item || "amt3" === query_item || "amt4" === query_item) {
        this.$api
            .getReport001List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      } else if ("cus_num1" === query_item || "cus_num2" === query_item || "cus_num3" === query_item || "cus_num4" === query_item) {
        this.$api
            .getReport002List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      }
    },
    childSearch(params) {
      this.parms.query_date = params.query_date;
      this.getData(this.parms)
    },
    getData(parms) {
      this.parms = parms;
      this.$api
          .getReport004List(parms)
          .then((res) => {
            this.option.xAxis.data = res.data.dateList;
            this.option.series[0].data = res.data.dataList;
          });
    },
    changeSource() {
      if (this.form.site_source != '1') {
        this.$set(this.form, "recommender", null);
      }
    },
    beforeAvatarUpload(file) {
      console.log(file);
      /*const isLt2M = file.size / 1024  < 300;
      if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 300KB');
      }
      return isLt2M;*/
    },
    // 对比两次fileList
    compareFile() {
      let list = []
      this.fileList.forEach(item => {
        let res = this.baseFileList.some(e => {
          return e.file_id === item.file_id
        })
        if (!res) {
          list.push(item)
        }
      })
      this.baseFileList.forEach(item => {
        let res = this.fileList.some(e => {
          return e.file_id === item.file_id
        })
        if (!res) {
          list.push(item)
        }
      })
      console.log(list)
      list = Array.from(new Set(list.map(item => this.showList[item.code].name)))
      this.update_items = this.update_items.concat(list)

    },
    fileUploaded(response, file, fileList) {
      let item = response.data[0]
      this.fileList[this.fileList.length] = {
        code: response.data[0].code,
        file_url: response.data[0].file_url,
        file_id: response.data[0].id,
        filelastname: response.data[0].local_file_path.substring(response.data[0]
            .local_file_path.lastIndexOf('.') + 1)
      }
      item.file_url && this.showList[item.code].fileList.push({
        code: response.data[0].code,
        file_url: response.data[0].file_url,
        file_id: response.data[0].id,
        filelastname: response.data[0].local_file_path.substring(response.data[0]
            .local_file_path.lastIndexOf('.') + 1)
      })

    },

    fileUploadedError(response, file, fileList) {
      console.log(JSON.parse(response.message));
      this.$message.error(JSON.parse(response.message).tips.message);
    },

    // 机构列表
    nodeselectForShow(node) {
      console.log(node);
      //this.form.branch_name = node.branch_name;
      //this.form.branch_id = node.branch_id;
      this.$set(this.form, "branch_name", node.branch_name);
      this.$set(this.form, "branch_id", node.branch_id);
    },
    // 客户经理列表
    getMgrList() {
      if (this.mgrList.length != 0) return;
      this.$api.geManagerList({}).then((res) => {
        this.mgrList = res.data;
        console.log(this.mgrList);
        // console
      });
    },
    // 保存（首次提交）时查询审批角色对应审批人员
    getApprovalAccountList() {
      this.$api
          .getApprovalAccountList({
            approval_type: this.$route.query.approval_type || this.type004,
            ...this.form,
          })
          .then((res) => {
            this.accountList = res.data.dataList;
            //this.use_up = res.data.use_up;
            // if (res.data.dataList.length == 1) {
            // 	this.$set(this.form, "approval_user", res.data.dataList[0].account_id);
            // }
          });
    },

    // 查询是否启用
    getApprovalQY() {
      this.$api
          .getApprovalQY({
            approval_type: this.$route.query.approval_type || this.type004,
          })
          .then((res) => {
            this.use_up = res.data.use_up;
          });
    },

    //暂存
    tempSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }

      if (this.form.p_site_adr_arry != undefined && this.form.p_site_adr_arry != null) {
        this.form.p_site_adr = this.form.p_site_adr_arry.join("/");
      }
      if (this.form.p_master_adr_arry != undefined && this.form.p_master_adr_arry != null) {
        this.form.p_master_adr = this.form.p_master_adr_arry.join("/");
      }

      this.form.fileIdList = [];
      for (let i = 0; i < this.fileList.length; i++) {
        if (this.fileList[i].file_id !== undefined && this.fileList[i].file_id !== null) {
          this.form.fileIdList.push(this.fileList[i].file_id);
        }
      }

      this.$api
          .tempaddSite(this.form)
          .then((res) => {
            //this.handleClose();
            this.$message.success(
                "暂存成功"
            );
            //this.$router.back(-1);
          });
    },

    onSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.form.approval_type = this.$route.query.approval_type || this.type004;
          this.form.req_place = this.req_place;
          this.form.apply_id = this.$route.query.apply_id || "";
          this.form.use_up = this.use_up;

          if ((this.form.p_site_adr_arry == undefined || this.form.p_site_adr_arry == null || this.form.p_site_adr_arry.length == 0) && (this.form.approval_type == this.type004 || this.form.approval_type == this.type005)) {
            this.$message.error("请选择站点区域");
            return;
          }
          if ((this.form.p_master_adr_arry == undefined || this.form.p_master_adr_arry == null || this.form.p_master_adr_arry.length == 0) && (this.form.approval_type == this.type004 || this.form.approval_type == this.type016)) {
            this.$message.error("请选择站长居住区域");
            return;
          }
          if ((this.form.card_no == undefined || this.form.card_no == null || this.form.card_no.length == 0) && (this.form.approval_type == this.type006 || this.form.approval_type == this.type007)) {
            this.$message.error("请输入服务点主卡卡号");
            return;
          }

          if (this.form.p_site_adr_arry != undefined && this.form.p_site_adr_arry != null) {
            this.form.p_site_adr = this.form.p_site_adr_arry.join("/");
          }
          if (this.form.p_master_adr_arry != undefined && this.form.p_master_adr_arry != null) {
            this.form.p_master_adr = this.form.p_master_adr_arry.join("/");
          }

          this.form.fileIdList = [];

          let missfile = "";
          let hasfileList = ["site06", "site11", "site13"];
          let education = this.form.education;
          if (education == "0" || education == "1") {
            hasfileList = ["site06", "site11", "site13", "site21"];
          }
          for (let i = 0; i < this.fileList.length; i++) {
            if (this.fileList[i].file_id !== undefined && this.fileList[i].file_id !== null) {
              hasfileList.push(this.fileList[i].code);
              this.form.fileIdList.push(this.fileList[i].file_id);
            }
          }
          this.fileList.forEach(item => {
            let flag = false;
            hasfileList.forEach(e => {
              if (e === item.code) {
                flag = true;
              }
            });
            if (!flag) {
              missfile += item.name + ",";
            }
          });

          if (missfile.length > 0) {
            this.$message.error("请上传以下文件:" + missfile);
            return;
          }

          for (let i in this.baseForm) {
            this.baseForm[i] != this.form[i] && this.update_items.push(this.labelList[i])
          }
          this.compareFile();
          if (this.update_items.length > 0) {
            this.form.update_items = this.update_items.join(',')
          }
          this.$api.addSite(this.form).then((res) => {
            this.handleClose();
            this.$message.success("操作成功，你已跳转至我的申请页面");
            this.$router.push({
              path: "/approvalApply",
              name: "我的申请",
            });
            //this.$router.back(-1);
          });
        }
      });
    },
    handleClose() {
      this.dialogVisible = false;
      this.visible = false;
      this.form = {};
    },
    handleRemove(file, fileList, index) {
      let id
      if (file.file_id) {
        id = file.file_id
      } else {
        id = file.response.data[0].id
      }
      fileList.splice(index, 1)
      this.fileList = this.fileList.filter(item => item.file_id !== id)
    },
    handleShowList() {
      console.log(this.fileList, 444444)
      this.fileList.forEach(item => {
        item.url = item.file_url
        if (!this.showList[item.code]) {
          this.$set(this.showList, item.code, {
            name: item.name,
            code: item.code,
            file_type: item.file_type,
            business: item.business,
            fileList: item.url ? [item] : []
          })
        } else {
          item.url && this.showList[item.code].fileList.push(item)
        }
      })
    }

  },
  // 跳转保存数据
  beforeRouteLeave(to, from, next) {
    to.params.searchKey = this.$route.params.searchKey;
    next();
  },
};
</script>
<style scoped>
.fileimg {
  width: 120px;
  height: 120px;
  background-color: #eeeeee;
}

.imgtigger {
  color: #FFFFFF;
  margin-top: 40px;
  text-align: center;
}

.mapdiv {
  width: 100%;
  height: 500px;
  /* background-color: #11B95C; */
}

.imgview {
  max-width: 100%;
}

.uploaditem {
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  align-items: center;
  /* width: 200px; */
}

.wenjian {
  text-decoration: none;
  outline: none;
  font-size: 60px;
  color: #bf0000;
}

.fa {
  color: cadetblue;
  font-size: 96px;
}

.el-image, a {
  margin: 0 10px;
}

.delete-logo {
  position: absolute;
  z-index: 999;
  right: 10px;
  top: -10px;
  cursor: pointer;
}

.el-input_noborder {
  width: 100px;
  border-top-width: 0;
  border-left-width: 0;
  border-right-width: 0;
  border-bottom-width: 1px;
}

</style>



