<template>
  <div>
    <el-button
        type="primary"
        size="small"
        @click="visible = true"
        style="margin-bottom: 10px"
    >新增类型
    </el-button
    > <el-button-group>
    <approval-field v-if="role_id ==='2021061500000001'"> </approval-field>
    </el-button-group
    >
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-form-item label="审批类型编号">
        <el-input
            v-model="form.approval_type"
            clearable
            placeholder="请输入审批类型编号"
            size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="审批类型名称">
        <el-input
            v-model="form.approval_type_name"
            clearable
            placeholder="请输入审批类型名称"
            size="small"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getApprovalTypeList"
            size="small"
            icon="el-icon-search"
        >搜索
        </el-button
        >
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="approvalTypeList">
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editApprovalType(scope.row.id,scope.row.approval_type)"
          >编辑
          </el-button
          >
          <el-button
              type="text"
              icon="el-icon-delete"
              style="color: red"
              @click="deleteApprovalType(scope.row.id)"
          >删除
          </el-button
          >
        </template>
      </el-table-column>
    </base-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog
        :title="form.id ? '修改类型' : '新增类型'"
        :visible.sync="visible"
        width="45%"
        :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="审批类型编号" prop="approval_type" v-if="form.id">
          <el-input size="small" v-model="form.approval_type" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="审批类型编号" prop="approval_type" v-if="!form.id">
          <el-input size="small" v-model="form.approval_type"></el-input>
        </el-form-item>
        <el-form-item label="审批类型名称" prop="approval_type_name">
          <el-input size="small" v-model="form.approval_type_name"></el-input>
        </el-form-item>
<!--        <el-form-item label="分行是否可操作" prop="branch_up">-->
<!--          <el-radio v-model="form.branch_up" label="0">否</el-radio>-->
<!--          <el-radio v-model="form.branch_up" label="1">是</el-radio>-->
<!--        </el-form-item>-->
        <el-form-item label="免审批项" prop="approval_items">
          <el-cascader
              v-model="form.approval_items"
              :options="items"
              :props="props"
              clearable
              filterable></el-cascader>
        </el-form-item>
        <el-form-item label="是否启用" prop="use_up">
          <el-radio v-model="form.use_up" label="0">否</el-radio>
          <el-radio v-model="form.use_up" label="1">是</el-radio>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              placeholder="请输入内容"
              v-model="form.remarks"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addApprovalType"
        >确 定
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import approvalField from "../../pages/flow/approvalField";


export default {
  components: {
    baseTable,
    basePage,
    approvalField,
  },
  name: "approvalType",
  data() {
    return {
      role_id: sessionStorage.getItem("role_id"),
      props: {multiple: true},
      form: {},
      dataList: [
        {
          label: "审批类型编号",
          prop: "approval_type",
          sort: true,
        },
        {
          label: "审批类型名称",
          prop: "approval_type_name",
          sort: true,
        },
        {
          label: "备注",
          prop: "remarks",
        },
        // {
        //   label: "分行是否可操作",
        //   prop: "branch_up",
        // },
        {
          label: "是否启用",
          prop: "use_up",
        },
      ],
      rules: {
        approval_type: [
          {required: true, message: "请输入审批类型编号", trigger: "blur"},
        ],
        approval_type_name: [
          {required: true, message: "请输入审批类型名称", trigger: "blur"},
        ],
        // branch_up: [
        //   {required: true, message: "请选择分行是否可操作", trigger: "change"},
        // ],
        use_up: [
          {required: true, message: "请选择是否启用", trigger: "change"},
        ],
      },
      approvalTypeList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
      items: [],
    };
  },
  mounted() {
    //this.getApprovalTypeList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {
    getApprovalField(approval_type) {

      this.$api.getApprovalField({"approval_type": approval_type}).then((res) => {
        this.items = [];
        let arr = res.data;

        if (arr != null && arr.length > 0) {
          for (let i = 0; i < arr.length; i++) {
            let obj = {};
            let arr2 = [];
            obj.value = arr[i].approval_items;
            obj.label = arr[i].approval_name;
            let arr1 = arr[i].list
            for (let j = 0; j < arr1.length; j++) {
              let obj1 = {};
              obj1.value = arr1[j].approval_items
              obj1.label = arr1[j].approval_name
              arr2.push(obj1)
            }
            obj.children = arr2
            this.items.push(obj)
          }
        }
        // console.log(this.items)
      })
    },

    // 新增类型/更新类型
    addApprovalType() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (!this.form.approval_type.match("[0-9]{3,3}")) {
            this.$message.error("审批类型编号格式不正确；例：001");
            return;
          }
          this.$api
              .checkApprovalType(this.form)
              .then((res) => {
                if (this.form.id) {
                  this.$api.updateApprovalType(this.form).then((res) => {
                    this.handleClose();
                    this.getApprovalTypeList();
                  });
                } else {
                  this.$api.addApprovalType(this.form).then((res) => {
                    this.handleClose();
                    this.getApprovalTypeList();
                  });
                }
              })
              .catch(() => {

              });
        }

      });
      this.items = [];
    },
    // 编辑审批类型
    editApprovalType(id, approval_type) {
      this.$api
          .getApprovalType({
            id,
          })
          .then((res) => {
            console.log(res);
            this.visible = true;
            this.form = res.data.msg[0];
            this.form.approval_items = res.data.approval_items;
          });
      this.getApprovalField(approval_type);
    },
    // 获取审批类型列表
    getApprovalTypeList() {
      this.$api
          .getApprovalTypeList({
            page: this.pageNum - 1,
            number: this.pageSize,
            ...this.form,
          })
          .then((res) => {
            res.data.approvalTypeList.map((item) => {
              return (
                   // item.branch_up = item.branch_up == "0" ? "否" : "是",
                  item.use_up = item.use_up == "0" ? "否" : "是");
            });
            this.approvalTypeList = res.data.approvalTypeList;
            this.total = res.data.total;
            //this.form = {};
          });
    },
    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getApprovalTypeList();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getApprovalTypeList();
    },
    // 删除审批类型
    deleteApprovalType(id) {
      this.$confirm("确定删除该审批类型吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      })
          .then(() => {
            this.$api
                .deleteApprovalType({
                  id: id,
                })
                .then((res) => {
                  console.log(res);
                  this.getApprovalTypeList();
                });
          })
          .catch(() => {
          });
    },

    handleClose() {
      this.visible = false;
      this.form = {};
      this.items = [];
    },
  },
  // 跳转保存数据
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (to.params.searchKey) {
        vm.form = to.params.searchKey;
      }
      vm.getApprovalTypeList();
    });
  },
};
</script>



