<template>
  <div style="margin-left: 1px;">
    <el-button size="small" type="primary" @click="approvalField()">免审批项目管理</el-button>
    <el-dialog title="免审批项目管理" :visible.sync="dialogVisible">
      <el-dialog :visible.sync="disible" :title="form.label">
        <el-form :model="form" :rules="rules">
          <el-form-item label="审核项名称" prop="approval_name" style="width: 200px">
            <el-input v-model="form.approval_name"></el-input>
          </el-form-item>
          <el-form-item label="审核项值" prop="approval_items" style="width: 200px">
            <el-input v-model="form.approval_items"></el-input>
          </el-form-item>
          <el-form-item label="是否为角色类" prop="is_role" style="width: 300px">
            <el-radio v-model="form.is_role" label="0">是</el-radio>
            <el-radio v-model="form.is_role" label="1">否</el-radio>
          </el-form-item>
        </el-form>
        <div slot="footer" style="text-align: center">
          <el-button size="small" @click="handleClose">取 消</el-button>
          <el-button type="primary" size="small" @click="addApprovalField">确 定
          </el-button>
        </div>
      </el-dialog>
      <el-tree
          :data="data"
          node-key="id"
          accordion
          :expand-on-click-node="false">
      <span class="custom-tree-node" slot-scope="{ node, data }">
        <span>{{ node.label }}</span>
        <div>
        <span v-if="node.level !== 3">
          <el-button
              type="text"
              size="mini"
              @click="() => append(data)">
            添加
          </el-button>
          </span>
          <span v-if="node.level !== 1">
          <el-button
              type="text"
              size="mini"
              @click="() => remove(node, data)"
          >
            删除
          </el-button>
        </span>
          </div>
      </span>
      </el-tree>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "approvalField",

  data() {

    return {
      rules: {
        approval_name: [
          {required: true, message: "请输入审核项名称", trigger: "blur"},
        ],
        approval_items: [
          {required: true, message: "请输入审核项值", trigger: "blur"},
        ],
        is_role: [
          {required: true, message: "请选择是否为角色类", trigger: "blur"},
        ],
      },
      form: {},
      data: [],
      disible: false,
      dialogVisible: false,
    }
  },
  mounted() {

  },
  methods: {
    getApprovalTypeByEnum() {
      this.data = []
      this.$api.getApprovalTypeByEnum().then((res1) => {
        res1.data.map((item) => {
          let arr0 = [];
          let obj0 = {};
          obj0.label = item.approvalTypeName;
          obj0.value = item.approvalType;
          obj0.approval_type = item.approvalType;
          this.$api.getApprovalField({"approval_type": item.approvalType}).then((res2) => {
            let arr = res2.data;

            if (arr != null && arr.length > 0) {
              for (let i = 0; i < arr.length; i++) {
                let obj2 = {};
                let arr2 = [];
                obj2.value = arr[i].approval_items;
                obj2.id = arr[i].id;
                obj2.label = arr[i].approval_name;
                obj2.approval_type = arr[i].approval_type;
                let arr1 = arr[i].list
                for (let j = 0; j < arr1.length; j++) {
                  let obj3 = {};
                  obj3.value = arr1[j].approval_items
                  obj3.label = arr1[j].approval_name
                  obj3.id = arr1[j].id;
                  // console.log(arr[j])
                  arr2.push(obj3)
                }
                obj2.children = arr2
                arr0.push(obj2)
              }
            }
          })
          obj0.children = arr0
          this.data.push(obj0)
        })
        // console.log(this.data)
      })
    },
    addApprovalField() {
      if (this.form.children !== null || this.form.children !== undefined) {
        delete this.form.children
      }
      if (this.form.approval_name === null || this.form.approval_name === undefined) {
        this.$message.error("请输入审核项名称");
        return;
      }
      if (this.form.approval_items === null || this.form.approval_items === undefined) {
        this.$message.error("请输入审核项值");
        return;
      }
      if (this.form.is_role === null || this.form.is_role === undefined) {
        this.$message.error("请选择是否为角色类");
        return;
      }
      this.$api.addApprovalField({
        ...this.form
      }).then(() => {
        this.$message.success("添加成功");
        this.handleClose();
        this.getApprovalTypeByEnum()
      })

    },
    handleClose() {
      this.disible = false;
    },
    append(data) {
      this.disible = true;
      this.form = JSON.parse(JSON.stringify(data));
      // console.log(data)
    },

    remove(node, data) {
      this.$api.addApprovalField({id: data.id, deleted: "1"}).then(() => {
        this.$message.success("删除成功");
        this.getApprovalTypeByEnum()
      })
    },
    approvalField() {

      this.dialogVisible = true;
      this.getApprovalTypeByEnum()
    },
  },
};
</script>


