<template>
  <div>
    <!--    <el-button type="primary" @click="addSetting" size="small">新增短信任务</el-button>-->
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-form-item label="短信任务名称">
        <el-input
            v-model="form.sms_pro "
            clearable
            placeholder="请输入规则名称"
            size="small"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getSettingList(true)"
            size="small"
            icon="el-icon-search"
        >搜索
        </el-button>
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="showList">
      <el-table-column label="短信接收人" prop="send_role" align="center" style="width: 5px">
        <template slot-scope="scope" v-if="roleList">
          <span>
            {{ computedsendRole(scope.row.send_role) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="80">
        <template slot-scope="scope">
          <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editSetting(scope.row)"
          >编辑
          </el-button
          >
        </template>
      </el-table-column>
    </base-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog
        :title="form.id ? '修改' : '新增'"
        :visible.sync="visible"
        width="45%"
        :before-close="handleClose"
    >
      <el-form ref="form" :model="form">
        <el-form-item label="短信任务名称" prop="sms_pro" style="width: 360px">
          <el-input size="midul" v-model="form.sms_pro "></el-input>
        </el-form-item>
        <el-form-item label="触发条件" prop="trigger_condition" style="width: 360px">
          <el-input size="small" v-model="form.trigger_condition"></el-input>
        </el-form-item>
        <el-form-item label="短信内容" prop="sms_content" style="width: 360px">
          <el-input size="small" v-model="form.sms_content" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="短信接收人" prop="send_role" style="width: 360px">
          <el-select size="small" v-model="form.send_role"
                     :disabled="form.id==='00000000002'|| form.id ==='00000000003'? true:false">
            <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name"
                       :value="item.role_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否启用" prop="use_up">
          <el-radio v-model="form.use_up" label="0">否</el-radio>
          <el-radio v-model="form.use_up" label="1">是</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addOrEditSetting"
        >确 定
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";

export default {
  components: {
    baseTable,
    basePage,
  },
  name: "setting",
  data() {
    return {
      form: {},
      roleList: [],
      dataList: [
        {
          label: "短信任务名称",
          prop: "sms_pro",
          width: "30",
        }, {
          label: "触发条件",
          prop: "trigger_condition",
          width: "40",
        }, {
          label: " 短信内容",
          prop: "sms_content",
        },
        // {
        //   label: "短信接收人",
        //   prop: "role_name",
        //   width:"30",
        // },
        {
          label: "是否启用",
          prop: "use_up",
          width: "20",
        },
      ],
      showList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
    };
  },

  mounted() {
    this.getRoleList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {
    computedsendRole(param) {
      let index = this.roleList.findIndex(function (item) {
        return item.role_id === param
      })

      return this.roleList[index].role_name
    },
    addOrEditSetting() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$api
              .addSetting(this.form)
              .then(() => {
                this.handleClose();
                this.getSettingList();
              })
              .catch(() => {

              });
        }
      })
    },
    //获取角色列表
    getRoleList() {
      this.$api.getUsertype().then((res) => {
        this.roleList = res.data;

        let obj1 = {role_id: '000001', role_name: '问题整改人'};
        let obj2 = {role_id: '000002', role_name: '审批人/沟通人'};
        this.roleList.push(obj1)
        this.roleList.push(obj2)
      });
    },
    addSetting() {
      this.form = {};
      this.visible = true;

    },
    // 编辑合规操作规则前操作
    editSetting(obj) {

      this.form = {};
      this.form = JSON.parse(JSON.stringify(obj))
      this.form.use_up = this.form.use_up === '否' ? '0' : '1';
      this.visible = true;
    },

    // 获取短信任务列表
    getSettingList(flag) {

      if (flag) {
        this.pageNum = 1;
      }
      if (this.form.sms_pro === null || this.form.sms_pro === "") {
        delete this.form.sms_pro
      }
      this.$api
          .getSettingList({
            page: this.pageNum - 1,
            number: this.pageSize,
            ...this.form,
          })
          .then((res) => {
            res.data.returnList.map((item) => {
              return (item.use_up = item.use_up === "0" ? "否" : "是");
            });
            this.showList = res.data.returnList;
            this.total = res.data.total;
          });
    },

    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getSettingList();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getSettingList();
    },
    handleClose() {
      this.visible = false;
      this.form = {};
    },
  },
  // 跳转保存数据
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (to.params.searchKey) {
        vm.form = to.params.searchKey;
      }
      vm.getSettingList();
    });
  },
};
</script>



