<template>
  <div>
    <el-button
        type="primary"
        size="small"
        @click="visible = true"
        style="margin-bottom: 10px"
        v-if="activeName == 'approval1'"
    >提交审批
    </el-button
    >
    <el-collapse v-model="activeNames" @change="handleChange">

      <el-collapse-item title="审批历史(点击展开)" name="approvalHis">
        <approval-his
        ></approval-his>
      </el-collapse-item>

      <el-collapse-item title="修改信息(点击展开)" name="updateInfo" v-if="approval_type == type003 || approval_type == type005 || approval_type == type006 || approval_type == type007 || approval_type == type008 || approval_type == type010
                || approval_type == type011 || approval_type == type012 || approval_type == type013 || approval_type == type014 || approval_type == type015 || approval_type == type016 || approval_type == type017 || approval_type == type018 || approval_type == type019">
        <div>
          <label style="color: red">{{ this.approvalApplyDetail.update_items }}</label>
        </div>
      </el-collapse-item>
      <el-collapse-item title="申请详情(点击展开)" name="baseInfo">
        <base-info-staff
            v-if="approval_type == type002 || approval_type == type003 || approval_type == type014 || approval_type == type015 || approval_type == type021"
        ></base-info-staff>
        <base-info-site
            v-if="approval_type == type004 || approval_type == type005 || approval_type == type006 || approval_type == type007 || approval_type == type008 || approval_type == type016 || approval_type == type017 || approval_type == type018 || approval_type == type019 || approval_type == type022"
        ></base-info-site>
        <base-info-team
            v-if="approval_type == type009 || approval_type == type010 || approval_type == type011 || approval_type == type012 || approval_type == type013 || approval_type == type020"
        ></base-info-team>
        <base-info-risk v-if="approval_type == type023">
        </base-info-risk>
      </el-collapse-item>
    </el-collapse>

    <el-dialog
        :title="'提交审批'"
        :visible.sync="visible"
        width="45%"
        :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="审批意见" prop="approval_opinion">
          <el-select
              v-model="form.approval_opinion"
              @change="cleanApprovalUser"
          >
            <el-option key="1" label="通过" value="1">通过</el-option>
            <el-option key="2" label="不通过" value="2">不通过</el-option>
            <el-option key="3" label="转办" value="3">转办</el-option>
            <el-option key="4" label="驳回" value="4">驳回</el-option>
            <el-option key="5" label="沟通" value="5">沟通</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审批意见描述" prop="approval_desc">
          <el-input
              type="textarea"
              maxlength="300"
              show-word-limit
              :autosize="{ minRows: 2, maxRows: 4 }"
              placeholder="请输入内容，最多300字"
              v-model="form.approval_desc"
          ></el-input>
        </el-form-item>
        <el-form-item label="下一审批人" prop="approval_user" v-if="form.approval_opinion == '1' && next_role">
          <el-select
              v-model="form.approval_user"
              clearable
              filterable
          >
            <el-option
                v-for="item in accountList"
                :key="item.account_id"
                :label="item.approval_name"
                :value="item.account_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="转办人" prop="up_approval_user" v-if="form.approval_opinion == '3'">
          <el-select
              v-model="form.up_approval_user"
              clearable
              filterable
          >
            <el-option
                v-for="item in upaccountList"
                :key="item.account_id"
                :label="item.approval_name"
                :value="item.account_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="沟通人" prop="gt_approval_user" v-if="form.approval_opinion == '5'">
          <el-select
              v-model="form.gt_approval_user"
              clearable
              filterable
              :multiple="true"
          >
            <el-option
                v-for="item in gtaccountList"
                :key="item.account_id"
                :label="item.account_id + '-' +item.name"
                :value="item.account_id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="submitApproval"
        >确 定
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseInfoStaff from "../bussiness/addStaff";
import baseInfoSite from "../bussiness/addSite";
import baseInfoTeam from "../bussiness/addTeam";
import approvalHis from "./approvalHis";
import baseInfoRisk from "../patrol/riskDetail";

export default {
  components: {
    baseInfoRisk,
    baseInfoStaff,
    baseInfoSite,
    baseInfoTeam,
    approvalHis,
  },
  name: "approvalDetail",
  data() {
    return {
      form: {},
      rules: {
        approval_opinion: [
          {required: true, message: "请选择审批意见", trigger: "blur"},
        ],
        approval_desc: [
          {required: true, message: "请输入审批意见描述", trigger: "blur"},
        ],
      },
      user: {},
      activeNames: ['baseInfo'],
      approval_type: this.$route.query.approval_type,
      apply_id: this.$route.query.apply_id,
      activeName: this.$route.query.activeName,
      next_role: this.$route.query.next_role,
      relation_id: this.$route.query.id,
      visible: false,
      accountList: [],
      upaccountList: [],
      gtaccountList: [],
      approvalApplyDetail: {},
      type002: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "002").code,
      type003: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "003").code,
      type004: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "004").code,
      type005: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "005").code,
      type006: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "006").code,
      type007: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "007").code,
      type008: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "008").code,
      type009: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "009").code,
      type010: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "010").code,
      type011: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "011").code,
      type012: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "012").code,
      type013: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "013").code,
      type014: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "014").code,
      type015: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "015").code,
      type016: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "016").code,
      type017: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "017").code,
      type018: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "018").code,
      type019: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "019").code,
      type020: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "020").code,
      type021: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "021").code,
      type022: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "022").code,
      type023: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "023").code,
    };
  },
  mounted() {
    this.getApprovalAccountList();
    this.getApprovalUpAccountList();
    this.getAllAccountList();
    this.getUpdateInfo();
    this.user =JSON.parse(sessionStorage.getItem("user"))
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {
    //查询修改项目
    getUpdateInfo() {
      this.$api.getUpdateInfo({
        apply_id: this.apply_id,
      }).then((res) => {
        this.approvalApplyDetail = res.data;
      });
    },
    cleanApprovalUser() {
      if (this.form.approval_opinion == '2' || this.form.approval_opinion == '3' || this.form.approval_opinion == '4' || this.form.approval_opinion == '5') {
        this.$set(this.form, "approval_user", null);
      }
      if (this.form.approval_opinion == '1' || this.form.approval_opinion == '2' || this.form.approval_opinion == '4' || this.form.approval_opinion == '5') {
        this.$set(this.form, "up_approval_user", null);
      }
      if (this.form.approval_opinion == '1' || this.form.approval_opinion == '2' || this.form.approval_opinion == '3' || this.form.approval_opinion == '4') {
        this.$set(this.form, "gt_approval_user", null);
      }
    },
    handleChange(val) {
      console.log(val);
    },
    handleClose() {
      this.visible = false;
      this.form = {};
    },
    //查询下一审批人员列表
    getApprovalAccountList() {
      this.$api.getApprovalAccountList2({
        apply_id: this.apply_id,
      }).then((res) => {
        this.accountList = res.data;
        if (res.data.length == 1) {
          this.$set(this.form, "approval_user", res.data[0].account_id);
        }
      });
    },
    //查询当前审批角色下其他人员
    getApprovalUpAccountList() {
      this.$api.getApprovalUpAccountList({
        apply_id: this.apply_id,
      }).then((res) => {
        this.upaccountList = res.data;
        for (let i = 0; i < this.upaccountList.length; i++) {
          if (this.upaccountList[i].account_id === this.user.account_id){
            this.upaccountList.splice(i,1);
          }
        }
        if (res.data.length == 1) {
          this.$set(this.form, "up_approval_user", res.data[0].account_id);
        }
      });
    },

    //查询所有人员
    getAllAccountList() {
      this.$api.getAllAccountList({}).then((res) => {
        this.gtaccountList = res.data;

        for (let i = 0; i < this.gtaccountList.length; i++) {
          if (this.gtaccountList[i].account_id === this.user.account_id){
             this.gtaccountList.splice(i,1);
          }
        }
        if (res.data.length == 1) {
          this.$set(this.form, "gt_approval_user", res.data[0].account_id);
        }
      });
    },

    //提交审批
    submitApproval() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.approval_opinion == "1" && this.next_role) {//同意  并有下一审批角色
            if (!this.form.approval_user) {
              this.$message.error("请选择下一审批人");
              return;
            }
          }
          if (this.form.approval_opinion == "3") {//转办
            if (!this.form.up_approval_user) {
              this.$message.error("请选择转办人");
              return;
            }
          }
          if (this.form.approval_opinion == "5") {//沟通
            if (!this.form.gt_approval_user || this.form.gt_approval_user == '') {
              this.$message.error("请选择沟通人");
              return;
            }
          }
          this.$api.submitApproval({
            apply_id: this.apply_id,
            relation_id: this.relation_id,
            ...this.form,
          }).then((res) => {
            this.$router.back(-1);
            //this.handleClose();
          });
        }
      });
    },
  },
};
</script>



