import Login from "./views/Login.vue";
import NotFound from "./views/404.vue";
import Home from "./views/Home.vue";

let routes = [
    {
        path: "/",
        name: "root",
        hidden: true,
        redirect: "/login",
        meta: {
            keepAlive: false
        }
    },
    {
        path: "/login",
        name: "登录",
        hidden: true,
        component: Login,
        meta: {
            keepAlive: false
        }
    },

    {
        path: "/404",
        component: NotFound,
        name: "404",
        hidden: true,
        meta: {
            keepAlive: false
        }
    },
    {
        path: "/",
        component: Home,
        name: "系统管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "admin",
                name: "用户管理",
                component: require("./views/pages/user/admin.vue"),
                meta: {
                    flag: true,
                    keepAlive: true
                },
            },
            {
                path: "adduser",
                component: require("./views/pages/user/adduser.vue"),
                name: "用户信息",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "menu",
                component: require("./views/pages/user/menu.vue"),
                name: "菜单管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "role",
                component: require("./views/pages/user/role.vue"),
                name: "角色管理",
                meta: {
                    keepAlive: true
                }
            },
        ],
    },

    {
        path: "/",
        component: Home,
        name: "组织架构",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "branch",
                component: require("./views/pages/user/branch.vue"),
                name: "机构管理",
                meta: {
                    flag: true,
                    keepAlive: true
                },
            },
            {
                path: "teamAdd",
                component: require("./views/pages/bussiness/addTeam.vue"),
                name: "团队新增",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "teamAddDetail",
                component: require("./views/pages/bussiness/addTeam.vue"),
                name: "团队详情",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "teamEdit",
                component: require("./views/pages/bussiness/team.vue"),
                name: "团队编辑",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "teamDetail",
                component: require("./views/pages/bussiness/team.vue"),
                name: "团队详情",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "addStaff",
                component: require("./views/pages/bussiness/addStaff.vue"),
                name: "新增人员",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "addStaffDetail",
                component: require("./views/pages/bussiness/addStaff.vue"),
                name: "人员详情",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "editStaff",
                component: require("./views/pages/bussiness/staff.vue"),
                name: "编辑人员",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "staffDetail",
                component: require("./views/pages/bussiness/staff.vue"),
                name: "人员详情",
                meta: {
                    keepAlive: true
                }
            },

        ],
    },

    {
        path: "/",
        component: Home,
        name: "站点管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "siteEdit",
                component: require("./views/pages/bussiness/site.vue"),
                name: "站点编辑",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "siteDetail",
                component: require("./views/pages/bussiness/site.vue"),
                name: "站点详情",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "addSite",
                component: require("./views/pages/bussiness/addSite.vue"),
                name: "站点新增",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "addSiteDetail",
                component: require("./views/pages/bussiness/addSite.vue"),
                name: "站点详情",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "exportSiteInfos",
                component: require("./views/pages/bussiness/exportSiteInfos.vue"),
                name: "导出全部站点信息",
                meta: {
                    keepAlive: false
                }
            },
        ],
    },

    {
        path: "/",
        component: Home,
        name: "审批管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "approvalType",
                name: "审批类型管理",
                component: require("./views/pages/flow/approvalType.vue"),
                meta: {
                    flag: true,
                    keepAlive: true
                },
            },
            {
                path: "approvalProcess",
                component: require("./views/pages/flow/approvalProcess.vue"),
                name: "审批流程管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "approvalProcessDetail",
                component: require("./views/pages/flow/approvalProcessDetail.vue"),
                name: "流程信息",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "approvalApply",
                component: require("./views/pages/flow/approvalApply.vue"),
                name: "我的申请",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "approvalApplyDetail",
                component: require("./views/pages/flow/approvalApplyDetail.vue"),
                name: "申请信息",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "approval",
                component: require("./views/pages/flow/approval.vue"),
                name: "我的审批",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "approvalDetail",
                component: require("./views/pages/flow/approvalDetail.vue"),
                name: "审批信息",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "approvalConnect",
                component: require("./views/pages/flow/approvalConnect.vue"),
                name: "我的沟通",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "approvalConnectDetail",
                component: require("./views/pages/flow/approvalConnectDetail.vue"),
                name: "沟通信息",
                meta: {
                    keepAlive: false
                }
            },
        ],
    },
    {
        path: "/",
        component: Home,
        name: "营销管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "siteWarnTime",
                name: "站点提醒设置",
                component: require("./views/pages/market/siteWarnTime.vue"),
                meta: {
                    flag: true,
                    keepAlive: true
                },
            },
            {
                path: "siteWarnRecord",
                component: require("./views/pages/market/siteWarnRecord.vue"),
                name: "站点提醒记录",
                meta: {
                    keepAlive: true
                }
            },
        ],
    },
    {
        path: "/",
        component: Home,
        name: "巡查管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "patrol",
                component: require("./views/pages/patrol/patrol.vue"),
                name: "巡查管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "mark_sheel",
                component: require("./views/pages/patrol/mark_sheel.vue"),
                name: "巡查操作",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "new_msheel",
                component: require("./views/pages/patrol/new_msheel.vue"),
                name: "巡检条款",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "quit_msheel",
                component: require("./views/pages/patrol/quit_msheel.vue"),
                name: "退出巡检条款",
                meta: {
                    keepAlive: false
                }
            },
        ],
    },{
        path: "/",
        component: Home,
        name: "风险管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [

            {
                path: "voucher",
                component: require("./views/pages/patrol/voucher.vue"),
                name: "助农交易管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "risk_customer",
                component: require("./views/pages/patrol/risk_customer.vue"),
                name: "风险客户名单",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "risk_customer_manage",
                component: require("./views/pages/patrol/risk_customer_manage.vue"),
                name: "风险客户管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "voucherDetail",
                component: require("./views/pages/patrol/voucherDetail.vue"),
                name: "助农交易详情",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "m_error",
                component: require("./views/pages/patrol/m_error.vue"),
                name: "风险管理",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "risk",
                component: require("./views/pages/patrol/risk.vue"),
                name: "风险处置",
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "riskDetail",
                component: require("./views/pages/patrol/riskDetail.vue"),
                name: "风险处置详情",
                meta: {
                    keepAlive: false
                }
            },
            {
                path: "operate",
                component: require("./views/pages/bussiness/operate.vue"),
                name: "合规模型",
                meta: {
                    keepAlive: true
                }
            },
        ],
    }, {
        path: "/",
        component: Home,
        name: "活动管理",
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false
        },
        children: [
            {
                path: "event",
                component: require("./views/pages/event/event.vue"),
                name: "活动管理",
                meta: {
                    keepAlive: true
                }
            },
        ],
    },
    {
        path: "/",
        name: '首页',
        component: Home,
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false,
        },
        children: [
            {
                path: "index",
                name: '首页',
                component: require("./views/index.vue"),
                meta: {
                    keepAlive: false
                }
            },
        ],
    },
    {
        name: '业绩管理',
        path: "/",
        component: Home,
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false,
        },
        children: [
            {
                path: '/deposit',
                name: '存款业绩',
                component: () => import('./views/pages/achievement/deposit.vue')
            },
            {
                path: '/aum',
                name: 'AUM业绩',
                component: () => import('./views/pages/achievement/aum.vue')
            },
            {
                path: '/expand',
                name: '拓展业绩',
                component: () => import('./views/pages/achievement/expand.vue')
            },
            {
                path: '/helpfarmers',
                name: '助农取款',
                component: () => import('./views/pages/achievement/helpfarmers.vue')
            },
            {
                path: '/customerdetails',
                name: '客户/账户明细',
                component: () => import('./views/pages/bussiness/customerdetails.vue')
            },
        ]
    },{
        name: '短信参数',
        path: "/",
        component: Home,
        leaf: true,
        hidden: true,
        meta: {
            keepAlive: false,
        },
        children:[
            {
                path: "setting",
                component: require("./views/pages/massge/setting"),
                name: "短信参数设置",
                meta: {
                    keepAlive: true
                }
            },
        ]
    }
];

export default routes;



