<template>
  <div>
    <el-row :gutter="50">
      <el-col :span="12">
        <el-divider direction="horizontal" content-position="left">
          个人基本信息
        </el-divider>
        <el-form
            ref="form"
            status-icon
            :rules="rules"
            :model="form"
            label-width="120px"
            style=""
        >
          <el-form-item
              label="工号"
              prop="mgr_id"
          >
            <el-input
                v-model="form.mgr_id"
                :disabled="
                id ? true : false
              "
                @change="checkAccountId()"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="姓名"
              prop="mgr_name"
          >
            <el-input
                v-model="form.mgr_name"
                :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="角色"
              prop="role"
          >
            <el-radio-group
                v-model="form.role"
                :disabled="check || (approval_type !== type014 && approval_type !== type002)"
            >
              <el-radio
                  v-for="item in roleList"
                  :key="item.role_id"
                  :label="item.role_id"
              >{{
                  item.role_name
                }}
              </el-radio
              >
            </el-radio-group>
          </el-form-item>
          <el-form-item
              label="所属机构"
              prop="branch_id"
          >
            <div style="display: flex">
              <el-input
                  :disabled="true"
                  v-model="
                  this.form.branch_name
                "
              ></el-input>
              <BranchTreePicker
                  @nodeselect="
                  nodeselectForShow
                "
                  :dfks="
                  this.form.branch_id
                "
                  v-show="!check && (approval_type === type015 || approval_type === type002)"
                  :appendToBody="true"
              ></BranchTreePicker>
            </div>
          </el-form-item>
          <el-form-item
              label="性别"
              prop="sex"
          >
            <el-radio-group
                v-model="form.sex"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            >
              <el-radio label="0"
              >男
              </el-radio
              >
              <el-radio label="1"
              >女
              </el-radio
              >
            </el-radio-group>
          </el-form-item>
          <el-form-item
              label="手机号"
              prop="phone"
          >
            <el-input
                v-model="form.phone"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="电子邮箱"
              prop="e_mail"
          >
            <el-input
                v-model="form.e_mail"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="年龄"
              prop="age"
          >
            <el-input
                v-model="form.age"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="学历"
              prop="education"
          >
            <el-radio-group
                v-model="form.education"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            >
              <el-radio label="0"
              >初中及以下
              </el-radio
              >
              <el-radio label="1"
              >高中（含中专等）
              </el-radio
              >
              <el-radio label="2"
              >大专
              </el-radio
              >
              <el-radio label="3"
              >本科
              </el-radio
              >
              <el-radio label="4"
              >研究生及以上
              </el-radio
              >
            </el-radio-group>
          </el-form-item>
          <el-form-item
              label="入职日期"
              prop="employment_date"
          >
            <el-date-picker
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期"
                v-model="
                form.employment_date
              "
                style="width: 100%"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            ></el-date-picker>
          </el-form-item>
          <el-form-item
              label="出生日期"
              prop="birthday"
          >
            <el-date-picker
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期"
                v-model="form.birthday"
                style="width: 100%"
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
            ></el-date-picker>
          </el-form-item>
          <el-form-item
              label="其他"
              prop="remarks"
          >
            <el-input
                :disabled="check || (approval_type !== type003 && approval_type !== type002)"
                type="textarea"
                :autosize="{
                minRows: 2,
                maxRows: 4,
              }"
                placeholder="请输入内容"
                v-model="form.remarks"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="审批人"
              v-if="
              !check && this.use_up === '1' && req_place !=='1'
            "
              prop="approval_user"
          >
            <el-select
                style="width: 100%"
                v-model="
                form.approval_user
              "
                :disabled="check"
                clearable
                filterable
                @click.native="getApprovalAccountList"
            >
              <el-option
                  v-for="item in accountList"
                  :key="item.account_id"
                  :label="
                  item.approval_name
                "
                  :value="item.account_id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button
                id="tempButton"
                v-if="!check && approval_type === type002 && req_place !=='1'"
                type="primary"
                size="medium"
                @click="tempSubmit"
            >{{ "暂存" }}
            </el-button>
            <el-button
                id="editButton"
                v-if="!check"
                type="primary"
                size="medium"
                @click="open"
            >{{ "提交" }}
            </el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider direction="horizontal" content-position="left">
          个人业绩数据
        </el-divider>
        <el-row class="table-item" v-for="(item, index) in depositList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({mgr_id:form.mgr_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider direction="horizontal" content-position="left">
          个人客户数据
        </el-divider>
        <el-row class="table-item" v-for="(item, index) in teamCusList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({mgr_id:form.mgr_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider direction="horizontal" content-position="left">
          个人拓展数据
        </el-divider>
        <el-row class="table-item" v-for="(item, index) in teamExpandList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({mgr_id:form.mgr_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <show-chart @get-data="childSearch" :option="option" ref="chartShow"></show-chart>

    <el-row v-if="check && req_place ==='0'" :gutter="50">
      <el-col :span="24">
        <el-divider direction="horizontal" content-position="center">
          审批记录
        </el-divider>
        <base-table :list="hisLogList" :data="approvalHisLogList">
          <el-table-column fixed="right" label="操作" align="center" width="240">
            <template slot-scope="scope">
              <el-button type="text" @click.native="showHisDetail(scope.row.id)">详情</el-button>
            </template>
          </el-table-column>
        </base-table>
      </el-col>
    </el-row>

    <el-dialog
        :title="'审批历史'"
        :visible.sync="his_visible"
        width="80%"
        :before-close="handleHisClose"
    >

      <base-table :list="hisList" :data="approvalHisList">
      </base-table>

      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleHisClose">关 闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import BranchTreePicker from "../../components/BranchTreePicker";
import addMixin from "@/mixins/addMixin.js";
import showChart from './components/showChart.vue';
import baseTable from "../../components/Table";

export default {
  mixins: [addMixin],
  components: {
    BranchTreePicker,
    showChart,
    baseTable
  },
  name: "addStaff",
  data() {
    return {
      depositList: [ //团队业绩数据
        {label: '资产余额', key: 'amt1'},
        {label: '资产年日均', key: 'amt2'},
        {label: '存款余额', key: 'amt3'},
        {label: '存款年日均', key: 'amt4'},
      ],
      teamCusList: [// 团队客户数据
        {label: '所有客户数', key: 'cus_num1'},
        {label: '高净值客户数', key: 'cus_num2'},
        {label: '价值客户数', key: 'cus_num3'},
        {label: '有效客户数', key: 'cus_num4'},
      ],
      teamExpandList: [ // 团队拓展数据
        {label: '试营业服务点数', key: 'site_num1'},
        {label: '试营业（已申请POS）服务点数', key: 'site_num2'},
        {label: '已开业服务点数', key: 'site_num3'},
        {label: '已退出服务点数', key: 'site_num4'},
      ],
      form: {
        roleids: [],
        suggestion: "",
      },
      id: "",
      userList: [],
      check: false,
      accountList: [],
      type002: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "002").code,
      type003: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "003").code,
      type014: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "014").code,
      type015: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "015").code,
      approval_type: this.$route.query.approval_type || this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "002").code,
      baseForm: {},
      roleList: [],
      use_up: "",
      req_place: "",
      parms: {},

      hisLogList: [
        {
          label: "申请日期",
          prop: "create_time",
          sort: true,
        },
        {
          label: "申请编号",
          prop: "id",
        },
        {
          label: "审批类型",
          prop: "approval_type_name",
        },
        {
          label: "审批状态",
          prop: "approval_status_name",
        },
      ],
      approvalHisLogList: [],

      hisList: [
        {
          label: "时间",
          prop: "create_time",
          sort: true,
        },
        {
          label: "节点名称",
          prop: "role_name",
        },
        {
          label: "操作人员编号",
          prop: "approval_user",
        },
        {
          label: "操作人员姓名",
          prop: "name",
        },
        {
          label: "操作",
          prop: "approval_opinion",
        },
        {
          label: "处理意见",
          prop: "approval_desc",
        },
      ],
      approvalHisList: [],
      his_visible: false,
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state
            .bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
    rules() {
      var checkID = (
          rule,
          value,
          callback
      ) => {
        if (value === undefined ||
            value == "" ||
            value == null) {
          callback();
        }
        // 身份证号正则
        var idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
        if (idcardReg.test(value)) {
          callback();
        } else {
          return callback(
              new Error("身份证非法")
          );
        }
      };
      var checkPhone = (
          rule,
          value,
          callback
      ) => {
        if (
            value === undefined ||
            value == "" ||
            value == null
        ) {
          callback();
        }
        var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
        if (phoneReg.test(value)) {
          callback();
        } else {
          return callback(
              new Error("手机号码非法")
          );
        }
      };
      var checkEmail = (
          rule,
          value,
          callback
      ) => {
        if (
            value === undefined ||
            value == "" ||
            value == null
        ) {
          callback();
        }
        var emailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/; // 电子邮箱正则
        if (emailReg.test(value)) {
          callback();
        } else {
          return callback(
              new Error("电子邮箱非法")
          );
        }
      };
      var checkAge = (
          rule,
          value,
          callback
      ) => {
        if (
            value === undefined ||
            value == "" ||
            value == null
        ) {
          callback();
        }
        var ageReg = /^(?:[1-9][0-9]?|1[01][0-9]|120)$/; // 年龄正则
        if (ageReg.test(value)) {
          callback();
        } else {
          return callback(
              new Error("年龄非法")
          );
        }
      };
      return {
        mgr_id: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type002 || this.$route.query.approval_type == this.type003,
            message: "请输入工号",
            trigger: "blur",
          },
          {
            min: 1,
            max: 10,
            message:
                "工号在 1 到 10 个字符",
            trigger: "blur",
          },
        ],
        mgr_name: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type002 || this.$route.query.approval_type == this.type003,
            message: "请输入员工姓名",
            trigger: "blur",
          },
          {
            min: 2,
            max: 5,
            message:
                "名字在 2 到 5 个字符",
            trigger: "blur",
          },
        ],
        role: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type002 || this.$route.query.approval_type == this.type014,
            message: "请选择角色",
            trigger: "change",
          },
        ],
        branch_id: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type002 || this.$route.query.approval_type == this.type015,
            message: "请选择所属机构",
            trigger: "change",
          },
        ],
        phone: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type002 || this.$route.query.approval_type == this.type003,
            message: "请输入手机号",
            trigger: "blur",
          },
          {
            validator: checkPhone,
            trigger: "blur",
          },
        ],
        e_mail: [
          {
            message: "请输入电子邮箱",
            trigger: "blur",
          },
          {
            validator: checkEmail,
            trigger: "blur",
          },
        ],
        age: [
          {
            message: "请输入年龄",
            trigger: "blur",
          },
          {
            validator: checkAge,
            trigger: "blur",
          },
        ],
        approval_user: [
          {
            required: true,
            message: "选择审批人",
            trigger: "change",
          },
        ],
      }
    },
  },
  mounted() {
    this.check =
        this.$route.query.check ===
        "false"
            ? false
            : Boolean(
            this.$route.query.check
            );
    this.id = this.$route.query.id;
    this.req_place = this.$route.query.req_place || '0';
    if (this.id) {
      this.$api
          .getStaffDetail({
            id: this.id,
            req_place: this.req_place,
          })
          .then((res) => {
            this.form =
                res.data.staffDetail;
            this.baseForm = JSON.parse(
                JSON.stringify(
                    res.data.staffDetail
                )
            );

            if (
                res.data.roleList.length ==
                1
            ) {
              this.$set(
                  this.form,
                  "role",
                  res.data.roleList[0]
                      .role_id
              );
              this.$set(
                  this.baseForm,
                  "role",
                  res.data.roleList[0]
                      .role_id
              );
            }
          });
    } else {
      //查询是否有暂存
      this.$api
          .getTempStaffDetail({})
          .then((res) => {
            if (res.data.staffDetail) {
              this.form = res.data.staffDetail;
              if (
                  res.data.roleList != null && res.data.roleList.length == 1
              ) {
                this.$set(
                    this.form,
                    "role",
                    res.data.roleList[0].role_id
                );
              }
            }
          });

    }
    this.getRoleList();
    //保存（首次提交）时查询审批角色对应审批人员
    // if (!this.check) {
    //   this.getApprovalAccountList();
    // }

      //查询是否启用
      if (!this.check) {
          this.getApprovalQY();
      }

    if (this.check && this.req_place === '0') {
      this.getApprovalHisLogList(this.id);
    }
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {

    // 审批记录列表
    getApprovalHisLogList(id) {
      this.$api.getApprovalHisLogList({
        "req_type": "2",
        "id": id,
      }).then((res) => {
        this.approvalHisLogList = res.data;
      });
    },
    showHisDetail(id) {
      this.approvalHisList = [];
      this.$api.getApprovalHisList({
        apply_id: id
      }).then((res) => {
        this.his_visible = true;
        this.approvalHisList = res.data;
      });
    },
    handleHisClose() {
      this.his_visible = false;
    },

    open() {
      this.$prompt('', '申请说明', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputType: 'textarea',
        inputPattern: /^(.|\n){1,300}$/,
        inputErrorMessage: '申请说明不能为空且不能超过300字',
        inputPlaceholder: '输入内容不能超过300字',
      }).then(({value}) => {
        this.form.suggestion = value
        this.onSubmit()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消提交'
        });
      });

    },
    getThanData(params) {
      this.parms.query_date = params.name;
      let parms = this.parms;
      let query_item = this.parms.query_item;
      if ("amt1" === query_item || "amt2" === query_item || "amt3" === query_item || "amt4" === query_item) {
        this.$api
            .getReport001List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      } else if ("cus_num1" === query_item || "cus_num2" === query_item || "cus_num3" === query_item || "cus_num4" === query_item) {
        this.$api
            .getReport002List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      } else if ("site_num1" === query_item || "site_num2" === query_item || "site_num3" === query_item || "site_num4" === query_item) {
        this.$api
            .getReport003List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      }
    },
    childSearch(params) {
      this.parms.query_date = params.query_date;
      this.getData(this.parms)
    },
    getData(parms) {
      this.parms = parms;
      this.$api
          .getReport004List(parms)
          .then((res) => {
            this.option.xAxis.data = res.data.dateList;
            this.option.series[0].data = res.data.dataList;
          });
    },
    //反显姓名
    checkAccountId() {
      this.$api
          .checkStaffDetail({
            user_id: this.form.mgr_id,
          })
          .then((res) => {
            this.form.mgr_name =
                res.data.name;
          });
      this.$set(
          this.form,
          "mgr_name",
          this.form.mgr_name
      );
    },
    // 角色类型
    getRoleList() {
      //if (this.check) return;
      this.$api
          .getStaffRoleList()
          .then((res) => {
            this.roleList = res.data;
            if (res.data.length == 1) {
              this.$set(
                  this.form,
                  "role",
                  res.data[0].role_id
              );
              this.$set(
                  this.baseForm,
                  "role",
                  res.data[0].role_id
              );
            }
          });
    },
    // 机构列表
    nodeselectForShow(node) {
      this.$set(
          this.form,
          "branch_name",
          node.branch_name
      );
      this.$set(
          this.form,
          "branch_id",
          node.branch_id
      );
    },
    // 保存（首次提交）时查询审批角色对应审批人员
    getApprovalAccountList() {
      this.$api
          .getApprovalAccountList({
            approval_type: this.$route.query.approval_type || this.type002,
            ...this.form,
          })
          .then((res) => {
            this.accountList =
                res.data.dataList;
            //this.use_up = res.data.use_up;
            // if (
            //     res.data.dataList.length ==
            //     1
            // ) {
            //     this.$set(
            //         this.form,
            //         "approval_user",
            //         res.data.dataList[0]
            //             .account_id
            //     );
            // }
          });
    },

      // 查询是否启用
      getApprovalQY() {
          this.$api
              .getApprovalQY({
                  approval_type: this.$route.query.approval_type || this.type002,
              })
              .then((res) => {
                  this.use_up = res.data.use_up;
              });
      },

    //暂存
    tempSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$set(this.form, "roleids", [
        this.form.role,
      ]);
      //人力系统校验工号
      this.$api
          .checkStaffDetail({
            user_id: this.form
                .mgr_id,
          })
          .then((res) => {
            this.$api
                .tempaddStaff(this.form)
                .then((res) => {
                  //this.handleClose();
                  this.$message.success(
                      "暂存成功"
                  );
                  //this.$router.back(-1);
                });
          });
    },

    onSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$set(this.form, "roleids", [
        this.form.role,
      ]);
      this.$refs.form.validate(
          (valid) => {
            if (valid) {
              this.form.approval_type =
                  this.$route.query
                      .approval_type ||
                  this.type002;
              this.form.req_place = this.req_place;
              this.form.apply_id = this.$route.query.apply_id || "";
              this.form.use_up = this.use_up;
              let update_items = [];
              for (let i in this
                  .baseForm) {
                this.baseForm[i] !=
                this.form[i] &&
                update_items.push(
                    this.labelList[i]
                );
              }
              if (
                  update_items.length > 0
              ) {
                this.form.update_items = update_items.join(
                    ","
                );
              }
              if (
                  this.form.approval_type ==
                  this.type002
              ) {
                //人力系统校验工号
                this.$api
                    .checkStaffDetail({
                      user_id: this.form
                          .mgr_id,
                    })
                    .then((res) => {
                      this.$api
                          .addStaff(this.form)
                          .then((res) => {
                            this.handleClose();
                            this.$message.success(
                                "操作成功，你已跳转至我的申请页面"
                            );
                            this.$router.push(
                                {
                                  path:
                                      "/approvalApply",
                                  name:
                                      "我的申请",
                                }
                            );
                            //this.$router.back(-1);
                          });
                    });
              } else {
                this.$api
                    .addStaff(this.form)
                    .then((res) => {
                      this.handleClose();
                      this.$message.success(
                          "操作成功，你已跳转至我的申请页面"
                      );
                      this.$router.push({
                        path:
                            "/approvalApply",
                        name: "我的申请",
                      });
                      //this.$router.back(-1);
                    });
              }
            }
          }
      );
    },
    handleClose() {
      this.visible = false;
      this.form = {};
    },
  },
  // 跳转保存数据
  beforeRouteLeave(to, from, next) {
    to.params.searchKey = this.$route.params.searchKey;
    next();
  },
};
</script>



