<template>
  <div>
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <!--<el-form-item label="所属机构" prop="branch_id" v-if="role == '1' || role == '2'">
        <el-select v-model="form.branch_id" filterable clearable size="small">
          <el-option
                  v-for="item in bankList"
                  :key="item.branch_id"
                  :label="item.branch_name"
                  :value="item.branch_id"
          ></el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item label="所属机构" prop="orgid">
        <div style="display: flex;">
          <el-input readonly="" size="small" v-model="orgname"></el-input>
          <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
        </div>
      </el-form-item>
      <el-form-item label="提醒日期" prop="start_date">
        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.start_date" style="width: 200px;"></el-date-picker>
      </el-form-item>
      <el-form-item label="——" prop="end_date">
        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.end_date" style="width: 200px;"></el-date-picker>
      </el-form-item>
      <el-form-item label="站点编号">
        <el-input
                v-model="form.site_no"
                clearable
                placeholder="请输入站点编号"
                size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="站点名称">
        <el-input
                v-model="form.site_name"
                clearable
                placeholder="请输入站点名称"
                size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="客户经理编号">
        <el-input
                v-model="form.mgr_id"
                clearable
                placeholder="请输入客户经理编号"
                size="small"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
                type="primary"
                @click="getSiteWarnRecordList"
                size="small"
                icon="el-icon-search"
        >搜索
        </el-button
        >
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="siteWarnRecordList">
    </base-table>
    <base-page
            :page="pageNum"
            :size="pageSize"
            :total="total"
            @size="changeSize"
            @page="changePage"
    ></base-page>
  </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";

    export default {
        components: {
            baseTable,
            basePage,
            BranchTreePicker
        },
        name: "siteWarnRecord",
        data() {
            return {
                role: sessionStorage.getItem("role"),
                form: {},
                orgid:"",
                orgname:"",
                dataList: [
                    {
                        label: "站点编号",
                        prop: "site_no",
                        sort: true,
                    },
                    {
                        label: "站点名称",
                        prop: "site_name",
                        sort: true,
                    },
                    {
                        label: "站点状态",
                        prop: "site_status",
                        sort: true,
                    },
                    {
                        label: "提醒状态",
                        prop: "site_step",
                    },
                    {
                        label: "所属机构",
                        prop: "org_name",
                    },
                    {
                        label: "落地支行",
                        prop: "branch_name",
                    },
                    {
                        label: "客户经理",
                        prop: "mgr_id",
                    },
                    {
                        label: "手机号码",
                        prop: "phone",
                    },
                    {
                        label: "是否发送短信",
                        prop: "send_msg",
                    },
                    {
                        label: "是否推送消息",
                        prop: "push_msg",
                    },
                    {
                        label: "提醒时间",
                        prop: "create_time",
                        sort: true,
                    },
                ],
                siteWarnRecordList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
        mounted() {
            //this.getSiteWarnRecordList();
            //this.getBankList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            // 机构列表
            nodeselectForSerch(node) {
                console.log(node);
                this.orgname = node.branch_name;
                this.orgid = node.branch_id;
            },
            /*getBankList() {
                if (this.bankList.length != 0) return;
                this.$api.getBranch().then((res) => {
                    this.bankList = res.data;
                });
            },*/
            // 获取列表
            getSiteWarnRecordList() {
                this.$api
                    .getSiteWarnRecordList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        orgid: this.orgid,
                        ...this.form,
                    })
                    .then((res) => {
                        res.data.siteWarnRecordList.map((item) => {
                            return (item.send_msg = item.send_msg == "1" ? "是" : "否",
                                    item.push_msg = item.push_msg == "1" ? "是" : "否",
                                    item.site_status = item.site_status === "0" ? "试营业" : item.site_status = item.site_status === "1" ? "试营业（已申请pos）" : item.site_status === "2" ? "已开业":"已退出");
                        });
                        this.siteWarnRecordList = res.data.siteWarnRecordList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getSiteWarnRecordList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getSiteWarnRecordList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getSiteWarnRecordList();
            });
        },
    };
</script>



