<template>
    <div>
        <el-form :inline="true" :model="form" style="margin-top: 15px">
            <el-form-item label="审批类型编号">
                <el-input
                        v-model="form.approval_type"
                        clearable
                        placeholder="请输入审批类型编号"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="审批类型名称">
                <el-input
                        v-model="form.approval_type_name"
                        clearable
                        placeholder="请输入审批类型名称"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button
                        type="primary"
                        @click="getApprovalTypeForProcessList"
                        size="small"
                        icon="el-icon-search"
                >搜索
                </el-button
                >
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="approvalTypeList">
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="showApprovalType(scope.row.approval_type)"
                    >查看
                    </el-button
                    >
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";

    export default {
        components: {
            baseTable,
            basePage,
        },
        name: "approvalProcess",
        data() {
            return {
                form: {},
                dataList: [
                    {
                        label: "审批类型编号",
                        prop: "approval_type",
                        sort: true,
                    },
                    {
                        label: "审批类型名称",
                        prop: "approval_type_name",
                        sort: true,
                    },
                    {
                        label: "备注",
                        prop: "remarks",
                    },
                ],
                approvalTypeList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        mounted() {
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            // 获取审批类型列表
            getApprovalTypeForProcessList() {
                this.$api
                    .getApprovalTypeForProcessList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        ...this.form,
                    })
                    .then((res) => {
                        this.approvalTypeList = res.data.approvalTypeList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },
            // 查看审批类型下审批流程列表
            showApprovalType(approval_type) {
                this.$router.push({
                    path: "/approvalProcessDetail",
                    name: "流程信息",
                    query: {
                        approval_type: approval_type,
                    },
                    params: {
                        //approval_type: approval_type,
                    },
                });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getApprovalTypeForProcessList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getApprovalTypeForProcessList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getApprovalTypeForProcessList();
            });
        },
    };
</script>



