<template>
    <div>
        <el-form :inline="true" :model="form" style="margin-top: 15px">
            <el-form-item label="申请日期" prop="start_date">
                <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.start_date" style="width: 200px;"></el-date-picker>
            </el-form-item>
            <el-form-item label="——" prop="end_date">
                <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.end_date" style="width: 200px;"></el-date-picker>
            </el-form-item>
            <el-form-item label="申请编号">
                <el-input
                        v-model="form.id"
                        clearable
                        placeholder="请输入申请编号"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="申请类型" prop="approval_type">
                <el-select v-model="form.approval_type" clearable size="small">
                    <el-option
                            v-for="item in approvalTypeList"
                            :key="item.approvalType"
                            :label="item.approvalTypeName"
                            :value="item.approvalType"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="审批状态" prop="approval_status">
                <el-select v-model="form.approval_status" clearable size="small">
                    <el-option
                            v-for="item in approvalStatusList"
                            :key="item.approvalStatus"
                            :label="item.approvalStatusName"
                            :value="item.approvalStatus"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button
                        type="primary"
                        @click="getApprovalApplyList"
                        size="small"
                        icon="el-icon-search"
                >搜索
                </el-button
                >
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="approvalApplyList">
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="showApprovalApply(scope.row.id,scope.row.approval_type,scope.row.relation_id,scope.row.approval_status)"
                    >查看
                    </el-button
                    >
                    <el-button
                            type="text"
                            icon="el-icon-star-on"
                            @click="fqApprovalApply(scope.row.id,scope.row.approval_type)"
                            v-if="scope.row.approval_status == '1'"
                    >废弃
                    </el-button
                    >
                    <el-button
                            type="text"
                            icon="el-icon-star-off"
                            @click="hfApprovalApply(scope.row.id,scope.row.approval_type)"
                            v-if="scope.row.approval_status == '9'"
                    >恢复
                    </el-button
                    >
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";

    export default {
        components: {
            baseTable,
            basePage,
        },
        name: "approvalApply",
        data() {
            return {
                role: sessionStorage.getItem("role"),
                form: {},
                dataList: [
                    {
                        label: "申请编号",
                        prop: "id",
                        sort: true,
                    },
                    {
                        label: "申请类型",
                        prop: "approval_type_name",
                    },
                    {
                        label: "申请项目",
                        prop: "message",
                    },
                    {
                        label: "审批状态",
                        prop: "approval_status_name",
                    },
                    {
                        label: "申请时间",
                        prop: "create_time",
                        sort: true,
                    },
                ],
                approvalTypeList:[],
                approvalStatusList:[],
                approvalApplyList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        mounted() {
            this.getApprovalTypeList();
            this.getApprovalStatusList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            // 审批类型列表
            getApprovalTypeList() {
                if (this.approvalTypeList.length != 0) return;
                this.$api.getApprovalTypeByEnum().then((res) => {
                    this.approvalTypeList = res.data;
                });
            },
            // 审批状态列表
            getApprovalStatusList() {
                if (this.approvalStatusList.length != 0) return;
                this.$api.getApprovalStatusByEnum().then((res) => {
                    this.approvalStatusList = res.data;
                });
            },

            // 获取我的申请列表
            getApprovalApplyList() {
                this.$api
                    .getApprovalApplyList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        ...this.form,
                    })
                    .then((res) => {
                        this.approvalApplyList = res.data.approvalApplyList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },

            // 查看我的申请
            showApprovalApply(apply_id,approval_type,relation_id,approval_status) {

                let check = true;
                if(approval_status == '4'){
                    //驳回
                    check = false;
                }

                this.$router.push({
                    path: "/approvalApplyDetail",
                    name: "申请信息",
                    query: {
                        id: relation_id,
                        role: this.role,
                        check: check, // 查看不可编辑
                        apply_id: apply_id,
                        approval_type: approval_type,
                        req_place: '1',
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },

            // 废弃此申请流程
            fqApprovalApply(apply_id,approval_type) {
                this.$confirm("确定废弃该申请吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .fqApprovalApply({
                                apply_id: apply_id,
                                approval_type: approval_type,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getApprovalApplyList();
                            });
                    })
                    .catch(() => {
                    });
            },
            // 恢复此申请流程
            hfApprovalApply(apply_id,approval_type) {
                this.$confirm("确定恢复该申请吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .hfApprovalApply({
                                apply_id: apply_id,
                                approval_type: approval_type,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getApprovalApplyList();
                            });
                    })
                    .catch(() => {
                    });
            },


            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getApprovalApplyList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getApprovalApplyList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getApprovalApplyList();
            });
        },
    };
</script>



