<template>
    <div>
        <el-form :inline="true" :model="form">
           <el-button type="warning" size="small" @click="exportSiteInfos" v-if="isSiteDetail" :loading="loading">导出全部查询数据</el-button>
            <div style="margin-top: 15px">
                <el-form-item label="新建日期" prop="start_date">
                    <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期"
                                    v-model="form.start_date" style="width: 200px;"></el-date-picker>
                </el-form-item>
                <el-form-item label="——" prop="end_date">
                    <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期"
                                    v-model="form.end_date" style="width: 200px;"></el-date-picker>
                </el-form-item>
                <el-form-item label="所属机构" prop="orgid">
                    <div style="display: flex;">
                        <el-input readonly="" size="small" v-model="orgname"></el-input>
                        <BranchTreePicker @nodeselect="nodeselectForSerchOrg"></BranchTreePicker>
                    </div>
                </el-form-item>
                <el-form-item label="落地支行" prop="bankid">
                    <div style="display: flex;">
                        <el-input readonly="" size="small" v-model="bankname"></el-input>
                        <BranchTreePickerAll @nodeselect="nodeselectForSerchBank"></BranchTreePickerAll>
                    </div>
                </el-form-item>
                <el-form-item label="站点编号">
                    <el-input
                            v-model="form.site_no"
                            clearable
                            placeholder="请输入站点编号"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="站点名称">
                    <el-input
                            v-model="form.site_name"
                            clearable
                            placeholder="请输入站点名称"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="站点状态" prop="status">
                    <el-select v-model="form.status" clearable size="small">
                        <el-option
                                key="0"
                                label="试营业"
                                value="0"
                        ></el-option>
                        <el-option
                                key="1"
                                label="试营业（已申请pos）"
                                value="1"
                        ></el-option>
                        <el-option
                                key="2"
                                label="已开业"
                                value="2"
                        ></el-option>
                        <el-option
                                key="3"
                                label="已退出"
                                value="3"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="客户经理编号">
                    <el-input
                            v-model="form.mgr_id"
                            clearable
                            placeholder="请输入客户经理编号"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="站长姓名">
                    <el-input
                            v-model="form.master_name"
                            clearable
                            placeholder="请输入站长姓名"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button
                            type="primary"
                            @click="getSiteList"
                            size="small"
                            icon="el-icon-search"
                    >搜索
                    </el-button>
                </el-form-item>
            </div>
        </el-form>
        <base-table :list="dataList" :data="siteList">
            <el-table-column fixed="right" label="操作" align="center" width="240">
                <template slot-scope="scope">
                    <el-dropdown placement="bottom" v-if="!isSiteDetail">
              <span
                      class="el-dropdown-link"
                      style="color: #e6a23c; margin-left: 10px"
                      @click="beforeUpdate(scope.row.status, scope.row.approval_status, scope.row.site_show)"
              >
               状态调整<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="toPos(scope.row.id, false, type006)"
                                              v-if="scope.row.status=='试营业' && scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >pos申请
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="toOpen(scope.row.id, false, type007)"
                                              v-if="(scope.row.status=='试营业' || scope.row.status=='试营业（已申请pos）') && scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >pos和开业申请
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="toOpen(scope.row.id, false, type022)"
                                              v-if="scope.row.status=='已开业' && scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >转试营业申请
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="toQuit(scope.row.id, false, type008)"
                                              v-if="scope.row.status!='已退出' && scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >退出申请
                            </el-dropdown-item
                            >
                        </el-dropdown-menu>
                    </el-dropdown>
                    <el-dropdown placement="bottom" v-if="!isSiteDetail">
              <span
                      class="el-dropdown-link"
                      style="color: #e6a23c; margin-left: 10px"
                      @click="beforeEdit(scope.row.approval_status, scope.row.site_show)"
              >
               信息调整<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="editSite(scope.row.id, false, type005)"
                                              v-if="scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >站点基本信息调整
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="editSite(scope.row.id, false, type016)"
                                              v-if="scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >站点站长信息调整
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="editSite(scope.row.id, false, type017)"
                                              v-if="scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >站点POS信息调整
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="editSite(scope.row.id, false, type018)"
                                              v-if="scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >站点管辖客户经理调整
                            </el-dropdown-item
                            >
                            <el-dropdown-item @click.native="editSite(scope.row.id, false, type019)"
                                              v-if="scope.row.approval_status=='2' && scope.row.site_show=='0'"
                            >站点落地支行调整
                            </el-dropdown-item
                            >
                        </el-dropdown-menu>
                    </el-dropdown>
                    <template v-else>
                        <el-button type="text" @click.native="editSite(scope.row.id, true)">查看</el-button>
                        <!--<el-dropdown placement="bottom">-->
                            <!--<el-button-->
                              <!--type="text"-->
                              <!--class="el-dropdown-link"-->
                              <!--style="color: #20a0ff; margin-left: 10px"-->
                            <!--&gt;-->
                                <!--选择二维码 <i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>-->
                            <!--</el-button>-->
                            <!--<el-dropdown-menu slot="dropdown">-->
                                <!--<el-dropdown-item @click.native="getqrcode(scope.row.id, 0)">-->
                                   <!--<span style="color: #20a0ff;">粤分享</span> <i class="fa fa-qrcode" style="color: #20a0ff; margin-left: 10px;"   />-->
                                <!--</el-dropdown-item>-->
                                <!--&lt;!&ndash;<el-dropdown-item @click.native="getqrcode(scope.row.id, 1)">&ndash;&gt;-->
                                    <!--&lt;!&ndash;<span style="color: #20a0ff;">线上存款</span><i class="fa fa-qrcode" style="color: #20a0ff; margin-left: 10px;"   />&ndash;&gt;-->
                                <!--&lt;!&ndash;</el-dropdown-item>&ndash;&gt;-->
                            <!--</el-dropdown-menu>-->
                        <!--</el-dropdown>-->

                    </template>
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";
    import BranchTreePickerAll from "../../components/BranchTreePickerAll";
    import XLSX from "xlsx";


    export default {
        name: "site",
        components: {
            baseTable,
            basePage,
            BranchTreePicker,
            BranchTreePickerAll
        },
        data() {
            return {
                user: JSON.parse(sessionStorage.getItem("user")),
                role_id: sessionStorage.getItem("role_id"),
                role: sessionStorage.getItem("role"),
                form: {},
                bankid: "",
                bankname: "",
                orgid: "",
                orgname: "",
                dataList: [
                    {
                        label: "站点编号",
                        prop: "site_no",
                        sort: true,
                    },
                    {
                        label: "站点名称",
                        prop: "site_name",
                    },
                    {
                        label: "所属机构",
                        prop: "org_name",
                    },
                    {
                        label: "落地支行",
                        prop: "branch_name",
                    },
                    {
                        label: "管辖客户经理",
                        prop: "mgr_name",
                    },
                    {
                        label: "服务点状态",
                        prop: "status",
                    },
                    {
                        label: "资产余额（元）",
                        prop: "amt1",
                    },
                    {
                        label: "资产年日均（元）",
                        prop: "amt2",
                    },
                    {
                        label: "存款余额（元）",
                        prop: "amt3",
                    },
                    {
                        label: "存款年日均（元）",
                        prop: "amt4",
                    },
                    {
                        label: "客户数",
                        prop: "cus_num1",
                    },
                ],
                siteList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                flag: "",
                type005: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "005").code,
                type006: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "006").code,
                type007: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "007").code,
                type008: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "008").code,
                type016: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "016").code,
                type017: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "017").code,
                type018: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "018").code,
                type019: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "019").code,
                type022: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "022").code,
                loading: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
            isSiteDetail() {
                return this.$route.path === '/siteDetail'
            },
        },
        mounted() {
        },
        methods: {
            getqrcode(id, codeType){
                this.$api.getqrcode({
                    id: id,
                    codeType: codeType
                }).then((res) => {
                    let site_name = res.data.site_name;
                    let qrcode = res.data.qrcode;
                    let htmlstr = '<img style="width: 390px; height: 390px" src="'+qrcode+'"/>';
                    this.$alert(htmlstr, site_name, {
                        dangerouslyUseHTMLString: true,
                        showClose: false,
                        confirmButtonText : "关闭"
                    });
                })
            },
            //所属机构列表
            nodeselectForSerchOrg(node) {
                console.log(node);
                this.orgname = node.branch_name;
                this.orgid = node.branch_id;
            },
            // 落地支行列表
            nodeselectForSerchBank(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },
            // 导出
            exportSiteInfos() {
                this.loading = true;
                this.$api.exportSiteInfos({
                    orgid: this.orgid,
                    bankid: this.bankid,
                    ...this.form
                }).then((res) => {
                    this.loading = false;
                    var fileName = res.data.fileName;
                    var filePath = res.data.filePath;
                    if (null == fileName || null == filePath) {
                        this.$message({
                            message: "导出失败，系统异常",
                            type: "error",
                        });
                        return false;
                    }

                    var objSelector = document.createElement("form");
                    document.body.appendChild(objSelector);
                    objSelector.method = "post";
                    objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
                    //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
                    objSelector.enctype = "multipart/form-data";
                    var input01 = document.createElement("input");
                    var input02 = document.createElement("input");
                    var input03 = document.createElement("input");
                    var input04 = document.createElement("input");
                    input01.setAttribute("name", "filePath");
                    input01.setAttribute("type", "hidden");
                    input01.setAttribute("value", filePath);
                    input02.setAttribute("name", "fileName");
                    input02.setAttribute("type", "hidden");
                    input02.setAttribute("value", fileName);
                    input03.setAttribute("name", "isDownload");
                    input03.setAttribute("type", "hidden");
                    input03.setAttribute("value", "1");
                    input04.setAttribute("name", "isDelete");
                    input04.setAttribute("type", "hidden");
                    input04.setAttribute("value", "1");
                    objSelector.appendChild(input01);
                    objSelector.appendChild(input02);
                    objSelector.appendChild(input03);
                    objSelector.appendChild(input04);
                    objSelector.submit();

                    this.$message({
                        message: "导出成功",
                        type: "success",
                    });
                });
            },

            beforeUpdate(status, approval_status, is_show){
                if(status === '已退出' || approval_status !== '2' || is_show !== '0'){
                    this.$message({
                        message: "暂无可做操作",
                        type: "warning",
                    });
                }
            },

            beforeEdit(approval_status, is_show){
                if(approval_status !== '2' || is_show !== '0'){
                    this.$message({
                        message: "暂无可做操作",
                        type: "warning",
                    });
                }
            },


            // 编辑站点
            editSite(id, flag, approval_type) {
                this.$router.push({
                    path: "/addSiteDetail",
                    // name: "站点信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 申请pos
            toPos(id, flag, approval_type) {

                this.$router.push({
                    path: "/addSiteDetail",
                    // name: "站点信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 申请pos及开业
            toOpen(id, flag, approval_type) {
                this.$router.push({
                    path: "/addSiteDetail",
                    // name: "站点信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 申请退出
            toQuit(id, flag, approval_type) {
                this.$router.push({
                    path: "/addSiteDetail",
                    // name: "站点信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 获取站点列表
            getSiteList() {
                this.$api.getSiteList({
                    page: this.pageNum - 1,
                    number: this.pageSize,
                    orgid: this.orgid,
                    bankid: this.bankid,
                    ...this.form,
                })
                    .then((res) => {
                        res.data.siteList.map((item) => {
                            return (item.site_source = item.site_source === "0" ? "自拓" : item.site_source = item.site_source === "1" ? "转介绍" : "其他",
                                item.status = item.status === "0" ? "试营业" : item.status = item.status === "1" ? "试营业（已申请pos）" : item.status === "2" ? "已开业" : "已退出");
                        });
                        this.siteList = res.data.siteList;
                        this.total = res.data.total;
                        // this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageNum = 1;
                this.pageSize = size;
                this.getSiteList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getSiteList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getSiteList();
            });
        },
    };
</script>



