<template>
  <div>
    <el-button
      type="primary"
      size="small"
      @click="visible = true"
      style="margin-bottom: 10px"
      >新增角色</el-button
    >
    <base-table :list="dataList" :data="roleList">
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            type="text"
            icon="el-icon-edit-outline"
            @click="editRole(scope.row.role_id)"
            >编辑</el-button
          >
          <el-button
            type="text"
            icon="el-icon-delete"
            style="color: red"
            v-if="scope.row.role_id !== super_manager && scope.row.role_id !== head_manager && scope.row.role_id !== sub_manager && scope.row.role_id !== team_leader && scope.row.role_id !== customer && scope.row.role_id !== head_science && scope.row.role_id !== sub_leader && scope.row.role_id !== area_leader"
            @click="deleteRole(scope.row.role_id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </base-table>
    <base-page :page="pageNum" :size="pageSize" :total="total" @size="changeSize" @page="changePage"></base-page>
    <el-dialog
      :title="form.role_id ? '修改角色' : '新增角色'"
      :visible.sync="visible"
      width="45%"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="角色名称" prop="role_name">
          <el-input size="small" v-model="form.role_name" :disabled="form.role_id === super_manager || form.role_id === head_manager || form.role_id === sub_manager || form.role_id === team_leader || form.role_id === customer || form.role_id === head_science || form.role_id === sub_leader || form.role_id === area_leader"></el-input>
        </el-form-item>
        <el-form-item label="角色类型" prop="role_type">
          <el-radio v-model="form.role_type" label="1">普通用户</el-radio>
          <!-- <el-radio v-model="form.role_type" label="2">商户</el-radio> -->
        </el-form-item>
        <el-form-item label="角色级别" prop="role_level">
          <el-radio-group v-model="form.role_level" :disabled="form.role_id === super_manager || form.role_id === head_manager || form.role_id === sub_manager || form.role_id === team_leader || form.role_id === customer || form.role_id === head_science || form.role_id === sub_leader || form.role_id === area_leader">
            <el-radio label="1">一级(总行)</el-radio>
            <el-radio label="2">二级(分行/支行)</el-radio>
            <el-radio label="3">三级(片区/团队)</el-radio>
            <el-radio label="4">四级(客户经理)</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="PC端菜单" prop="menuids">
          <div style="overflow-y: scroll; height: 200px; width: 100%">
            <el-tree
              ref="tree1"
              :data="menuList"
              show-checkbox
              node-key="menu_code"
              icon-class="el-icon-s-flag"
              :default-checked-keys="[5]"
              :props="defaultProps"
              default-expand-all
              check-on-click-node
            >
            </el-tree>
          </div>
        </el-form-item>
        <el-form-item label="APP端菜单" prop="mobile_menuids">
          <div style="overflow-y: scroll; height: 200px; width: 100%">
            <el-tree
                    ref="tree2"
                    :data="menuList"
                    show-checkbox
                    node-key="menu_code"
                    icon-class="el-icon-s-flag"
                    :default-checked-keys="[5]"
                    :props="defaultProps"
                    default-expand-all
                    check-on-click-node
            >
            </el-tree>
          </div>
        </el-form-item>
        <el-form-item label="角色描述" prop="role_desc">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder="请输入内容"
            v-model="form.role_desc"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addRole"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";

export default {
  name: "role",
  components: {
    baseTable,
    basePage,
  },
  data() {
    return {
      defaultProps: {
        label: "menu_name",
        children: "sub_tree",
      },
      form: {
        menuids: [],
        mobile_menuids:[],
      },
      dataList: [
        {
          label: "角色名称",
          prop: "role_name",
        },
        {
          label: "角色类型",
          prop: "role_type",
        },
        {
          label: "角色级别",
          prop: "role_level",
        },
        {
          label: "角色id",
          prop: "role_id",
        },
        {
          label: "角色描述",
          prop: "role_desc",
        },
      ],
      rules: {
        role_name: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
        ],
        role_type: [
          { required: true, message: "请选择角色类型", trigger: "change" },
        ],
          role_level: [
              { required: true, message: "请选择角色级别", trigger: "change" },
          ],
        firstMenu1: [],
        firstMenu2: [],
      },
      menuList: [],
      roleList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
      super_manager: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"super_manager").value,
      head_manager: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"head_manager").value,
      head_science: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"head_science").value,
      sub_manager: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"sub_manager").value,
      sub_leader: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"sub_leader").value,
      area_leader: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"area_leader").value,
      team_leader: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"team_leader").value,
      customer: this.$loacalData.code_GetEnums(this.$loacalData.listRole,"customer").value,
    };
  },
  mounted() {
    this.getRoleList();
    this.getMenuList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {
            menuids: [],
            mobile_menuids: [],
        };
      }
    },
  },
  methods: {
    // 获取所有菜单
    getMenuList() {
      this.$api.getMenu().then((res) => {
        this.menuList = res.data.menu_list;
        this.firstMenu1 = res.data.menu_list
          .filter((item) => {
            return item.menu_path == "/";
          })
          .map((item) => {
            return item.menu_code;
          });
          this.firstMenu2 = res.data.menu_list
              .filter((item) => {
                  return item.menu_path == "/";
              })
              .map((item) => {
                  return item.menu_code;
              });
        console.log(this.firstMenu1);
        console.log(this.firstMenu2);
      });
    },
    // 新增角色/更新角色
    addRole() {
      this.form.menuids = [
        ...this.$refs.tree1.getCheckedKeys(),
        ...this.$refs.tree1.getHalfCheckedKeys(),
      ];
      this.form.mobile_menuids = [
        ...this.$refs.tree2.getCheckedKeys(),
        ...this.$refs.tree2.getHalfCheckedKeys(),
      ];
      if (this.form.menuids.length == 0) {
        this.$message.error("请选择PC端角色权限");
        return;
      }
      if (this.form.mobile_menuids.length == 0) {
        this.$message.error("请选择APP端角色权限");
        return;
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.role_id) {
            this.$api.updateRole(this.form).then((res) => {
              this.handleClose();
              this.getRoleList();
            });
          } else {
            this.$api.addRole(this.form).then((res) => {
              this.handleClose();
              this.getRoleList();
            });
          }
        }
      });
    },
    // 编辑角色
    editRole(role_id) {
      this.$api
        .getRoleInfo({
          role_id,
        })
        .then((res) => {
          console.log(res);
          this.visible = true;
          this.form = res.data.role_msg[0];

          this.$nextTick(() => {
            let arr = res.data.menu_code.filter((item) => {
              return !this.firstMenu1.some((el) => el === item);
            });
            this.$refs.tree1.setCheckedKeys(arr);
            let mobilearr = res.data.mobile_menu_code.filter((item) => {
              return !this.firstMenu2.some((el) => el === item);
            });
            this.$refs.tree2.setCheckedKeys(mobilearr);
          });
        });
    },
    // 获取角色列表
    getRoleList() {
      this.$api
        .getRoleList({
          page: this.pageNum - 1,
          number: this.pageSize,
        })
        .then((res) => {
          res.data.userList.map((item) => {
            return (item.role_type = item.role_type == "2" ? "商户" : "员工");
          });
          res.data.userList.map((item) => {
            if(item.role_level == "1"){
                return item.role_level = "一级(总行)";
            }else if(item.role_level == "2"){
                return item.role_level = "二级(分行/支行)";
            }else if(item.role_level == "3"){
                return item.role_level = "三级(片区/团队)";
            }else if(item.role_level == "4"){
                return item.role_level = "四级(客户经理)";
            };
          });
          this.roleList = res.data.userList;
          this.total = res.data.total;
          this.form = {
              menuids: [],
              mobile_menuids: [],
          };
        });
    },
    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getRoleList();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getRoleList();
    },
    // 删除角色
    deleteRole(role_id) {
      this.$confirm("确定删除该角色吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      })
        .then(() => {
          this.$api
            .deleteRole({
              role_id: role_id,
            })
            .then((res) => {
              console.log(res);
              this.getRoleList();
            });
        })
        .catch(() => {
          this.delList = [];
        });
    },

    handleClose() {
      this.visible = false;
      this.form = {
          menuids: [],
          mobile_menuids: [],
      };
      this.$refs.tree1.setCheckedKeys([]);
      this.$refs.tree2.setCheckedKeys([]);
    },
  },
};
</script>



