<template>
    <div>
        <el-collapse v-model="activeNames" @change="handleChange">
            <el-collapse-item title="交易流水" name="tranflow">
                <base-table :list="dataList" :data="tranflowList">
                    <el-table-column fixed="right" label="操作" align="center" width="240">
                        <template slot-scope="scope">

                            <el-radio-group v-model="scope.row.voucher_result">
                                <el-radio-button @click.native="addVoucherDetail(scope.row.tsdt,scope.row.site_no,scope.row.f011,scope.row.prid,'0')" label="0">登记正确</el-radio-button>
                                <el-radio-button @click.native="addVoucherDetail(scope.row.tsdt,scope.row.site_no,scope.row.f011,scope.row.prid,'1')" label="1">登记不正确</el-radio-button>
                            </el-radio-group>
                        </template>
                    </el-table-column>
                </base-table>
            </el-collapse-item>
            <el-form ref="form" status-icon :rules="rules" :model="form" label-width="150px">
                <el-collapse-item title="助农取款登记簿" name="register">
                    <span style="color: red">请拍照并上传交易登记情况</span>
                    <el-form-item v-for="(file, i) in showList" :key="i">
                        <div class="uploaditem" style="width: 100%; overflow: auto;">
                            <div @mouseenter="showDelete = true" @mouseleave="showDelete = false" style="position:relative"
                                 v-for="(item, index) in file.fileList" :key="index">
                                <div class="delete-logo" v-if="showDelete" @click="handleRemove(item, file.fileList, index)">
                                    <i
                                            class="el-icon-delete"
                                            size="24">
                                    </i>
                                </div>
                                <el-image
                                        v-if="item.filelastname!=='pdf' && item.filelastname!=='doc' && item.filelastname!=='docx' && item.filelastname!=='xls' && item.filelastname!=='xlsx' && item.filelastname!=='txt'"
                                        :src="item.file_url" class="fileimg"
                                        :preview-src-list="[item.file_url]">
                                    <div class="imgtigger" slot="error">没有图片</div>
                                </el-image>

                                <a target="_blank" v-if="item.filelastname==='doc' || item.filelastname==='docx' " :href="item.file_url">
                                    <i class="fa fa-file-word-o" aria-hidden="true"></i>
                                </a>
                                <a target="_blank" v-if="item.filelastname==='xls' || item.filelastname==='xlsx' " :href="item.file_url">
                                    <i class="fa fa-file-excel-o" aria-hidden="true"></i>
                                </a>
                                <a target="_blank" v-if="item.filelastname==='pdf'" :href="item.file_url">
                                    <i class="fa fa-file-pdf-o" aria-hidden="true"></i>
                                </a>
                                <a target="_blank" v-if="item.filelastname==='txt'" :href="item.file_url">
                                    <i class="fa fa-file-o" aria-hidden="true"></i>
                                </a>
                            </div>
                            <el-upload class="upload-demo" :name="file.code" ref="upload"
                                       action="/hnm/txCtrl?txcode=upload001" :show-file-list="false"
                                       list-type="picture-card"
                                       :file-list="file.fileList"
                                       :before-upload="beforeAvatarUpload"
                                       :data="{name: file.name, code: file.code, file_type: file.file_type, business: file.business}"
                                       :on-success="fileUploaded" :on-error="fileUploadedError"
                                       :headers='{Accept:"application/json"}' :disabled="false">
                                <i class="el-icon-plus" slot="default" v-show="true"></i>
                            </el-upload>
                        </div>
                    </el-form-item>
                </el-collapse-item>
                <el-collapse-item title="检查结果" name="gd_result">
                    <el-form-item label="检查结果" prop="gd_result">
                        <el-select v-model="form.gd_result" clearable
                                   :disabled="false" @change="showquestion">
                            <el-option key="0" label="账实相符" value="0"></el-option>
                            <el-option key="1" label="账实不符" value="1"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="情况说明" prop="remarks" style="width: 720px">
                        <el-input
                                type="textarea"
                                :autosize="{ minRows: 2, maxRows: 4 }"
                                placeholder="请输入内容"
                                maxlength="300"
                                show-word-limit
                                v-model="form.remarks"
                        ></el-input>
                    </el-form-item>

                    <div v-if="showque">
                            <el-form-item>
                                <template>
                                    <i style="color: red">点击单元格即可编辑内容</i>
                                    <el-table
                                            :header-cell-class-name="starAdd"
                                            border
                                            :data="questionList"
                                            style="width: 100%"
                                            max-height="250"
                                            @cell-click="tableDbEdit"
                                    >
                                        <el-table-column
                                                fixed
                                                align="center"
                                                type="index"
                                                prop="index"
                                                label="序号"
                                                width="50">
                                        </el-table-column>
                                        <el-table-column
                                                resizable show-overflow-tooltip
                                                align="center"
                                                prop="gd_id"
                                                label="工单流水号"
                                                width="150">
                                            <template slot-scope="scope">
                                                <span>{{ scope.row.gd_id }}</span>
                                            </template>
                                        </el-table-column>

                                        <el-table-column
                                                resizable show-overflow-tooltip
                                                align="center"
                                                prop="content_text"
                                                label="问题描述"
                                                min-width="250"
                                        >
                                            <template slot-scope="scope">
                                                <!-- 条件判断如果满足则显示表单，否则默认展示文字 -->
                                                <el-input type="textarea" size="small"
                                                          v-model="scope.row.content_text"
                                                          v-if="showInput == `content_text${scope.row.id}`"
                                                          @blur='blurInput(scope.row.id,"content_text",scope.row.content_text)'
                                                          v-focusTextarea>
                                                </el-input>
                                                <span v-else>{{ scope.row.content_text }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column
                                                align="center"
                                                prop="risk_level"
                                                label="风险等级"
                                                width="100">
                                            <template slot-scope="scope">
                                                <el-select
                                                        v-model="scope.row.risk_level"
                                                        size="small"
                                                        v-if="showInput == `risk_level${scope.row.id}`"
                                                        @change='blurInput(scope.row.id,"riskLevel",scope.row.risk_level)'
                                                        v-focus>
                                                    <el-option
                                                            v-for="item in items2"
                                                            :label="item.label"
                                                            :value="item.label"
                                                            :key="item.label"
                                                    >
                                                    </el-option>
                                                </el-select>
                                                <span v-else>{{ scope.row.risk_level }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column
                                                align="center"
                                                prop="end_date"
                                                label="整改完成时间"
                                                width="120">
                                            <template slot-scope="scope">
                                                <el-date-picker size="small"
                                                                value-format="yyyy-MM-dd"
                                                                v-model="scope.row.end_date"
                                                                v-if="showInput == `end_date${scope.row.id}`"
                                                                @blur='blurInput(scope.row.id,"end_date",scope.row.end_date)'
                                                                :picker-options="setFulltime"
                                                                @change="checkDate(scope.row.end_date)"
                                                                v-focus>
                                                </el-date-picker>
                                                <span v-else>{{ scope.row.end_date }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column
                                                align="center" show-tooltip-when-overflow resizable
                                                prop="requirement"
                                                label="整改要求"
                                                min-width="200"
                                                >
                                            <template slot-scope="scope">
                                                <el-input type="textarea" size="small"
                                                          v-model="scope.row.requirement"
                                                          v-if="showInput == `requirement${scope.row.id}`"
                                                          @blur='blurInput(scope.row.id,"requirement",scope.row.requirement)'
                                                          v-focusTextarea>
                                                </el-input>
                                                <span v-else>{{ scope.row.requirement }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column
                                                disabled="true"
                                                align="center"
                                                prop="responsible"
                                                label="整改人"
                                                width="120">
                                            <template slot-scope="scope">
                                                <el-select
                                                        filterable
                                                        value-key="account_id"
                                                        v-model="scope.row.responsible"
                                                        size="small"
                                                        v-if="showInput == `responsible${scope.row.id}`"
                                                        @change='blurInput(scope.row.id,"responsible",scope.row.responsible)'
                                                >
                                                    <el-option
                                                            v-for="item in userList" :key="item.account_id" :label="item.name"
                                                            :value="item.account_id">
                                                        <span>{{ item.name }}({{ item.account_id }})</span>
                                                    </el-option>
                                                </el-select>
                                                <span v-else>{{ scope.row.responsible }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column
                                                align="center"
                                                fixed="right"
                                                label="操作"
                                                width="60">
                                            <template slot-scope="scope">
                                                <el-button
                                                        @click.native.prevent="removequestion(scope.row.status,scope.$index, questionList)"
                                                        type="text"
                                                        size="small" v-if="scope.row.status !== 1 && scope.row.status !== 2 && scope.row.status !== 3">
                                                    移除
                                                </el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                    <el-button @click="addItem" icon="el-icon-plus"></el-button>
                                </template>
                            </el-form-item>
                    </div>

                </el-collapse-item>

                <el-form-item>
                    <el-button
                            id="editButton"
                            v-if="true"
                            type="primary"
                            size="medium"
                            @click="onSubmit">
                        {{ "提交" }}
                    </el-button>
                </el-form-item>
            </el-form>
        </el-collapse>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";

    export default {
        components: {
            baseTable,
        },
        name: "voucherDetail",
        data() {
            return {
                form: {},
                rules: {
                    gd_result: [{
                        required: true,
                        message: "检查结果",
                        trigger: "change",
                    },],
                    remarks: [{
                        required: true,
                        message: "情况说明",
                        trigger: "blur",
                    },],
                },
                activeNames: ['tranflow','register','gd_result'],
                tsdt: this.$route.query.tsdt,
                site_no: this.$route.query.site_no,
                site_name: this.$route.query.site_name,
                mgr_id: this.$route.query.mgr_id,
                mgr_name: this.$route.query.mgr_name,
                activeName: this.$route.query.activeName,
                tranflowList: [],
                dataList: [
                    {
                        label: "凭证号",
                        prop: "f011",
                        sort: true,
                    },
                    {
                        label: "交易时间",
                        prop: "mask_from_v2",
                    },
                    {
                        label: "交易类型",
                        prop: "prid_name",
                    },
                    {
                        label: "卡号",
                        prop: "f002",
                    },
                    {
                        label: "金额",
                        prop: "f004",
                    },
                ],
                showDelete: false,
                showList: {},
                fileList: [
                    {
                        business: "voucher",
                        code: "voucher01",
                        name: "助农取款登记簿",
                        file_type: 0,
                        file_url: "",
                    },
                ],
                voucher_result_list: [],
                isload: false,
                check: false,
                visible: false,
                showque: true,
                questionList: [],
                showInput: "",
                items2: [
                    {
                        label: '高',
                    }, {
                        label: '中',
                    }, {
                        label: '低',
                    },
                ],
                setFulltime: this.disabledDate(),
                userList: [],
                oldData: {},
            };
        },
        directives: {
            // 通过自定义指令实现的表单自动获得光标的操作
            focus: {
                inserted: function (el) {
                    if (el.tagName.toLocaleLowerCase() == 'input') {
                        el.focus()
                    } else {
                        if (el.getElementsByTagName('input')) {
                            el.getElementsByTagName('input')[0].focus()
                        }
                    }
                    el.focus()
                }
            },
            focusTextarea: {
                inserted: function (el) {
                    if (el.tagName.toLocaleLowerCase() == 'textarea') {
                        el.focus()
                    } else {
                        if (el.getElementsByTagName('textarea')) {
                            el.getElementsByTagName('textarea')[0].focus()
                        }
                    }
                    el.focus()
                }
            }
        },
        mounted() {
            this.getTranflowList(this.tsdt, this.site_no);
            this.showVoucherDetail(this.tsdt, this.site_no);
            this.getAccountList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    //this.form = {};
                }
            },
        },
        methods: {

            starAdd(obj) {
                if (obj.columnIndex !== 0 && obj.columnIndex !== 1 && obj.columnIndex !== 7) {
                    return 'star';
                }
            },
            /*
                方法参数皆为框架方法的默认传参
                row 	行数据
                column	被点击的触发了方法的单元格
                event	事件
            */
            tableDbEdit(row, column) {
                if (row.status !==1 && row.status !==2  && row.status !==3){
                    this.showInput = column.property + row.id
                    this.oldData[column.property] = row[column.property]
                }
            },
            async blurInput(id, name, value) {
                let obj = {}
                if ((this.questionList[name] != value)) {
                    obj[name] = value//被改变的数据
                    // 然后再写调用接口，提交内容的东西就可以了
                }
                this.showInput = ""
            },

            //自定义禁用时间
            disabledDate() {
                return {
                    disabledDate: time => {
                        let start_date = new Date() - 24 * 60 * 60 * 1000;
                        let end_date = start_date + 91 * 24 * 60 * 60 * 1000;
                        // 不可选历史天、可选当前天、可选未来天
                        return time.getTime() < start_date || time.getTime() > end_date;
                    }
                }
            },

            checkDate(end_date) {
                let counDay = (new Date(end_date).getTime() - new Date().getTime()) / 24 / 60 / 60 / 1000;
                if (counDay > 90) {
                    this.$message.error("整改日期不能超过当前日期的90天")
                    return false;
                } else {
                    return true;
                }
            },
            getAccountList() {
                if (this.userList.length != 0) return;
                this.$api.getAccountList({}).then((res) => {
                    let dataList = res.data;
                    if (dataList) {
                        for (let i = 0; i < dataList.length; i++) {
                            this.userList = dataList;
                        }
                    }

                });
            },
            addItem() {
                this.questionList.push({
                        id: this.questionList.length === 0 ? 1 : this.questionList[this.questionList.length-1].id + 1,
                        content_text: "",
                        risk_level: "",
                        end_date: "",
                        requirement: "",
                        site_no: this.site_no,
                        responsible: this.mgr_id,
                        responsible_name: this.mgr_name,
                        pj_classify: "1",
                        pj_classify_name: "定期巡检"
                    }
                )
            },



            //获取num天后的日期
            fun_date(num) {
                //今天时间
                var date = new Date();
                date.setDate(date.getDate() + num);
                //num是正数表示之后的时间，num负数表示之前的时间，0表示今天
                var time = this.addZero(date.getFullYear()) + "-" + this.addZero((date.getMonth() + 1)) + "-" + this.addZero(date.getDate());
                return time;
            },

            addZero(num){
                if(parseInt(num) < 10){
                    num = '0' + num;
                }
                return num;
            },

            //显示自动生成问题清单
            showquestion(){
                if(this.activeName === 'voucher1'){
                    this.questionList = [];
                    //账实不符
                    if(this.form.gd_result === "1"){
                        this.makequestion();
                    }
                }
            },

            //生成问题清单
            makequestion(){
                let missfileList = [];
                let hasfileList = [];
                for (let i = 0; i < this.fileList.length; i++) {
                    if (this.fileList[i].file_id !== undefined && this.fileList[i].file_id !== null) {
                        hasfileList.push(this.fileList[i].code);
                    }
                }
                this.fileList.forEach(item => {
                    let flag = false;
                    hasfileList.forEach(e => {
                        if (e === item.code) {
                            flag = true;
                        }
                    });
                    if (!flag) {
                        missfileList.push(item.name);
                    }
                });

                if(missfileList != null && missfileList.length > 0){
                    missfileList.forEach(item => {
                        let question_arr ={
                            "id": this.questionList.length + 1,
                            "site_no": this.site_no,
                            "pj_classify": "1",
                            "pj_classify_name": "定期巡检",
                            "content_text": this.site_name+"，"+this.tsdt+"的"+item+"未规范登记",
                            "risk_level": "中",
                            "end_date": this.fun_date(7),
                            "requirement": "请根据《广东南粤银行农村普惠金融支付服务点管理办法》要求开展整改",
                            "responsible": this.mgr_id,
                            "responsible_name": this.mgr_name
                        };
                        this.questionList.push(question_arr);
                    });
                }

                //流水凭证
                if(this.tranflowList != null && this.tranflowList.length > 0){
                    if(this.voucher_result_list == null || this.voucher_result_list.length === 0){
                        this.tranflowList.forEach(item => {
                            let question_arr ={
                                "id": this.questionList.length + 1,
                                "site_no": this.site_no,
                                "pj_classify": "1",
                                "pj_classify_name": "定期巡检",
                                "content_text": this.site_name+"，"+this.tsdt+"的"+item.prid_name+"交易(凭证号"+item.f011+")，未规范登记",
                                "risk_level": "中",
                                "end_date": this.fun_date(7),
                                "requirement": "请根据《广东南粤银行农村普惠金融支付服务点管理办法》要求开展整改",
                                "responsible": this.mgr_id,
                                "responsible_name": this.mgr_name
                            };
                            this.questionList.push(question_arr);
                        });
                    }else{
                        this.tranflowList.forEach(item => {
                            let flag = false;
                            this.voucher_result_list.forEach(e => {
                                if (item.site_no === e.site_no && item.tsdt === e.tsdt && item.f011 === e.f011
                                    && item.prid === e.prid) {
                                    flag = true;
                                }
                            });
                            if (!flag) {
                                let question_arr ={
                                    "id": this.questionList.length + 1,
                                    "site_no": this.site_no,
                                    "pj_classify": "1",
                                    "pj_classify_name": "定期巡检",
                                    "content_text": this.site_name+"，"+this.tsdt+"的"+item.prid_name+"交易(凭证号"+item.f011+")，未规范登记",
                                    "risk_level": "中",
                                    "end_date": this.fun_date(7),
                                    "requirement": "请根据《广东南粤银行农村普惠金融支付服务点管理办法》要求开展整改",
                                    "responsible": this.mgr_id,
                                    "responsible_name": this.mgr_name
                                };
                                this.questionList.push(question_arr);
                            }
                        });
                    }

                    this.voucher_result_list.forEach(item => {
                        if (item.voucher_result === undefined || item.voucher_result == null || item.voucher_result === "1") {
                            let prid_name = "";
                            if(item.prid === "56"){
                                prid_name = "小额取款";
                            }else if(item.prid === "57"){
                                prid_name = "现金汇款";
                            }else if(item.prid === "58"){
                                prid_name = "转账汇款";
                            }

                            let question_arr ={
                                "id": this.questionList.length + 1,
                                "site_no": this.site_no,
                                "pj_classify": "1",
                                "pj_classify_name": "定期巡检",
                                "content_text": this.site_name+"，"+this.tsdt+"的"+prid_name+"交易(凭证号"+item.f011+")，未规范登记",
                                "risk_level": "中",
                                "end_date": this.fun_date(7),
                                "requirement": "请根据《广东南粤银行农村普惠金融支付服务点管理办法》要求开展整改",
                                "responsible": this.mgr_id,
                                "responsible_name": this.mgr_name
                            };
                            this.questionList.push(question_arr);
                        }
                    });
                }
            },

            //移除问题清单
            removequestion(status,index, rows){
                this.$confirm("确定删除该问题吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        rows.splice(index, 1);
                    })
                    .catch(() => {
                    });
            },

            //查询助农取款交易列表
            getTranflowList(tsdt,site_no) {
                this.$api.getTranflowList({
                    "tsdt": tsdt,
                    "site_no": site_no,
                }).then((res) => {
                    this.tranflowList = res.data;
                });
            },

            //查询助农取款业务凭证详情
            showVoucherDetail(tsdt,site_no){
                this.isload = true;
                this.$api
                    .getVoucherDetail({
                        tsdt: tsdt,
                        site_no: site_no
                    })
                    .then((res) => {
                        if (res.data.voucherDetail != null){
                            this.form = res.data.voucherDetail;
                        }
                        if (res.data.fileList != null && res.data.fileList.length > 0) {
                            this.fileList = this.fileList.concat(res.data.fileList)
                            for (let i = 0; i < this.fileList.length; i++) {
                                if (this.fileList[i].id) {
                                    this.fileList[i].file_id = this.fileList[i].id;
                                    this.fileList[i].filelastname = this.fileList[i].local_file_path.substring(this.fileList[i]
                                        .local_file_path.lastIndexOf('.') + 1);
                                }
                                if (this.fileList[i].code === "voucher01") {
                                    this.fileList[i].name = "助农取款登记簿";
                                }
                            }
                        }
                        if (res.data.voucher_result_list != null && res.data.voucher_result_list.length > 0) {
                            this.voucher_result_list = res.data.voucher_result_list
                        }
                        if (res.data.questionList != null && res.data.questionList.length > 0) {
                            this.questionList = res.data.questionList
                        }
                        this.handleShowList();
                        this.isload = false;
                    }).catch(() => {
                    this.isload = false;
                })
            },

            //影像操作 展示
            handleShowList() {
                this.fileList.forEach(item => {
                    item.url = item.file_url
                    if (!this.showList[item.code]) {
                        this.$set(this.showList, item.code, {
                            name: item.name,
                            code: item.code,
                            file_type: item.file_type,
                            business: item.business,
                            fileList: item.url ? [item] : []
                        })
                    } else {
                        item.url && this.showList[item.code].fileList.push(item)
                    }
                })
            },

            //影像操作 移除
            handleRemove(file, fileList, index) {
                let id
                if (file.file_id) {
                    id = file.file_id
                } else {
                    id = file.response.data[0].id
                }
                fileList.splice(index, 1)
                this.fileList = this.fileList.filter(item => item.file_id !== id)
                this.showquestion();
            },

            //影像操作 上传前
            beforeAvatarUpload(file) {
                console.log(file);
                /*const isLt2M = file.size / 1024  < 300;
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 300KB');
                }
                return isLt2M;*/
            },


            //影像操作 上传后
            fileUploaded(response, file, fileList) {
                let item = response.data[0];
                this.fileList[this.fileList.length] = {
                    code: response.data[0].code,
                    file_url: response.data[0].file_url,
                    file_id: response.data[0].id,
                    filelastname: response.data[0].local_file_path.substring(response.data[0]
                        .local_file_path.lastIndexOf('.') + 1)
                };
                item.file_url && this.showList[item.code].fileList.push({
                    code: response.data[0].code,
                    file_url: response.data[0].file_url,
                    file_id: response.data[0].id,
                    filelastname: response.data[0].local_file_path.substring(response.data[0]
                        .local_file_path.lastIndexOf('.') + 1)
                });
                this.showquestion();
            },

            //影像操作 上传失败后
            fileUploadedError(response, file, fileList) {
                console.log(JSON.parse(response.message));
                this.$message.error(JSON.parse(response.message).tips.message);
            },

            //流水凭证点击事件
            addVoucherDetail(tsdt,site_no,f011,prid,voucher_result){
                let voucher_result_arr ={
                    "tsdt": tsdt,
                    "site_no": site_no,
                    "f011": f011,
                    "prid": prid,
                    "voucher_result": voucher_result,
                };
                let voucher_result_list_new = [];
                //先删后加
                this.voucher_result_list.forEach(item => {
                    if (item.tsdt === voucher_result_arr.tsdt && item.site_no === voucher_result_arr.site_no
                        && item.f011 === voucher_result_arr.f011 && item.prid === voucher_result_arr.prid) {
                        //this.voucher_result_list.delete(item);
                    }else{
                        voucher_result_list_new.push(item);
                    }
                });

                voucher_result_list_new.push(voucher_result_arr);
                this.voucher_result_list = voucher_result_list_new;
                this.showquestion();
            },

            //提交
            onSubmit() {
                if (this.check) {
                    this.$router.back(-1);
                    return;
                }
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.form.fileIdList = [];
                        this.form.file_ids = null;
                        let missfile = "";
                        let hasfileList = [];
                        for (let i = 0; i < this.fileList.length; i++) {
                            if (this.fileList[i].file_id !== undefined && this.fileList[i].file_id !== null) {
                                hasfileList.push(this.fileList[i].code);
                                this.form.fileIdList.push(this.fileList[i].file_id);
                            }
                        }

                        this.fileList.forEach(item => {
                            let flag = false;
                            hasfileList.forEach(e => {
                                if (e === item.code) {
                                    flag = true;
                                }
                            });
                            if (!flag) {
                                missfile += item.name + ",";
                            }
                        });

                        let pass_flag = true;
                        //账实相符需校验
                        if(this.form.gd_result === "0"){
                            if (missfile.length > 0) {
                                this.$message.error("请上传助农取款登记簿");
                                return;
                            }

                            if(this.tranflowList != null && this.tranflowList.length > 0){
                                if(this.voucher_result_list == null || this.voucher_result_list.length === 0
                                    || this.voucher_result_list.length !== this.tranflowList.length){
                                    this.$message.error("流水未规范登记，请核查");
                                    return;
                                }

                                this.voucher_result_list.forEach(item => {
                                    if (item.voucher_result === undefined || item.voucher_result == null || item.voucher_result === "1") {
                                        this.$message.error("服务点:"+item.site_name+";凭证号:"+item.f011+"未规范登记，请核查");
                                        pass_flag = false;
                                        return false;
                                    }
                                });
                            }
                        }else{
                            //账实不符
                            if(this.tranflowList != null && this.tranflowList.length > 0){
                                if(this.voucher_result_list == null || this.voucher_result_list.length === 0
                                    || this.voucher_result_list.length !== this.tranflowList.length){
                                    this.$message.error("流水未规范登记，请核查");
                                    return;
                                }

                                this.voucher_result_list.forEach(item => {
                                    if (item.voucher_result === undefined || item.voucher_result == null) {
                                        this.$message.error("服务点:"+item.site_name+";凭证号:"+item.f011+"未规范登记，请核查");
                                        pass_flag = false;
                                        return false;
                                    }
                                });
                            }
                        }


                        if(!pass_flag){
                            return;
                        }

                        for (let i = 0; i < this.questionList.length; i++) {

                            if (this.questionList[i].id === undefined || this.questionList[i].id === null) {
                                this.questionList[i].id = i + 1;
                            }
                            if (this.questionList[i].content_text === undefined || this.questionList[i].content_text === null || this.questionList[i].content_text.length === 0) {
                                this.$message.error("问题描述、风险等级、整改完成时间、整改要求、整改人不可为空");
                                return;
                            }
                            if (this.questionList[i].risk_level === undefined || this.questionList[i].risk_level === null || this.questionList[i].risk_level.length === 0) {
                                this.$message.error("问题描述、风险等级、整改完成时间、整改要求、整改人不可为空");
                                return;
                            }
                            if (this.questionList[i].end_date === undefined || this.questionList[i].end_date == null || this.questionList[i].end_date.length === 0) {
                                this.$message.error("问题描述、风险等级、整改完成时间、整改要求、整改人不可为空");
                                return;
                            }
                            if (this.questionList[i].requirement === undefined || this.questionList[i].requirement === null || this.questionList[i].requirement.length === 0) {
                                this.$message.error("问题描述、风险等级、整改完成时间、整改要求、整改人不可为空");
                                return;
                            }
                            if (this.questionList[i].responsible === undefined || this.questionList[i].responsible === null || this.questionList[i].responsible.length === 0) {
                                this.$message.error("问题描述、风险等级、整改完成时间、整改要求、整改人不可为空");
                                return;
                            }
                            if (!this.checkDate(this.questionList[i].end_date)) {
                                this.$message.error("整改完成时间不能超过当前时间后的90天");
                                return;
                            }
                        }

                        if(this.form.fileIdList.length > 0){
                            this.form.file_ids = this.form.fileIdList.join(",");
                        }

                        this.$api.addVoucher({
                                "tsdt": this.tsdt,
                                "site_no": this.site_no,
                                "voucher_result_list": this.voucher_result_list,
                                "questionList": this.questionList,
                                ...this.form,
                            }
                        ).then((res) => {
                            this.handleClose();
                            this.$message.success("操作成功");
                            this.$router.back(-1);
                        });
                    }
                });
            },

            handleChange(val) {
                console.log(val);
            },
            handleClose() {
                this.visible = false;
                this.form = {};
            },
        },
    };
</script>
<style scoped>
    .fileimg {
        width: 120px;
        height: 120px;
        background-color: #eeeeee;
    }

    .imgtigger {
        color: #FFFFFF;
        margin-top: 40px;
        text-align: center;
    }

    .imgview {
        max-width: 100%;
    }

    .uploaditem {
        display: flex;
        /* flex-direction: column; */
        /* justify-content: center; */
        align-items: center;
        /* width: 200px; */
    }

    .wenjian {
        text-decoration: none;
        outline: none;
        font-size: 60px;
        color: #bf0000;
    }

    .fa {
        color: cadetblue;
        font-size: 96px;
    }

    .el-image, a {
        margin: 0 10px;
    }

    .delete-logo {
        position: absolute;
        z-index: 999;
        right: 10px;
        top: -10px;
        cursor: pointer;
    }
</style>
<style>
    table th.star div::before {
        content: '*';
        color: red;
    }
</style>


