<template>
  <div>
    <el-button type="primary" size="small" @click="addOrEditView" style="margin-bottom: 10px">
      新增巡查任务
    </el-button>
    <el-button type="warning" size="small" @click="exportPatrolInfos">
      导出全部查询数据
    </el-button>
    <el-form :inline="true" :model="form" style="margin-top: 15px">

      <el-form-item label="巡查时间" prop="inspect_start_time">
        <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                        v-model="form.inspect_start_time" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item label="-" prop="inspect_end_time">
        <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                        v-model="form.inspect_end_time" style="width: 100%;"></el-date-picker>
      </el-form-item>

      <el-form-item label="服务点" prop="site_id">
        <el-select v-model="form.site_id" filterable clearable size="small">
          <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                     :value="item.site_no"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="巡查类别" prop="category">
        <el-select v-model="form.category" filterable clearable size="small">
          <el-option v-for="item in items" :key="item.value" :label="item.label"
                     :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="巡查人姓名" prop="account_id">
        <el-select v-model="form.account_id" filterable clearable size="small">
          <el-option v-for="item in sysUserList" :key="item.account_id" :label="item.name"
                     :value="item.account_id">
            <span>{{ item.name }}({{ item.account_id }})</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="所属机构" prop="bankid">
        <div style="display: flex;">
          <el-input readonly="" size="small" v-model="bankname"></el-input>
          <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getPatrolList(true)" size="small" icon="el-icon-search">搜索
        </el-button>
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="patrolList">
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="text" icon="el-icon-edit-outline"
                     @click="editMarkSheel(scope.row)">
            巡查操作
          </el-button>
          <el-button type="text" icon="el-icon-edit-outline" @click="editPatrol(scope.row.patrol_id)">
            编辑
          </el-button>
          <el-button type="text" icon="el-icon-delete" style="color: red"
                     @click="deletePatrol(scope.row.patrol_id,scope.row.patrol_problem)">删除
          </el-button>
        </template>
      </el-table-column>
    </base-table>
    <base-page :page="pageNum" :size="pageSize" :total="total" @size="changeSize" @page="changePage"></base-page>

    <el-dialog :title="form.patrol_id ? '修改' : '新增'" :visible.sync="visible" width="45%"
               :before-close="handleClose">
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="主键" prop="patrol_id" v-show="false">
          <el-select v-model="form.patrol_id" filterable clearable size="small">
            <el-input v-model="form.patrol_id"></el-input>
          </el-select>
        </el-form-item>
        <el-form-item label="服务点" prop="site_id" style="width: 360px">
          <el-select v-model="form.site_id" filterable clearable size="small">
            <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                       :value="item.site_no"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="巡查类别" prop="category" style="width: 360px">
          <el-select v-model="form.category" filterable clearable size="small">
            <el-option v-for="item in items" :key="item.value" :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="巡查人" prop="account_id" style="width: 360px">
          <el-select v-model="form.account_id" filterable clearable size="small">
            <el-option v-for="item in sysUserList" :key="item.account_id" :label="item.name"
                       :value="item.account_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="巡查时间" prop="inspect_time" style="width: 360px">
          <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                          v-model="form.inspect_time" style="width: 100%;"
                          v-show="form.patrol_id === undefined"></el-date-picker>
          <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                          v-model="form.inspect_time" style="width: 100%;"
                          :picker-options="setFulltime" v-show="form.patrol_id !== undefined"></el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="patrol_remark" style="width: 360px">
          <el-input v-model="form.patrol_remark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addtPatrol">确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import BranchTreePicker from "../../components/BranchTreePicker";


export default {
  components: {
    baseTable,
    basePage,
    BranchTreePicker,
  },
  name: "patrol",
  data() {
    return {
      role: sessionStorage.getItem("role"),
      form: {},
      siteList: [],
      dataList: [
        {
          label: "服务点名称",
          prop: "site_name",
          sort: true,
        },
        {
          label: "巡查类别",
          prop: "category",
          sort: true,
        },
        {
          label: "巡查人姓名",
          prop: "name",
          sort: true,
        },
        {
          label: "巡查时间",
          prop: "inspect_time",
          sort: true,
        },
        {
          label: "移动打卡",
          prop: "clock_in",
          sort: true,
        },
        // {
        //     label: "巡查评分",
        //     prop: "log_sum",
        //     sort: true,
        // },
        {
          label: "巡查结果",
          prop: "patrol_remarks",
        },
        {
          label: "巡查结果描述",
          prop: "patrol_problem",
        },
      ],
      rules: {
        site_id: [{
          required: true,
          message: "请选择服务点",
          trigger: "change"
        },],
        account_id: [{
          required: true,
          message: "请选择巡查人",
          trigger: "change"
        },],
        inspect_time: [{
          required: true,
          message: "请选择巡查时间",
          trigger: "blur"
        },],
      },
      items: [
        {
          value: 0,
          label: '开业定期巡查'
        },
        {
          value: 1,
          label: '退出开业巡查'
        }
      ],
      setFulltime: this.disabledDate(),
      patrolList: [],
      sysUserList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
      bankname: '',
      bankid: '',
      inspect_time: '',
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
  },
  mounted() {
    //人员列表
    this.getAccountList();
    //站点列表
    this.getSiteSelectList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  methods: {
    //自定义禁用时间
    disabledDate() {
      return {

        disabledDate: time => {
          //上线时间
          let start_date = new Date("2022-04-14");
          //巡查时间
          let inspect_time = new Date(this.inspect_time);
          //巡查时间大于上线时间 则只能选择上线后的时间，否则只能选择上线前的时间
          if (inspect_time > start_date) {

            return time.getTime() < start_date;
          } else {

            return time.getTime() > start_date;
          }
        }
      }
    },
    // 机构列表
    nodeselectForSerch(node) {
      console.log(node);
      this.bankname = node.branch_name;
      this.bankid = node.branch_id;
    },
    // 机构下用户列表
    getAccountList() {
      if (this.sysUserList.length != 0) return;
      this.$api.getAccountList({}).then((res) => {
        this.sysUserList = res.data;
      });
    },
    //站点列表
    getSiteSelectList() {
      this.$api.getSiteSelectList().then((res) => {
        this.siteList = res.data.siteList;
      });

    },
    // 获取巡查任务列表
    getPatrolList(flag) {
      if (flag) {
        this.pageNum = 1;
      }
      this.$api
          .getPatrolList({
            page: this.pageNum - 1,
            number: this.pageSize,
            bankid: this.bankid,
            site_id: this.form.site_id,
            account_id: this.form.account_id,
            inspect_start_time: this.form.inspect_start_time,
            inspect_end_time: this.form.inspect_end_time,
            category: this.form.category,
          })
          .then((res) => {
            this.patrolList = res.data.patrolList;
            this.total = res.data.total;
            this.patrolList.map((item) => {
              // alert(item.category)
              if (item.log_sum == null) {
                item.log_sum = "0";
              }

              if (item.clock_in == "0") {
                 item.clock_in = "否";
              } else if (item.clock_in == "1") {
                 item.role_level = "是";
              }
              if (item.category === 0) {
                 item.category = "开业定期巡查";
              } else if (item.category === 1) {
                 item.category = "退出开业巡查";
              }

            });
            //this.form = {};
          });
    },
    //打开新增、更新窗口
    addOrEditView() {
      this.visible = true;
      this.form = {};
      this.inspect_time = '';
      console.log(this.form.patrol_id)
    },
    //新增/更新
    addtPatrol() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.patrol_id) {
            this.$api.updatePatrol(this.form).then((res) => {
              this.handleClose();
              this.getPatrolList();
            });
          } else {
            this.$api.addPatrol(this.form).then((res) => {
              let patrol_id = res.data.patrol_id;
              if (patrol_id) {
                Object.defineProperty(this.form, "patrol_id", {value: patrol_id})
                console.log(this.form)
                this.editMarkSheel(this.form)
              } else {
                this.handleClose();
                this.getPatrolList();
              }
            });
          }
        }
      });
    },
    // 编辑
    editPatrol(patrol_id) {
      this.$api
          .getPatrolDetail({
            patrol_id,
          })
          .then((res) => {
            console.log(res);
            this.visible = true;
            this.form = res.data.msg[0];
            this.inspect_time = this.form.inspect_time;
          });
    },
    // 删除机构
    deletePatrol(patrol_id, patrol_problem) {
      let msg = '确定删除吗？'
      if (patrol_problem) {
        msg = '若删除，将同时删除该任务下的巡查问题，请确认。'
      }
      this.$confirm(msg, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      })
          .then(() => {
            this.$api
                .deletePatrol({
                  patrol_id: patrol_id,
                })
                .then((res) => {
                  console.log(res);
                  this.getPatrolList();
                });
          })
          .catch(() => {
          });
    },
    // 评分表
    // markSheel(patrol_id) {
    //     this.$api
    //         .getMarkSheel({})
    //         .then((res) => {
    //             console.log(res);
    //         });
    // },
    // 巡查操作
    editMarkSheel(obj) {

      let path = '';
      let name = '';
      let flag = false;
      if (obj.category==='退出开业巡查' || obj.category===1) {
        path = '/quit_msheel.vue'
        name = '退出巡检条款'
      } else {
        let time1 = new Date(obj.inspect_time).getTime();//巡查时间
        let time2 = new Date("2022-04-14").getTime();//上线时间
        if (time1 > time2) {
          path = '/new_msheel.vue'
          name = '巡检条款'
        } else {
          path = '/mark_sheel'
          name = '巡查操作'
        }
      }
      if (obj.patrol_remarks) {
        flag = true;
      }
      this.$router.push({
        path: path,
        name: name,
        query: {
          patrol_id: obj.patrol_id,
          inspect_time: obj.inspect_time,
          flag: flag,
          role: this.role
        },
      });
    },

    // 导出
    exportPatrolInfos() {
      this.$api.exportPatrolInfos({
        ...this.form
      }).then((res) => {
        var fileName = res.data.fileName;
        var filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "导出失败，系统异常",
            type: "error",
          });
          return false;
        }

        var objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        var input01 = document.createElement("input");
        var input02 = document.createElement("input");
        var input03 = document.createElement("input");
        var input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "导出成功",
          type: "success",
        });
      });
    },


    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getPatrolList();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getPatrolList();
    },

    handleClose() {
      this.visible = false;
      this.form = {};
      this.inspect_time = '';
    },
  },
  // 跳转保存数据
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (to.params.searchKey) {
        vm.form = to.params.searchKey;
      }
      vm.getPatrolList();
    });
  },
};
</script>



