<template>
  <div>
    <el-form
      ref="form"
      status-icon
      :rules="rules"
      :model="form"
      label-width="120px"
      style="width: 600px">
      <el-form-item label="用户编号" prop="account_id">
        <el-input @change="checkAccountId()"
          v-model="form.account_id"
          :disabled="account_id  ? true : false"
        ></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="name">
        <el-input v-model="form.name" :disabled="true" ></el-input>
      </el-form-item>
      <el-form-item label="身份证号" prop="identify_no">
        <el-input v-model="form.identify_no" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item label="角色" prop="role">
        <el-radio-group v-model="form.role" :disabled="check" @change="getLevel" >
          <el-radio
                  v-for="item in userList"
                  :key="item.role_id"
                  :label="item.role_id"
                  :role_level="item.role_level"
          >{{ item.role_name }}</el-radio
          >
        </el-radio-group>
      </el-form-item>
      <el-form-item label="所属机构" prop="branch_id" v-if="showByRoleLevel">
        <div style="display: flex;">
          <el-input :disabled="true" v-model="this.form.branch_name"></el-input>
          <BranchTreePicker @nodeselect="nodeselectForShow" :dfks="this.form.branch_id" v-show="!check" :appendToBody="true"></BranchTreePicker>
        </div>
      </el-form-item>
      <el-form-item label="手机号码" prop="phone">
        <el-input v-model="form.phone" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item label="电子邮箱" prop="email">
        <el-input v-model="form.email" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item
        label="登录密码"
        prop="login_pwd"
        v-if="!account_id"
      >
        <el-input
          v-model="form.login_pwd"
          show-password
          :disabled="check"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          v-if="!check"
          type="primary"
          size="medium"
          @click="onSubmit"
          >{{ account_id ? "更新信息" : "立即创建" }}</el-button
        >
        <el-button v-else type="primary" size="medium" @click="onSubmit"
          >返回</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import BranchTreePicker from "../../components/BranchTreePicker";

export default {
    components: {
        BranchTreePicker
    },
  name: "adduser",
  data() {
    var checkID = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("身份证不能为空"));
      }
      // 身份证号正则
      var idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
      if (idcardReg.test(value)) {
        callback();
      } else {
        return callback(new Error("身份证号非法"));
      }
    };
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号码不能为空"));
      }
      var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
      if (phoneReg.test(value)) {
        callback();
      } else {
        return callback(new Error("手机号码非法"));
      }
    };
      var checkEmail = (rule, value, callback) => {
          if (value === undefined || value=="" || value==null){
              callback();
          }
          var emailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/; // 电子邮箱正则
          if (emailReg.test(value)) {
              callback();
          } else {
              return callback(new Error("电子邮箱非法"));
          }
      };
    return {
      form: {
        roleids: [],
      },
      rules: {
        name: [
          { required: true, message: "请输入员工姓名", trigger: "blur" },
          { min: 2, max: 10, message: "名字在 2 到 10 个字符", trigger: "blur" },
        ],
        account_id: [
          { required: true, message: "请输入员工编号", trigger: "blur" },
          { min: 1, max: 10, message: "工号在 1 到 10 个字符", trigger: "blur" },
        ],
        identify_no: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { validator: checkID, trigger: "blur" },
        ],
        branch_id: [
          { required: true, message: "请选择所属机构", trigger: "change" },
        ],
        role: [{ required: true, message: "选择角色", trigger: "change" }],
        phone: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" },
        ],
          email: [
              {  message: "请输入电子邮箱", trigger: "blur" },
              { validator: checkEmail, trigger: "blur" },
          ],
        login_pwd: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码在 6 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      account_id: "",
      userList: [],
      check: false,
      userName:"",
      showByRoleLevel: false,
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
  },
  mounted() {
    this.check = this.$route.query.check === 'false' ? false : Boolean(this.$route.query.check);
    let role = this.$route.query.role;
    this.account_id = this.$route.query.id;
    if (this.account_id) {
      this.$api
        .getUserInfo({
          account_id: this.account_id,
        })
        .then((res) => {
          console.log(res.data);
          this.form = res.data.user;
          if (this.check) {
            this.userList = res.data.roleList;
          } else {
            this.getUsertype();
          }
          if(res.data.roleList.length == 1){
              this.$set(this.form, "role", res.data.roleList[0].role_id);
          }
            this.getLevel();
        });
    } else {
      this.getUsertype();
    }
  },
  methods: {
    //获取角色级别
    getLevel(){
        console.log(this.userList);
        for(let i = 0;i<this.userList.length;i++){
            if(this.userList[i].role_id==this.form.role){
                let level = this.userList[i].role_level;
                if(level == "1"){
                    this.showByRoleLevel = false;
                    this.$set(this.form, "branch_id", null);
                }else if(level == "2" || level == "3" || level == "4"){
                    this.showByRoleLevel = true;
                }
                break;
            }
        }
    },
    //反显姓名
    checkAccountId(){
      this.$api.checkStaffDetail({user_id:this.form.account_id}).then((res) => {
        this.form.name = res.data.name;
      });
      this.$set(this.form, "name", this.form.name);
    },
    // 机构列表
      nodeselectForShow(node) {
          this.$set(this.form, "branch_name", node.branch_name);
          this.$set(this.form, "branch_id", node.branch_id);
      },
    // 角色类型
    getUsertype() {
      if (this.check) return;
      this.$api.getUsertype().then((res) => {
        this.userList = res.data;
        if (res.data.length == 1) {
          this.$set(this.form, "role", res.data[0].role_id);
        }
        this.getLevel();
      });
    },
    onSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$set(this.form, "roleids", [this.form.role]);
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.account_id) {
            delete this.form.create_time;
            this.$api.updateUser(this.form).then((res) => {
              this.$message({
                message: "更新用户成功",
                type: "success",
              });
              this.$router.back(-1);
            });
          } else {
            //人力系统校验工号
            this.$api.checkStaffDetail({user_id:this.form.account_id}).then((res) => {
                this.$api.addUser(this.form).then((res) => {
                  this.$message({
                    message: "创建用户成功",
                    type: "success",
                  });
                  this.$router.back(-1);
                });
            });
          }
        }
      });
    },
  },
  // 跳转保存数据
  beforeRouteLeave(to, from, next) {
    to.params.searchKey = this.$route.params.searchKey;
    next();
  },
};
</script>



