<template>
    <div>
        <el-button type="primary" size="small" @click="adduser">新增用户</el-button>
        <el-button
                type="danger"
                size="small"
                @click="deleteUser"
                :disabled="delList.length == 0"
                v-if="role == '1' || role == '2'"
        >批量删除
        </el-button
        >
        <el-button
                type="primary"
                size="small"
                icon="el-icon-folder-add"
                @click="uploadFile"
                v-if="role == '1' || role == '2'"
        >批量导入
        </el-button
        >
        <el-button type="warning" size="small" @click="downModal" v-if="role == '1' || role == '2'"
        >下载用户导入模版
        </el-button
        >
        <input type="file" id="file" @change="chooseFile" style="display: none"/>
        <el-form :inline="true" :model="form" style="margin-top: 15px">
            <el-form-item label="姓名">
                <el-input
                        v-model="form.name"
                        clearable
                        placeholder="请输入姓名"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="用户编号">
                <el-input
                        v-model="form.account_id"
                        clearable
                        placeholder="请输入用户编号"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="用户角色">
                <el-select v-model="form.role_id" filterable clearable size="small">
                    <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name"
                               :value="item.role_id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="手机号码">
                <el-input
                        v-model="form.phone"
                        clearable
                        placeholder="请输入手机号码"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="所属机构" prop="bankid">
                <div style="display: flex;">
                    <el-input readonly="" size="small" v-model="bankname"></el-input>
                    <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
                </div>
            </el-form-item>
            <el-form-item>
                <el-button
                        type="primary"
                        @click="getUserList"
                        size="small"
                        icon="el-icon-search"
                >搜索
                </el-button
                >
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="userList" @select="chooseUser">
            <el-table-column type="selection" slot="begin" width="40">
            </el-table-column>
            <el-table-column label="操作" align="center" width="240">
                <template slot-scope="scope">
                    <template
                            v-if="scope.row.role_id == role_id || scope.row.role_id == super_manager || (role != '1' && role != '2')">
                        <el-button
                                type="text"
                                icon="el-icon-edit-outline"
                                @click="editUser(scope.row.account_id, true)"
                        >查看
                        </el-button
                        >
                    </template>
                    <template v-else>
                        <el-button
                                type="text"
                                icon="el-icon-edit-outline"
                                @click="editUser(scope.row.account_id)"
                        >编辑
                        </el-button
                        >
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                style="color: red"
                                @click="delOne(scope.row.account_id)"
                        >停用
                        </el-button
                        >
                        <el-button
                                type="text"
                                icon="el-icon-unlock"
                                style="color: green"
                                @click="clearPwd(scope.row.account_id)"
                        >解锁
                        </el-button
                        >
                        <el-button
                                type="text"
                                icon="el-icon-refresh"
                                style="color: yellowgreen"
                                @click="resetPassword(scope.row,'1')"
                        >重置密码
                        </el-button
                        >
                    </template>
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";
    import XLSX from "xlsx";

    export default {
        name: "admin",
        components: {
            baseTable,
            basePage,
            BranchTreePicker
        },
        data() {
            return {
                user: JSON.parse(sessionStorage.getItem("user")),
                role_id: sessionStorage.getItem("role_id"),
                role: sessionStorage.getItem("role"),
                form: {},
                bankid:"",
                bankname:"",
                dataList: [
                    {
                        label: "用户编号",
                        prop: "account_id",
                        sort: true,
                    },
                    {
                        label: "姓名",
                        prop: "name",
                    },
                    {
                        label: "电话",
                        prop: "phone",
                    },
                    {
                        label: "所属机构",
                        prop: "branch_name",
                    },
                    {
                        label: "角色",
                        prop: "role_name",
                    },
                    {
                        label: "创建时间",
                        prop: "create_time",
                        width: 120,
                    },
                ],
                userList: [],
                delList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                flag: "",
                newUserList: [],
                super_manager: this.$loacalData.code_GetEnums(this.$loacalData.listRole, "super_manager").value,
                roleList: [],
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
        mounted() {
            this.getRoleList();
        },
        methods: {
            // 机构列表
            nodeselectForSerch(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },
            //获取角色列表
            getRoleList() {
                this.$api.getUsertype().then((res) => {
                    this.roleList = res.data;
                });
            },
            // 新增用户
            adduser() {
                this.$router.push({
                    path: "/adduser",
                    name: "用户信息",
                    query: {
                        role: this.role,
                    },
                    params: {
                        //role: this.role,
                    },
                });
            },
            // 编辑用户
            editUser(id, flag) {
                this.$router.push({
                    path: "/adduser",
                    name: "用户信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 获取用户列表
            getUserList() {
                this.$api
                    .getUserList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        bankid: this.bankid,
                        ...this.form,
                    })
                    .then((res) => {
                        this.userList = res.data.userList;
                        this.total = res.data.total;
                        // this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageNum = 1;
                this.pageSize = size;
                this.getUserList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getUserList();
            },
            delOne(id) {
                this.delList = [];
                this.delList.push(id);
                this.deleteUser();
            },
            // 选择成员
            chooseUser(data) {
                this.delList = data.map((item) => {
                    return item.account_id;
                });
            },
            // 删除用户
            deleteUser() {
                console.log(this.delList);
                if (this.delList.indexOf("admin") > -1 || this.delList.indexOf("admin2") > -1) {
                    this.$message.error("用户超级管理员不可停用，请重新勾选用户");
                    return;
                }
                let user = JSON.parse(sessionStorage.getItem("user"));
                if (this.delList.indexOf(user.account_id) > -1) {
                    this.$message.error("用户" + user.name + "不可停用，请重新勾选用户");
                    return;
                }
                this.$confirm("确定停用选中的用户吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .deleteUser({
                                accountIds: this.delList,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getUserList();
                                this.$message.success("删除成功");
                                this.delList = [];
                            });
                    })
                    .catch(() => {
                        this.delList = [];
                    });
            },

            // 解锁
            clearPwd(account_id) {
                this.$api
                    .clearPwd({
                        account_id: account_id,
                    })
                    .then((res) => {
                        console.log(res);
                        this.getUserList();
                        this.$message.success("解除锁定成功");
                    });
            },

            // 重置登录/交易密码
            resetPassword(row, flag) {
                this.$confirm(
                    `确定重置${
                        flag == "1" ? "登录" : "交易"
                        }密码吗？重置后密码将设置为123456`,
                    "提示",
                    {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning",
                        center: true,
                    }
                ).then(() => {
                    this.$api
                        .resetPassword({
                            account_id: row.account_id,
                            flag: flag,
                        })
                        .then((res) => {
                            console.log(res);
                            this.$notify({
                                title: "重置密码成功",
                                duration: 3000,
                                message: `用户${row.name}的${
                                    flag == "1" ? "登录" : "交易"
                                    }密码已重置为123456`,
                                type: "success",
                            });
                        });
                });
            },
            // 批量导入Excel
            uploadFile() {
                let input = document.getElementById("file");
                input.value = "";
                input.click();
            },
            chooseFile() {
                let that = this;
                let input = document.getElementById("file");
                let file = input.files[0];
                input.value = "";
                this.$api
                    .uploadUser({
                        file: file,
                    })
                    .then((res) => {
                        if (res.fail_count == 0) {
                            this.$notify.success(`成功导入${res.success_count}条数据`);
                        } else {
                            let arr = "";
                            res.exist_account_id_list.map((item) => {
                                arr = arr + item.account_id + ", ";
                            });
                            arr = arr.substring(0, arr.length - 2);
                            this.$notify.info({
                                message: `共导入${res.total_count}条数据，${res.success_count}条成功，
              ${res.fail_count}条失败。员工编号${arr}已存在`,
                            });
                        }
                        this.getUserList();
                    });
            },
            // 下载模版
            downModal() {
                var aTag = document.createElement("a");
                aTag.download = "用户导入模版.xlsx";
                aTag.href = this.baseURL + "download/account_import.xlsx";
                aTag.click();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getUserList();
            });
        },
    };
</script>



