<template>
  <div>
<!--    <el-button type="warning" @click="exportPatrolLog">打印巡查表</el-button>-->
    <h1 align="center">广东南粤银行普惠金融支付服务点合规检查标准</h1>
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-tabs>
        <el-tab-pane v-for="(pj,index) in markSheelList" :key="index" :label="pj.content">
          <div v-for="cont in pj.list">
            <el-form-item :label="cont.content">
              <el-radio-group v-model="form[cont.mark_sheel_id]"
                              v-if="pj.mark_sheel_id !=='8' && pj.mark_sheel_id !=='9'">
                <el-radio :label="'0'" v-if="cont.mark_sheel_id !=='10101' && cont.mark_sheel_id !=='10106'">是
                </el-radio>
                <el-radio :label="'1'" v-if="cont.mark_sheel_id !=='10101' && cont.mark_sheel_id !=='10106'">否
                </el-radio>
              </el-radio-group>
              <el-radio-group v-model="form[cont.mark_sheel_id]"
                              v-if="pj.mark_sheel_id !=='8' && pj.mark_sheel_id !=='9'">
                <el-radio :label="'0'" v-if="cont.mark_sheel_id ==='10101' || cont.mark_sheel_id ==='10106'">否
                </el-radio>
                <el-radio :label="'1'" v-if="cont.mark_sheel_id ==='10101' || cont.mark_sheel_id ==='10106'">是
                </el-radio>
              </el-radio-group>
              <div v-if="pj.mark_sheel_id ==='8'">
                <el-select v-model="form[cont.mark_sheel_id]"
                           @change="checkResul"
                           clearable
                           placeholder="请选择">
                  <el-option
                      v-for="item in items1"
                      :label="item.label"
                      :key="item.label"
                      :value="item.label">
                  </el-option>
                </el-select>
                <p style="color: red">请上传服务点门头、内部环境、业务办理区、村民访谈合影的四张现场照片（照片内显示日期、地点）。</p>
                <div style="flex: auto">
                  <el-form-item label="巡查照片" prop="patrol_photo" style="width: 1000px">
                    <el-upload
                        action="/hnm/txCtrl?txcode=upload001"
                        list-type="picture-card"
                        ref="upload"
                        :before-upload="beforeAvatarUpload"
                        :file-list="fileList"
                        :data="{business: 'patrol',name: '123'}"
                        :headers='{Accept:"application/json"}'
                        :on-success="uploadReturn"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove"
                        :limit="6"
                        :on-exceed="checkPhotos">
                      <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                  </el-form-item>

                </div>
              </div>
              <div v-if="pj.mark_sheel_id ==='9'">
                <el-form-item>
                  <template>
                    <i style="color: red">点击单元格即可编辑内容</i>
                    <el-table
                        :header-cell-class-name="starAdd"
                        v-model="form[cont.mark_sheel_id]"
                        border
                        :data="tableData"
                        style="width: 100%"
                        max-height="250"
                        @cell-click="tableDbEdit"
                    >
                      <el-table-column
                          fixed
                          align="center"
                          type="index"
                          prop="index"
                          label="序号"
                          width="50">
                      </el-table-column>
                      <el-table-column
                          disabled="true"
                          align="center"
                          prop="ids"
                          label="工单流水号"
                          width="80">
                      </el-table-column>
                      <el-table-column
                          align="center"
                          prop="project_name"
                          label="项目名称"
                          width="110">
                        <template slot-scope="scope">
                          <el-select
                              filterable
                              v-model="scope.row.project_name"
                              size="small"
                              v-if="showInput === `project_name${scope.row.id}`"
                              @change='blurInput(scope.row.id,"pj_classify",scope.row.project_name)'
                          >
                            <el-option
                                v-for="item in projectList"
                                :label="item.project_name"
                                :value="item.project_name"
                                :key="item.pj_classify"
                                :disabled="item.project_name ==='系统自动排查'">
                            </el-option>
                          </el-select>
                          <span v-else>{{ scope.row.project_name }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          resizable show-overflow-tooltip
                          align="center"
                          prop="content_text"
                          label="问题描述"
                          width="180">
                        <template slot-scope="scope">
                          <!-- 条件判断如果满足则显示表单，否则默认展示文字 -->
                          <el-input type="textarea" size="small"
                                    v-model="scope.row.content_text"
                                    v-if="showInput == `content_text${scope.row.id}`"
                                    @blur='blurInput(scope.row.id,"content_text",scope.row.content_text)'
                                    v-focusTextarea>
                          </el-input>
                          <span v-else>{{ scope.row.content_text }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          align="center"
                          prop="risk_level"
                          label="风险等级"
                          width="100">
                        <template slot-scope="scope">
                          <el-select
                              v-model="scope.row.risk_level"
                              size="small"
                              v-if="showInput == `risk_level${scope.row.id}`"
                              @change='blurInput(scope.row.id,"riskLevel",scope.row.risk_level)'
                              v-focus>
                            <el-option
                                v-for="item in items2"
                                :label="item.label"
                                :value="item.label"
                                :key="item.label"
                            >
                            </el-option>
                          </el-select>
                          <span v-else>{{ scope.row.risk_level }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          align="center"
                          prop="end_date"
                          label="整改完成时间"
                          width="110">
                        <template slot-scope="scope">
                          <el-date-picker size="small"
                                          value-format="yyyy-MM-dd"
                                          v-model="scope.row.end_date"
                                          v-if="showInput == `end_date${scope.row.id}`"
                                          @blur='blurInput(scope.row.id,"end_date",scope.row.end_date)'
                                          :picker-options="setFulltime"
                                          @change="checkDate(scope.row.end_date)"
                                          v-focus>
                          </el-date-picker>
                          <span v-else>{{ scope.row.end_date }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          align="center" show-tooltip-when-overflow resizable
                          prop="requirement"
                          label="整改要求"
                          width="180">
                        <template slot-scope="scope">
                          <el-input type="textarea" size="small"
                                    v-model="scope.row.requirement"
                                    v-if="showInput == `requirement${scope.row.id}`"
                                    @blur='blurInput(scope.row.id,"requirement",scope.row.requirement)'
                                    v-focusTextarea>
                          </el-input>
                          <span v-else>{{ scope.row.requirement }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          disabled="true"
                          align="center"
                          prop="responsible"
                          label="整改人"
                          width="130">
                        <template slot-scope="scope">
                          <el-select
                              filterable
                              value-key="account_id"
                              v-model="scope.row.responsible"
                              size="small"
                              v-if="showInput == `responsible${scope.row.id}`"
                              @change='blurInput(scope.row.id,"responsible",scope.row.responsible)'
                          >
                            <el-option
                                v-for="item in userList" :key="item.account_id" :label="item.name"
                                :value="item.account_id">
                              <span>{{ item.name }}({{ item.account_id }})</span>
                            </el-option>
                          </el-select>
                          <span v-else>{{ scope.row.responsible }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                          align="center"
                          fixed="right"
                          label="操作"
                          width="60">
                        <template slot-scope="scope">
                          <el-button
                              @click.native.prevent="deleteRow(scope.$index, tableData)"
                              type="text"
                              size="small"
                              v-if="scope.row.status !== 1 && scope.row.status !== 2 && scope.row.status !== 3">
                            移除
                          </el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                    <el-button @click="addItem" icon="el-icon-plus"></el-button>
                  </template>
                </el-form-item>
              </div>
            </el-form-item>
          </div>
        </el-tab-pane>
      </el-tabs>
      <el-form-item>
        <el-button type="primary" @click="addOrEditPatLog">
          保存
        </el-button>
        <el-button type="primary" @click="reSetValue">
          重置
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>


<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import project from "./project";

export default {
  components: {
    project,
    baseTable,
    basePage,
  },
  data() {
    return {
      responsible: "",
      userList: [],
      projectList: [],
      fileList: [],
      role: sessionStorage.getItem("role"),
      form: {},
      dialogImageUrl: '',
      dialogVisible: false,
      markSheelList: {},
      items1: [
        {
          label: '情况正常',
        }, {
          label: '情况异常'
        }
      ],
      items2: [
        {
          label: '高',
        }, {
          label: '中',
        }, {
          label: '低',
        },
      ],
      photosNmb: false,
      setFulltime: this.disabledDate(),
      tableData: [],
      showInput: "",
      oldData: {},
      siteId: "",
      mgr_id: "",
      inspect_time: "",
      noneBtnImg: true,
    };
  },
  directives: {
// 通过自定义指令实现的表单自动获得光标的操作
    focus: {
      inserted: function (el) {
        if (el.tagName.toLocaleLowerCase() == 'input') {
          el.focus()
        } else {
          if (el.getElementsByTagName('input')) {
            el.getElementsByTagName('input')[0].focus()
          }
        }
        el.focus()
      }
    },
    focusTextarea: {
      inserted: function (el) {
        if (el.tagName.toLocaleLowerCase() == 'textarea') {
          el.focus()
        } else {
          if (el.getElementsByTagName('textarea')) {
            el.getElementsByTagName('textarea')[0].focus()
          }
        }
        el.focus()
      }
    }
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
  },
  mounted() {
    this.inspect_time = this.$route.query.inspect_time;
    //评分表
    this.$api.getQuitMarkSheel({}).then((res) => {
      this.markSheelList = res.data.markSheelList;
      console.log(res);
    });
    this.getPatrolLog()
    this.getAccountList();
    this.getProjectList();
  },
  methods: {
    checkResul() {
      if (this.form[80101] === '情况正常') {
        for (let i = 0; i < this.markSheelList.length; i++) {
          let pj = this.markSheelList[i]
          if (pj.mark_sheel_id !== '8' && pj.mark_sheel_id !== '9') {
            for (let j = 0; j < pj.list.length; j++) {
              if (this.form[pj.list[j].mark_sheel_id] === null || this.form[pj.list[j].mark_sheel_id] === undefined || this.form[pj.list[j].mark_sheel_id] === '1') {
                // this.$message.error("巡检条款 '" + pj.list[j].content + "' 未勾选或异常，请核实！");
                this.$message.error('巡检条款至少有一项未勾选或存在风险隐患，请将巡查结果修改为”情况异常“并提交巡查问题！');
                return false;
              }
            }
          }
        }
      }
      return true;
    },
    getPatrolLog() {
      //获取巡查记录
      this.$api.getPatrolLog({"patrol_id": this.$route.query.patrol_id}).then((res) => {
        this.fileList = [];
        this.form = res.data.logList;
        this.tableData = res.data.logList.contentList;

        if (this.form.patrol_photo) {
          this.$api.getPhotoList({"ids": this.form.patrol_photo}).then((res) => {
            this.fileList = res.data.photoList;
            this.form.patrol_photo = '';
            this.fileList.map((item) => {
              item.response = {data: [{id: item.name}]};
              this.form.patrol_photo += "'" + item.response.data[0].id + "',";
            })
            this.form.patrol_photo = this.form.patrol_photo.substr(0, this.form.patrol_photo.length - 1);
            console.log(this.form.patrol_photo);
          });

        }
      });
    },
    getProjectList() {

      if (this.projectList.length > 1) return;
      this.$api.getProjectList({}).then((res) => {
        this.projectList = res.data;
      });
      this.project_name = "";
    },
    getAccountList() {
      if (this.userList.length != 0) return;
      this.$api.getAccountList({}).then((res) => {
        let dataList = res.data;
        if (dataList) {
          for (let i = 0; i < dataList.length; i++) {
            this.userList = dataList;
          }
        }

      });
    },
    //自定义禁用时间
    disabledDate() {
      return {
        disabledDate: time => {
          let start_date = new Date(this.inspect_time) - 24 * 60 * 60 * 1000;
          let end_date = start_date + 91 * 24 * 60 * 60 * 1000;
          // alert(end_date)
          // 不可选历史天、可选当前天、可选未来天
          return time.getTime() < start_date || time.getTime() > end_date;
        }
      }
    },
    beforeAvatarUpload(file) {
      if (file.type !== 'image/png' && file.type !== 'image/jpg' && file.type !== 'image/jpeg') {
        this.$message.error('不支持png、jpg、jpeg以外格式的图片')
        return false
      }
      return true
    },
    starAdd(obj) {
      if (obj.columnIndex !== 0 && obj.columnIndex !== 1 && obj.columnIndex !== 8) {
        return 'star';
      }
    },
    checkPhotos(files, fileList) {
      this.$message.error("最多只能上传6张照片")
    },
    //图片上传成功回调
    uploadReturn(response, file, fileList) {
      console.log(file)
      this.form.patrol_photo = '';
      fileList.map((item) => {
        this.form.patrol_photo += "'" + item.response.data[0].id + "',";
      })
      this.form.patrol_photo = this.form.patrol_photo.substr(0, this.form.patrol_photo.length - 1);

      this.photosNmb = fileList.length < 4;

    },
    //删除上传图片
    handleRemove(file, fileList) {
      this.form.patrol_photo = '';
      fileList.map((item) => {
        this.form.patrol_photo += "'" + item.response.data[0].id + "',";
      })
      this.form.patrol_photo = this.form.patrol_photo.substr(0, this.form.patrol_photo.length - 1);
      this.photosNmb = fileList.length < 4;
      console.log(this.form.patrol_photo);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    async blurInput(id, name, value) {
      let obj = {}
      if ((this.tableData[name] != value)) {
        obj[name] = value//被改变的数据
        // 然后再写调用接口，提交内容的东西就可以了
      }
      this.showInput = ""
    },
    /*
    方法参数皆为框架方法的默认传参
    row 	行数据
    column	被点击的触发了方法的单元格
    event	事件
    */
    tableDbEdit(row, column) {
      if (row.status !== 1 && row.status !== 2 && row.status !== 3) {
        this.showInput = column.property + row.id
        this.oldData[column.property] = row[column.property]
      }
    },
    addItem() {
      this.tableData.push({
            id: this.tableData.length === 0 ? 1 : this.tableData[this.tableData.length - 1].id + 1,
            content_text: "",
            risk_level: "",
            end_date: "",
            requirement: "",
            site_no: this.form.site_id,
            responsible: this.form.mgr_id,
            project_name: "退出开业巡查"
          }
      )
      console.log(this.tableData)
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    addOrEditPatLog() {
      this.form.patrol_id = this.$route.query.patrol_id;
      this.form.source = 1;

      // for (let i = 0; i < this.markSheelList.length; i++) {
      //   let pj = this.markSheelList[i]
      //   if (pj.mark_sheel_id !== '8' && pj.mark_sheel_id !== '9') {
      //     for (let j = 0; j < pj.list.length; j++) {
      //       if (this.form[pj.list[j].mark_sheel_id] === null || this.form[pj.list[j].mark_sheel_id] === undefined || this.form[pj.list[j].mark_sheel_id] === '') {
      //         this.$message.error("请选择" + pj.list[j].content);
      //         return;
      //       }
      //     }
      //   }
      // }
      if (this.form[80101] === undefined || this.form[80101] === null || this.form[80101].length === 0) {
        this.$message.error("请选择巡查结果");
        return;
      }
      if (!this.checkResul()) {
        return;
      }
      if (this.form.patrol_photo === undefined || this.patrol_photo === null || this.form.patrol_photo === '') {
        this.$message.error("请上传巡查照片");
        return;
      } else if (this.photosNmb) {
        this.$message.error("请上传服务点门头、内部环境、业务办理区、村民访谈合影的四张现场照片（照片内显示日期、地点）");
        return;
      }
      console.log(this.photosNmb)
      for (let i = 0; i < this.tableData.length; i++) {

        if (this.tableData[i].id === undefined || this.tableData[i].id === null) {
          this.tableData[i].id = i + 1;
        }
        if (this.tableData[i].content_text === undefined || this.tableData[i].content_text === null || this.tableData[i].content_text.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        }
        if (this.tableData[i].project_name === undefined || this.tableData[i].project_name === null || this.tableData[i].project_name.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        } else {
          let project_name = this.tableData[i].project_name;
          let nameList = this.projectList;
          for (let j = 0; j < nameList.length; j++) {
            if (nameList[j].project_name === project_name) {
              this.tableData[i].pj_classify = nameList[j].pj_classify
            }
          }
        }
        if (this.tableData[i].risk_level === undefined || this.tableData[i].risk_level === null || this.tableData[i].risk_level.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        }
        if (this.tableData[i].end_date === undefined || this.tableData[i].end_date == null || this.tableData[i].end_date.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        }
        if (this.tableData[i].requirement === undefined || this.tableData[i].requirement === null || this.tableData[i].requirement.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        }
        if (this.tableData[i].responsible === undefined || this.tableData[i].responsible === null || this.tableData[i].responsible.length === 0) {
          this.$message.error("项目名称、问题描述、风险等级、整改时间、整改要求、整改人不可为空");
          return;
        }
        if (!this.checkDate(this.tableData[i].end_date)) {
          this.$message.error("整改时间不能超过巡查时间的90天");
          return;
        }
      }
      delete this.form.site_id;
      delete this.form.mgr_id;
      this.$api.addOrEditPatrolLog(this.form).then((res) => {
        this.$api.getPatrolLog({"patrol_id": this.$route.query.patrol_id}).then((res) => {
          this.$message.success("保存成功");
          this.$router.push({
            path: "/patrol",
            name: "巡查管理",
            query: {},
          });
        });
      });
    },
    reSetValue() {
      this.getPatrolLog()
    },
    checkDate(end_date) {
      let counDay = (new Date(end_date).getTime() - new Date(this.inspect_time).getTime()) / 24 / 60 / 60 / 1000;
      if (counDay > 90) {
        this.$message.error("整改时间不能超过巡查时间的90天")
        return false;
      } else {
        return true;
      }
    },
    exportPatrolLog() {
      //this.loading = true
      this.$api.exportPatrolLog({"patrol_id": this.$route.query.patrol_id}).then((res) => {
        //this.loading = false
        var fileName = res.data.fileName;
        var filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "导出失败，系统异常",
            type: "error",
          });
          return false;
        }

        var objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        var input01 = document.createElement("input");
        var input02 = document.createElement("input");
        var input03 = document.createElement("input");
        var input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "导出成功",
          type: "success",
        });
      });
    },
  }
};
</script>
<style>
table th.star div::before {
  content: '*';
  color: red;
}

.disUoloadSty .el-upload--picture-card {
  display: none; /* 上传按钮隐藏 */
}
</style>


