<template>
    <div>
        <el-button
                type="primary"
                size="small"
                @click="visible = true , data = []"
                style="margin-bottom: 10px"
        >新增流程
        </el-button
        >
        <el-form :inline="true" :model="form" style="margin-top: 15px">
            <el-form-item label="审批流程编号">
                <el-input
                        v-model="form.id"
                        clearable
                        placeholder="请输入审批流程编号"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="审批流程名称">
                <el-input
                        v-model="form.approval_process_name"
                        clearable
                        placeholder="请输入审批流程名称"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item label="所属分行">
                <el-input
                        v-model="form.branch"
                        clearable
                        placeholder="请输入所属分行"
                        size="small"
                ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button
                        type="primary"
                        @click="getApprovalProcessList"
                        size="small"
                        icon="el-icon-search"
                >搜索
                </el-button
                >
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="approvalProcessList">
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                            type="text"
                            icon="el-icon-edit-outline"
                            @click="editApprovalProcess(scope.row.id)"
                    >编辑
                    </el-button
                    >
                    <el-button
                            type="text"
                            icon="el-icon-delete"
                            style="color: red"
                            @click="deleteApprovalProcess(scope.row.id)"
                    >删除
                    </el-button
                    >
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
        <el-dialog
                :title="form.id ? '修改流程' : '新增流程'"
                :visible.sync="visible"
                width="45%"
                :before-close="handleClose"
        >
            <el-form ref="form" :model="form" :rules="rules" label-width="90px">
                <el-form-item label="审批流程名称" prop="approval_process_name">
                    <el-input size="small" v-model="form.approval_process_name"></el-input>
                </el-form-item>
                <el-form-item label="所属分行" prop="branch_id">
                    <div style="display: flex;">
                        <el-input :disabled="true" v-model="this.form.branch_name"></el-input>
                        <BranchTreePickerFH @nodeselect="nodeselectForShow" :dfks="this.form.branch_id"
                                            v-show="!form.id" :appendToBody="true"></BranchTreePickerFH>
                    </div>
                </el-form-item>

                <el-form-item label="备注" prop="remarks">
                    <el-input
                            type="textarea"
                            :autosize="{ minRows: 2, maxRows: 4 }"
                            placeholder="请输入内容"
                            v-model="form.remarks"
                    ></el-input>
                </el-form-item>

                <el-form-item
                        label="审批流程配置"
                >
                    <el-button type="primary" size="small" @click="() => appendFirst(data)">
                        添加审批流节点
                    </el-button>
                    <el-popover
                            placement="top-start"
                            trigger="hover"
                    >
                        <div style="color: red;height: 600px;overflow: auto">
                            <h5>1、目前可以对【新增站点】【风险处置整改】字段规则判断</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(1)、年龄:可以使用【&gt;/>=/&lt;/<=/=/!=】操作符,例:年龄 &gt; 60</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(2)、学历:可以使用【包含/不包含】操作符,若值为多项,请以|分割,例:学历 包括 本科|研究生及以上</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(3)、站长候选人信息情况:可以使用【包含/不包含】操作符,若值为多项,请以|分割,例:站长候选人信息情况 包括 企业主或个体户|企事单位职员</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(4)、站长是否特殊人员:可以使用【=/!=】操作符,例:站长是否特殊人员 = 是</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(5)、征信逾期情况:可以使用【=/!=】操作符,例:征信逾期情况 = 当期有逾期</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(6)、近2年是否发生过90天及以上的逾期:可以使用【=/!=】操作符,例:近2年是否发生过90天及以上的逾期 = 是</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(7)、信用类、保证类贷款余额:可以使用【&gt;/>=/&lt;/<=/=/!=】操作符,例:信用类、保证类贷款余额 >= 500000</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(8)、风险等级:可以使用【=/!=】操作符,例:风险等级 = 高</h5>
                            <h5>2、可以使用或项/和项关系符,或项下的节点只需满足其一即可,和项下的节点需要全部满足</h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;    模板一:<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;            "或项":[<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "项目":"年龄 > 60",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                },<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "项目":"学历 包含 本科|研究生及以上",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                }<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;            ]<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;        }
                            </h5>
                            <h5>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;    模板二:<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;            "或项":[<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "或项":[<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "项目":"年龄 > 60",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        },<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "项目":"学历 包含 本科|研究生及以上",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        }<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    ]<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                },<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    "和项":[<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "项目":"年龄 <= 60",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        },<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        {<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "项目":"征信逾期情况 = 当期有逾期",<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                            "备注":"无"<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                        }<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                    ]<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                }<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;            ]<br/>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;        }
                            </h5>
                        </div>
                        <span slot="reference" style="color: red">说明</span>
                    </el-popover>
                </el-form-item>
            </el-form>

            <div>
                <el-tree
                        :data="data"
                        :props="defaultProps"
                        node-key="path"
                        default-expand-all
                        :expand-on-click-node="false"
                >
                    <div class="custom-tree-node" slot-scope="{ node, data }">
                        <span><i class="el-icon-user"></i>{{ node.label }} </span>

                        <el-popover
                                placement="top-start"
                                trigger="hover"
                                width="500"
                        >
                            <span style="color: red">备注:<br>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一、若符合下面条件,则由该节点角色审批<br>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;二、若不符合下面条件,则转同级节点的下一角色处理（若无下一角色或不满
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                足所有角色条件,则结束审批）
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            </span>
                            <el-input
                                    type="textarea"
                                    :autosize="{ minRows: 6, maxRows: 12 }"
                                    placeholder="请输入内容"
                                    :value="JSON.stringify(data.node_item,null,4)"
                            ></el-input>
                            <span slot="reference">说明</span>
                        </el-popover>

                        <span>
                              <el-button
                                      type="primary"
                                      size="small"
                                      @click="() => append(node, data)"
                              >
                                添加
                              </el-button>
                              <el-button type="warning" size="small" @click="() => editNode(data)">
                                编辑
                              </el-button>
                              <el-button type="danger" size="small" @click="() => removeNode(data)">
                                删除
                              </el-button>
                        </span>
                    </div>
                </el-tree
                >
            </div>

            <div slot="footer" style="text-align: center">
                <el-button size="small" @click="handleClose">取 消</el-button>
                <el-button type="primary" size="small" @click="addApprovalProcess"
                >确 定
                </el-button
                >
            </div>
        </el-dialog>

        <el-dialog
                title="审批流节点设置"
                :visible.sync="visible2"
                width="40%"
                :show-close="false"
        >
            <el-form ref="form2" :model="form2" :rules="rules2" label-position="right" label-width="60px">
                <el-form-item label="角色" prop="role_id">
                    <el-select
                            v-model="form2.role_id"
                            @change="changeValue"
                    >
                        <el-option
                                v-for="item in approvalRoleList"
                                :key="item.role_id"
                                :label="item.role_name"
                                :value="item.role_id"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="条件项" prop="approval_item">
                    <el-input
                            type="textarea"
                            :autosize="{ minRows: 6, maxRows: 12 }"
                            placeholder="请输入内容"
                            v-model="form2.approval_item"
                    ></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="visible2 = false , form2 = {}">取 消</el-button>
                <el-button size="small" type="primary" @click="createNode"
                >确 定
                </el-button
                >
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePickerFH from "../../components/BranchTreePickerFH";

    export default {
        components: {
            baseTable,
            basePage,
            BranchTreePickerFH,
        },
        name: "approvalProcessDetail",
        data() {
            return {

                defaultProps: {
                    children: "sub_tree",
                    label: "node_name",
                },
                labelPosition: "right",
                form2: {},
                data: [],
                visible2: false,
                approvalItemList: [],
                approvalSymbolList1:[
                    ">",">=","<","<=","=","!="
                ],
                approvalSymbolList2:[
                    "=","!="
                ],
                approvalSymbolList3:[
                    "包含","不包含"
                ],
                num: "0000",



                form: {},
                dataList: [
                    {
                        label: "审批流程编号",
                        prop: "id",
                        sort: true,
                    },
                    {
                        label: "审批流程名称",
                        prop: "approval_process_name",
                        sort: true,
                    },
                    {
                        label: "审批类型",
                        prop: "approval_type",
                    },
                    {
                        label: "所属分行",
                        prop: "branch",
                    },
                    {
                        label: "备注",
                        prop: "remarks",
                    },
                ],
                rules: {
                    approval_process_name: [
                        {required: true, message: "请输入审批流程名称", trigger: "blur"},
                    ],
                },
                rules2: {
                    role_id: [
                        {required: true, message: "请选择角色", trigger: "change"},
                    ],
                },
                approvalRoleList: [],
                approvalProcessList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        computed: {},
        mounted() {
            this.getApprovalRoleList();
            this.getApprovalItemList();
            this.initNodeData();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {

            //获取角色名称
            changeValue(val){
                let obj = {};
                obj = this.approvalRoleList.find((item)=>{
                    return item.role_id === val;
                });
                this.$set(this.form2, "role_name", obj.role_name);
            },
            //初始化data
            initNodeData() {
                this.data = [];
            },
            // 创建一级岗位
            appendFirst() {
                this.form2 = {};
                this.visible2 = true;
            },

            //校验审批项
            checkItem(approval_item){
                try {
                    let node_item = JSON.parse(approval_item);
                    if(Array.isArray(node_item)){
                        this.$message({
                            message: "审批项格式错误,首级节点不能为数组",
                            type: "error",
                        });
                        return false;
                    }
                    let keys = Object.keys(node_item);

                    let num1 = 0;
                    let num2 = 0;
                    let num3 = 0;
                    keys.forEach(
                        (key)=>{
                            if(key === "或项"){
                                num1 = 1;
                            }else if(key === "和项"){
                                num2 = 1;
                            }else{
                                num3 = 1;
                                return false;
                            }
                        }
                    );
                    if(num3 === 1){
                        this.$message({
                            message: "审批项格式错误,首级节点只能包括或项/和项",
                            type: "error",
                        });
                        return false;
                    }
                    if(num1 === 1 && num2 === 1){
                        this.$message({
                            message: "审批项格式错误,首级节点只能包括一个或项/和项",
                            type: "error",
                        });
                        return false;
                    }
                    return this.checkItemDetail(node_item,keys);
                }catch (e) {
                    console.log(e);
                    this.$message({
                        message: "审批项格式错误,json解析错误",
                        type: "error",
                    });
                }
            },

            checkItemDetail(node_item,keys){
                this.num = "0000";
                this.checkItemDetail2(node_item,keys);
                if(this.num === "0000"){
                    return true;
                }else{
                    return false;
                }
            },




            checkItemDetail2(node_item,keys){

                //校验详细项
                let num1 = "0";
                let num2 = "0";
                let num3 = "0";
                let num4 = "0";

                for(let i=0;i<keys.length;i++){
                    let key = keys[i];
                    if(key ==="或项"){
                        num1 = "1";
                        if(Array.isArray(node_item.或项)){
                            for(let j=0;j<node_item.或项.length;j++) {
                                let 或项 = node_item.或项[j];
                                if(Array.isArray(或项)){
                                    this.$message({
                                        message: "审批项格式错误,节点:"+key+"不能重复为数组",
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                                if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")){
                                    this.checkItemDetail2(或项,Object.keys(或项));
                                }
                            }
                        }else{
                            if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                this.checkItemDetail2(node_item.或项, Object.keys(node_item.或项));
                            }
                        }
                    }else if(key ==="和项"){
                        num1 = "1";
                        if(Array.isArray(node_item.和项)){
                            for(let j=0;j<node_item.和项.length;j++) {
                                let 和项 = node_item.和项[j];
                                if(Array.isArray(和项)){
                                    this.$message({
                                        message: "审批项格式错误,节点:"+key+"不能重复为数组",
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                                if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                    this.checkItemDetail2(和项, Object.keys(和项));
                                }
                            }
                        }else{
                            if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                this.checkItemDetail2(node_item.和项, Object.keys(node_item.和项));
                            }
                        }
                    } else if(key ==="项目"){
                        num2 = "1";
                        //一级校验
                        if(Array.isArray(node_item.项目)){
                            num4 = "1";
                            this.$message({
                                message: "审批项格式错误,节点:"+key+"不能为数组",
                                type: "error",
                            });
                            this.num = num1+num2+num3+num4;
                            return false;
                        }

                        //二级校验
                        let value = node_item.项目;
                        let value_arr = value.split(" ");
                        if(value_arr.length !== 3){
                            num4 = "1";
                            this.$message({
                                message: "审批项格式错误,节点:"+key+"格式不正确",
                                type: "error",
                            });
                            this.num = num1+num2+num3+num4;
                            return false;
                        }

                        let value1 = value_arr[0];//校验项
                        let value2 = value_arr[1];//符号
                        let value3 = value_arr[2];//值

                        let obj1 = this.approvalItemList.find((item)=>{
                            return item.approval_item === value1;
                        });

                        if(obj1 === undefined){
                            num4 = "1";
                            this.$message({
                                message: "审批项格式错误,节点:"+key+"含有非支持校验项:"+value1,
                                type: "error",
                            });
                            this.num = num1+num2+num3+num4;
                            return false;
                        }

                        if(value1 === "年龄"){
                            let obj2 = this.approvalSymbolList1.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let numReg = /^(0|\+?[1-9][0-9]*)$/; // 数值正则
                            if(!numReg.test(value3)){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }
                        }

                        if(value1 === "信用类、保证类贷款余额"){
                            let obj2 = this.approvalSymbolList1.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let numReg = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/; // 数值正则
                            if(!numReg.test(value3)){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }
                        }

                        if(value1 === "站长是否特殊人员"){
                            let obj2 = this.approvalSymbolList2.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let obj3 = this.approvalItemList.find((item)=>{
                                if(item.approval_item === "站长是否特殊人员"){
                                    let item_value_arr = item.approval_item_value.split("|");
                                    return item_value_arr.find((item2)=>{
                                        return item2 === value3;
                                    });
                                }
                            });

                            if(obj3 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }
                        }

                        if(value1 === "征信逾期情况"){
                            let obj2 = this.approvalSymbolList2.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let obj3 = this.approvalItemList.find((item)=>{
                                if(item.approval_item === "征信逾期情况"){
                                    let item_value_arr = item.approval_item_value.split("|");
                                    return item_value_arr.find((item2)=>{
                                        return item2 === value3;
                                    });
                                }
                            });

                            if(obj3 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }
                        }

                        if(value1 === "近2年是否发生过90天及以上的逾期"){
                            let obj2 = this.approvalSymbolList2.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let obj3 = this.approvalItemList.find((item)=>{
                                if(item.approval_item === "近2年是否发生过90天及以上的逾期"){
                                    let item_value_arr = item.approval_item_value.split("|");
                                    return item_value_arr.find((item2)=>{
                                        return item2 === value3;
                                    });
                                }
                            });

                            if(obj3 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }
                        }

                        if(value1 === "学历"){
                            let obj2 = this.approvalSymbolList3.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let value3_arr = value3.split("|");
                            value3_arr.forEach((item)=>{
                                let flag = false;
                                this.approvalItemList.forEach((item2)=>{
                                    if(item2.approval_item === "学历"){
                                        let item_value_arr = item2.approval_item_value.split("|");
                                        item_value_arr.forEach((item3)=>{
                                            if(item === item3){
                                                flag = true;
                                            }
                                        });
                                    }
                                });
                                if(!flag){
                                    num4 = "1";
                                    this.$message({
                                        message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                            });
                        }

                        if(value1 === "站长候选人信息情况"){
                            let obj2 = this.approvalSymbolList3.find((item)=>{
                                return item === value2;
                            });
                            if(obj2 === undefined){
                                num4 = "1";
                                this.$message({
                                    message: "审批项格式错误,校验项:"+value1+"含有非支持符号:"+value2,
                                    type: "error",
                                });
                                this.num = num1+num2+num3+num4;
                                return false;
                            }

                            let value3_arr = value3.split("|");
                            value3_arr.forEach((item)=>{
                                let flag = false;
                                this.approvalItemList.forEach((item2)=>{
                                    if(item2.approval_item === "站长候选人信息情况"){
                                        let item_value_arr = item2.approval_item_value.split("|");
                                        item_value_arr.forEach((item3)=>{
                                            if(item === item3){
                                                flag = true;
                                            }
                                        });
                                    }
                                });
                                if(!flag){
                                    num4 = "1";
                                    this.$message({
                                        message: "审批项格式错误,校验项:"+value1+"含有非支持值:"+value3,
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                            });
                        }
                    }else if(key ==="备注"){
                        num2 = "1";
                        if(Array.isArray(node_item.备注)){
                            num4 = "1";
                            this.$message({
                                message: "审批项格式错误,节点:"+key+"不能为数组",
                                type: "error",
                            });
                            this.num = num1+num2+num3+num4;
                            return false;
                        }
                    }else{
                        num3 = "1";
                        this.$message({
                            message: "审批项格式错误,节点含有非支持条件项:"+key,
                            type: "error",
                        });
                        this.num = num1+num2+num3+num4;
                        return false;
                    }
                }
                if(num1 === "1" && num2 === "1"){
                    this.$message({
                        message: "审批项格式错误,同级节点不能同时存在归集项及条件项",
                        type: "error",
                    });
                    this.num = num1+num2+num3+num4;
                    return false;
                }
            },



            createNode() {
                this.$refs.form2.validate((valid) => {
                    if (valid) {
                        if (this.form2.node_code) {
                            // 修改审批流节点
                            //校验
                            if(this.form2.approval_item && !this.checkItem(this.form2.approval_item)){
                                return;
                            }
                            //重新给data赋值
                            let nodedata = {};
                            nodedata.node_code = this.form2.node_code;
                            nodedata.node_id = this.form2.role_id;
                            nodedata.node_name = this.form2.role_name;
                            if(this.form2.approval_item){
                                nodedata.node_item = JSON.parse(this.form2.approval_item);
                            }else{
                                nodedata.node_item = this.form2.approval_item;
                            }
                            //修改
                            this.updateNodeData(this.data,nodedata);
                            this.form2 = {};
                            this.visible2 = false;

                        } else {
                            // 新增审批流节点
                            //校验
                            if(this.form2.approval_item && !this.checkItem(this.form2.approval_item)){
                                return;
                            }
                            //重新给data赋值
                            let nodedata = {};
                            nodedata.node_code = this.guid();
                            nodedata.node_id = this.form2.role_id;
                            nodedata.node_name = this.form2.role_name;
                            if(this.form2.approval_item){
                                nodedata.node_item = JSON.parse(this.form2.approval_item);
                            }else{
                                nodedata.node_item = this.form2.approval_item;
                            }
                            nodedata.super_code = this.form2.super_code;
                            nodedata.sub_tree = [];
                            this.pushNodeData(this.data,nodedata);
                            this.form2 = {};
                            this.visible2 = false;
                        }
                    }
                })
            },

            pushNodeData(data,nodedata){
                if(!nodedata.super_code){
                    data.push(nodedata);
                }else{
                    data.map((item) => {
                        if(item.node_code == nodedata.super_code){
                            item.sub_tree.push(nodedata);
                        }else{
                            if(item.sub_tree.length>0){
                                this.pushNodeData(item.sub_tree,nodedata);
                            }
                        }
                    });
                }
            },

            updateNodeData(data,nodedata){
                data.map((item) => {
                    if(item.node_code == nodedata.node_code){
                        item.node_id = nodedata.node_id;
                        item.node_name = nodedata.node_name;
                        item.node_item = nodedata.node_item;
                        item.role_id = nodedata.node_id;
                        item.role_name = nodedata.node_name;
                        if(nodedata.node_item){
                            item.approval_item = JSON.stringify(nodedata.node_item,null,4);
                        }else{
                            item.approval_item = nodedata.node_item;
                        }

                    }else{
                        if(item.sub_tree.length>0){
                            this.updateNodeData(item.sub_tree,nodedata);
                        }
                    }
                });
            },

            // 添加下一级节点
            append(node, data) {
                this.visible2 = true;
                this.$set(this.form2, "super_code", data.node_code);
            },
            editNode(data) {
                console.log(data);
                this.visible2 = true;
                this.form2 = data;
                this.$set(this.form2, "role_id", data.node_id);
                this.$set(this.form2, "role_name", data.node_name);
                if(data.node_item){
                    this.$set(this.form2, "approval_item", JSON.stringify(data.node_item,null,4));
                }else{
                    this.$set(this.form2, "approval_item", data.node_item);
                }
            },
            removeNode(data) {
                this.$confirm("确定删除审批流节点？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                }).then(() => {
                    //重新给data赋值
                    this.deleteNodeData(this.data,data.node_code);
                }).catch(()=>{});
            },

            deleteNodeData(data,node_code){
                data.map((item) => {
                    if(item.node_code == node_code){
                        if(item.sub_tree.length>0){
                            this.$message({
                                message: "存在下级节点，不可删除",
                                type: "warning",
                            });
                            return;
                        }
                        let index = data.indexOf(item);
                        if(index > -1){
                            data.splice(index,1);
                        }
                    }else{
                        if(item.sub_tree.length>0){
                            this.deleteNodeData(item.sub_tree,node_code);
                        }
                    }
                });
            },
            //用于生成uuid
            guid() {
                function S4() {
                    return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
                };
                return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
            },
            // 获取审批项目列表
            getApprovalItemList() {
                let approval_type = this.$route.query.approval_type;
                this.$api
                    .getApprovalItemList({
                        approval_type: approval_type,
                    })
                    .then((res) => {
                        this.approvalItemList = res.data;
                    });
            },



            // 机构列表
            nodeselectForShow(node) {
                this.$set(this.form, "branch_name", node.branch_name);
                this.$set(this.form, "branch_id", node.branch_id);
            },
            // 审批角色列表
            getApprovalRoleList() {
                this.$api.getApprovalRoleList().then((res) => {
                    this.approvalRoleList = res.data;
                });
            },

            // 新增流程/更新流程
            addApprovalProcess() {
                this.$refs.form.validate((valid) => {
                    let approval_type = this.$route.query.approval_type;
                    if (valid) {

                        if(this.data.length < 1){
                            this.$message({
                                message: "请设置审批流程",
                                type: "error",
                            });
                            return;
                        }

                        this.form.new_approval_process = JSON.stringify(this.data);
                        //this.$set(this.form, "new_approval_process", JSON.stringify(this.data));
                        if (this.form.id) {
                            this.$api.updateApprovalProcess({
                                approval_type: approval_type,
                                ...this.form,
                            }).then((res) => {
                                this.handleClose();
                                this.getApprovalProcessList();
                            });
                        } else {
                            this.$api.addApprovalProcess({
                                approval_type: approval_type,
                                ...this.form,
                            }).then((res) => {
                                this.handleClose();
                                this.getApprovalProcessList();
                            });
                        }
                    }
                });
            },
            // 编辑审批流程
            editApprovalProcess(id) {
                this.$api
                    .getApprovalProcess({
                        id,
                    })
                    .then((res) => {
                        console.log(res);
                        this.visible = true;
                        this.form = res.data.msg[0];
                        this.data = [];
                        if(res.data.msg[0].new_approval_process){
                            this.data = JSON.parse(res.data.msg[0].new_approval_process);
                        }
                    });
            },
            // 获取审批流程列表
            getApprovalProcessList() {
                let approval_type = this.$route.query.approval_type;
                this.$api
                    .getApprovalProcessList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        approval_type: approval_type,
                        ...this.form,
                    })
                    .then((res) => {
                        this.approvalProcessList = res.data.approvalProcessList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getApprovalProcessList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getApprovalProcessList();
            },
            // 删除审批流程
            deleteApprovalProcess(id) {
                this.$confirm("确定删除该审批流程吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .deleteApprovalProcess({
                                id: id,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getApprovalProcessList();
                            });
                    })
                    .catch(() => {
                    });
            },

            handleClose() {
                this.visible = false;
                this.form = {};
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getApprovalProcessList();
            });
        },
    };
</script>
