<template>
	<div>
		<el-form :inline="true" :model="form" style="margin-top: 15px">
			<el-form-item label="所属机构" prop="bankid">
				<div style="display: flex;">
					<el-input readonly="" size="small" v-model="bankname"></el-input>
					<BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
				</div>
			</el-form-item>

			<el-form-item label="机构类型" prop="team_flag">
				<el-select v-model="form.team_flag" filterable clearable size="small">
					<el-option key="0" label="组织" value="0"></el-option>
					<el-option key="1" label="团队" value="1"></el-option>
					<el-option key="2" label="片区" value="2"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="getBranchList" size="small" icon="el-icon-search">搜索
				</el-button>
			</el-form-item>
		</el-form>
		<base-table :list="dataList" :data="branchList">
			<el-table-column label="操作" align="center">
				<template slot-scope="scope"
					v-if="(role == '1' || role == '2') && (scope.row.team_flag == '1' || scope.row.team_flag == '2')">
					<el-button type="text" icon="el-icon-edit-outline" @click="editBranch(scope.row.branch_id)">编辑
					</el-button>
					<el-button type="text" icon="el-icon-delete" style="color: red"
						@click="deleteBranch(scope.row.branch_id)">删除
					</el-button>
				</template>
			</el-table-column>
		</base-table>
		<base-page :page="pageNum" :size="pageSize" :total="total" @size="changeSize" @page="changePage"></base-page>

		<el-dialog :title="form.branch_id ? '修改' : '新增'" :visible.sync="visible" width="45%"
			:before-close="handleClose">
			<el-form ref="form" :model="form" :rules="rules" label-width="90px">
				<el-form-item label="团队/片区编号" prop="branch_id" style="width: 360px" v-if="this.form.branch_id ? true:false">
					<el-input v-model="form.branch_id" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="团队/片区名称" prop="branch_name" style="width: 360px">
					<el-input v-model="form.branch_name"></el-input>
				</el-form-item>
				<el-form-item label="团队/片区类型" prop="team_flag" style="width: 360px">
					<el-radio-group v-model="form.team_flag" :disabled="form.branch_id ? true:false">
						<el-radio label="1">团队</el-radio>
						<el-radio label="2">片区</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="所属机构" prop="porgid" style="width: 360px">
					<div style="display: flex;">
						<el-input :disabled="true" v-model="this.form.porgname"></el-input>
						<BranchTreePicker @nodeselect="nodeselectForShow" :dfks="this.form.porgid" :appendToBody="true"></BranchTreePicker>
					</div>
				</el-form-item>
				<el-form-item label="成立日期" style="width: 360px" prop="generate_date">
					<el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
						v-model="form.generate_date" style="width: 100%;"></el-date-picker>
				</el-form-item>
				<el-form-item label="团队/片区负责人" prop="team_leader" style="width: 360px">
					<el-select v-model="form.team_leader" clearable filterable>
						<el-option v-for="item in sysUserList" :key="item.account_id" :label="item.account_id+'-'+item.name"
							:value="item.account_id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="团队人数" prop="team_num" style="width: 360px" v-if="form.team_flag == '1'">
					<el-input v-model="form.team_num" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="团队数" prop="team_count" style="width: 360px" v-if="form.team_flag == '2'">
					<el-input v-model="form.team_count" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="片区人数" prop="area_num" style="width: 360px" v-if="form.team_flag == '2'">
					<el-input v-model="form.area_num" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="其他" prop="remarks" style="width: 360px">
					<el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4 }" placeholder="请输入内容"
						v-model="form.remarks"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" style="text-align: center">
				<el-button size="small" @click="handleClose">取 消</el-button>
				<el-button type="primary" size="small" @click="addtBranch">确 定
				</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import baseTable from "../../components/Table";
	import basePage from "../../components/Pagination";
	import BranchTreePicker from "../../components/BranchTreePicker";

	export default {
		components: {
			baseTable,
			basePage,
			BranchTreePicker
		},
		name: "branch",
		data() {
			return {
				role: sessionStorage.getItem("role"),
				form: {},
                bankid:"",
				bankname:"",
				dataList: [{
						label: "机构编号",
						prop: "branch_id",
						sort: true,
					},
					{
						label: "机构名称",
						prop: "branch_name",
						sort: true,
					},
					{
						label: "上级机构编号",
						prop: "porgid",
					},
				],
				rules: {
					branch_name: [{
						required: true,
						message: "请输入团队/片区名称",
						trigger: "blur"
					}, ],
					team_flag: [{
						required: true,
						message: "请选择团队/片区类型",
						trigger: "change"
					}, ],
					porgid: [{
						required: true,
						message: "请选择所属机构",
						trigger: "change"
					}, ],
					generate_date: [{
						required: true,
						message: "请选择成立日期",
						trigger: "blur"
					}, ],
				},
				branchList: [],
				sysUserList: [],
				pageNum: 1,
				pageSize: 10,
				total: 0,
				visible: false,
			};
		},
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
		mounted() {
			//人员列表
			this.getAccountList();
		},
		watch: {
			visible(val) {
				if (!val) {
					this.form = {};
				}
			},
		},
		methods: {
			// 机构列表
            nodeselectForSerch(node) {
				console.log(node);
				this.bankname = node.branch_name;
				this.bankid = node.branch_id;
			},
            nodeselectForShow(node) {
                this.$set(this.form, "porgname", node.branch_name);
                this.$set(this.form, "porgid", node.branch_id);
            },
			// 机构下用户列表
			getAccountList() {
				if (this.sysUserList.length != 0) return;
				this.$api.getAccountList({}).then((res) => {
					this.sysUserList = res.data;
				});
			},
			// 获取机构列表
			getBranchList() {
				this.$api
					.getBranchList({
						page: this.pageNum - 1,
						number: this.pageSize,
                        bankid: this.bankid,
						...this.form,
					})
					.then((res) => {
						this.branchList = res.data.branchList;
						this.total = res.data.total;
						//this.form = {};
					});
			},

			//新增/更新
			addtBranch() {
				this.$refs.form.validate((valid) => {
					if (valid) {
						if (this.form.branch_id) {
							this.$api.updatetBranch(this.form).then((res) => {
								this.handleClose();
								this.getBranchList();
							});
						} else {
							this.$api.addtBranch(this.form).then((res) => {
								this.handleClose();
								this.getBranchList();
							});
						}
					}
				});
			},
			// 编辑
			editBranch(branch_id) {
				this.$api
					.getBranchDetail({
						branch_id,
					})
					.then((res) => {
						console.log(res);
						this.visible = true;
						this.form = res.data.msg[0];
					});
			},
			// 删除机构
			deleteBranch(branch_id) {
				this.$confirm("确定删除吗？", "提示", {
						confirmButtonText: "确定",
						cancelButtonText: "取消",
						type: "warning",
						center: true,
					})
					.then(() => {
						this.$api
							.deleteBranch({
								branch_id: branch_id,
							})
							.then((res) => {
								console.log(res);
								this.getBranchList();
							});
					})
					.catch(() => {});
			},


			// 修改页面大小
			changeSize(size) {
				this.pageSize = size;
				this.getBranchList();
			},
			// 跳转页码
			changePage(page) {
				this.pageNum = page;
				this.getBranchList();
			},

			handleClose() {
				this.visible = false;
				this.form = {};
			},
		},
		// 跳转保存数据
		beforeRouteEnter(to, from, next) {
			next((vm) => {
				if (to.params.searchKey) {
					vm.form = to.params.searchKey;
				}
				vm.getBranchList();
			});
		},
	};
</script>



