<template>
    <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="未处理助农交易" name="voucher1">
            <div>
                <el-form :inline="true" :model="form" style="margin-top: 15px">

                    <el-form-item label="交易日期" prop="start_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.start_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="——" prop="end_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.end_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="所属机构" prop="orgid">
                        <div style="display: flex;">
                            <el-input size="small" v-model="orgname" clearable></el-input>
                            <BranchTreePicker @nodeselect="nodeselectForSerchOrg"></BranchTreePicker>
                        </div>
                    </el-form-item>
                    <el-form-item label="落地支行" prop="bankid">
                        <div style="display: flex;">
                            <el-input size="small" v-model="bankname" clearable></el-input>
                            <BranchTreePickerAll @nodeselect="nodeselectForSerchBank"></BranchTreePickerAll>
                        </div>
                    </el-form-item>
                    <el-form-item label="服务点" prop="site_no">
                        <el-select v-model="form.site_no" filterable clearable size="small">
                            <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                                       :value="item.site_no"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="管辖客户经理" prop="account_id">
                        <el-select v-model="form.account_id" filterable clearable size="small">
                            <el-option v-for="item in sysUserList" :key="item.account_id" :label="item.name"
                                       :value="item.account_id"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item>
                        <el-button
                                type="primary"
                                @click="getVoucherList"
                                size="small"
                                icon="el-icon-search"
                        >搜索
                        </el-button
                        >
                    </el-form-item>
                </el-form>
                <base-table :list="dataList" :data="voucherList">
                    <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    icon="el-icon-edit-outline"
                                    @click="showVoucher(scope.row.tsdt,scope.row.site_no,scope.row.mgr_id,scope.row.mgr_name,scope.row.site_name)"
                            >操作
                            </el-button
                            >
                        </template>
                    </el-table-column>
                </base-table>
                <base-page
                        :page="pageNum"
                        :size="pageSize"
                        :total="total"
                        @size="changeSize"
                        @page="changePage"
                ></base-page>
            </div>
        </el-tab-pane>
        <el-tab-pane label="已处理助农交易" name="voucher2">
            <div>
                <el-form :inline="true" :model="form" style="margin-top: 15px">

                    <el-form-item label="交易日期" prop="start_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.start_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="——" prop="end_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="选择日期" v-model="form.end_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="所属机构" prop="orgid">
                        <div style="display: flex;">
                            <el-input readonly="" size="small" v-model="orgname"></el-input>
                            <BranchTreePicker @nodeselect="nodeselectForSerchOrg"></BranchTreePicker>
                        </div>
                    </el-form-item>
                    <el-form-item label="落地支行" prop="bankid">
                        <div style="display: flex;">
                            <el-input readonly="" size="small" v-model="bankname"></el-input>
                            <BranchTreePickerAll @nodeselect="nodeselectForSerchBank"></BranchTreePickerAll>
                        </div>
                    </el-form-item>
                    <el-form-item label="服务点" prop="site_no">
                        <el-select v-model="form.site_no" filterable clearable size="small">
                            <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                                       :value="item.site_no"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="管辖客户经理" prop="account_id">
                        <el-select v-model="form.account_id" filterable clearable size="small">
                            <el-option v-for="item in sysUserList" :key="item.account_id" :label="item.name"
                                       :value="item.account_id"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item>
                        <el-button
                                type="primary"
                                @click="getVoucherList"
                                size="small"
                                icon="el-icon-search"
                        >搜索
                        </el-button
                        >
                    </el-form-item>
                </el-form>
                <base-table :list="dataList2" :data="voucherList">
                    <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    icon="el-icon-edit-outline"
                                    @click="showVoucher(scope.row.tsdt,scope.row.site_no,scope.row.mgr_id,scope.row.mgr_name,scope.row.site_name)"
                            >详情
                            </el-button
                            >
                        </template>
                    </el-table-column>
                </base-table>
                <base-page
                        :page="pageNum"
                        :size="pageSize"
                        :total="total"
                        @size="changeSize"
                        @page="changePage"
                ></base-page>
            </div>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";
    import BranchTreePickerAll from "../../components/BranchTreePickerAll";

    export default {
        components: {
            baseTable,
            basePage,
            BranchTreePicker,
            BranchTreePickerAll,
        },
        name: "voucher",
        data() {
            return {
                activeName: 'voucher1',
                form: {},
                bankid: "",
                bankname: "",
                orgid: "",
                orgname: "",
                siteList: [],
                sysUserList: [],
                dataList: [
                    {
                        label: "落地支行",
                        prop: "branch_name",
                    },
                    {
                        label: "服务点编号",
                        prop: "site_no",
                        sort: true,
                    },
                    {
                        label: "服务点名称",
                        prop: "site_name",
                    },
                    {
                        label: "交易日期",
                        prop: "tsdt",
                        sort: true,
                    }
                ],
                dataList2: [
                    {
                        label: "落地支行",
                        prop: "branch_name",
                    },
                    {
                        label: "服务点编号",
                        prop: "site_no",
                        sort: true,
                    },
                    {
                        label: "服务点名称",
                        prop: "site_name",
                    },
                    {
                        label: "交易日期",
                        prop: "tsdt",
                        sort: true,
                    },
                    {
                        label: "处理人",
                        prop: "creator_name",
                    },
                    {
                        label: "检查结果",
                        prop: "gd_result",
                    }
                ],
                voucherList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
        mounted() {
            //人员列表
            this.getAccountList();
            //站点列表
            this.getSiteSelectList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
                this.form = {};
                this.voucherList=[];
                this.pageNum=1;
                this.pageSize=10;
                this.total=0;
                this.visible=false;
                this.getVoucherList();
            },

            //所属机构列表
            nodeselectForSerchOrg(node) {
                console.log(node);
                this.orgname = node.branch_name;
                this.orgid = node.branch_id;
            },
            // 落地支行列表
            nodeselectForSerchBank(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },
            // 机构下用户列表
            getAccountList() {
                if (this.sysUserList.length != 0) return;
                this.$api.getAccountList({}).then((res) => {
                    this.sysUserList = res.data;
                });
            },
            //站点列表
            getSiteSelectList() {
                this.$api.getSiteSelectList({
                    "all_flag" : "1"
                }).then((res) => {
                    this.siteList = res.data.siteList;
                });

            },

            // 获取助农取款业务凭证列表
            getVoucherList() {
                if(this.activeName == "voucher1"){//未处理助农取款业务凭证列表
                    this.$api
                        .getVoucherList({
                            page: this.pageNum - 1,
                            number: this.pageSize,
                            orgid: this.orgid,
                            bankid: this.bankid,
                            ...this.form,
                        })
                        .then((res) => {
                            this.voucherList = res.data.voucherList;
                            this.total = res.data.total;
                            //this.form = {};
                        });
                }else{//已处理助农取款业务凭证列表
                    this.$api
                        .getVoucher2List({
                            page: this.pageNum - 1,
                            number: this.pageSize,
                            orgid: this.orgid,
                            bankid: this.bankid,
                            ...this.form,
                        })
                        .then((res) => {
                            res.data.voucherList.map((item) => {
                                return (item.gd_result = item.gd_result === "0" ? "账实相符" : "账实不符");
                            });
                            this.voucherList = res.data.voucherList;
                            this.total = res.data.total;
                            //this.form = {};
                        });
                }
            },

            // 查看助农取款业务凭证
            showVoucher(tsdt,site_no,mgr_id,mgr_name,site_name) {
                this.$router.push({
                    path: "/voucherDetail",
                    name: "助农交易详情",
                    query: {
                        tsdt: tsdt,
                        site_no: site_no,
                        site_name: site_name,
                        mgr_id: mgr_id,
                        mgr_name: mgr_name,
                        activeName:this.activeName,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getVoucherList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getVoucherList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getVoucherList();
            });
        },
    };
</script>



