<template>
  <el-row class="container">
    <el-col :span="24" class="header">
      <el-col :span="8" class="logo" :class="collapsed ? 'logo-collapse-width' : 'logo-width'">
        {{ collapsed ? "" : sysName }}
      </el-col>
      <el-col :span="2">
        <div class="tools" @click.prevent="collapse" style="display: none">
          <i class="fa fa-align-justify"></i>
        </div>
        <el-button type="primary" size="small" icon="el-icon-arrow-left" @click="$router.back(-1)">
          返回
        </el-button>
      </el-col>
      <el-col :span="14" class="userinfo">
				<span class="el-dropdown-link userinfo-inner">
					<i class="el-icon-s-shop" size="26" style="margin-right: 5px"></i>{{ branchName }}
				</span>
        <span class="el-dropdown-link userinfo-inner">
					<i class="el-icon-s-custom" size="26" style="margin-right: 5px"></i>{{ roleName }}
				</span>
        <span class="el-dropdown-link userinfo-inner">
					<i class="el-icon-user-solid" size="26" style="margin-right: 5px"></i>{{ sysUserName }}
				</span>
        <el-button type="primary" icon="el-icon-edit" @click="updatePassWord">修改密码</el-button>
        <el-button type="primary" icon="el-icon-switch-button" @click="logout">退出登录</el-button>
      </el-col>
    </el-col>
    <el-col :span="24" class="main">
      <aside>
        <el-menu :default-openeds="dav" :unique-opened="true" :default-active="menuIndex">
          <template v-for="(item,i) in routerList">
            <el-submenu v-if="item.children.length>0" :key="i" :index="item.menu_code">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{ item.menu_name }}</span>
              </template>
              <template v-for="(child,c) in item.children">
                <el-submenu v-if="child.children" :key="c" :index="child.menu_code">
                  <template slot="title">
                    <i :class="child.icon"></i>
                    <span>{{ child.menu_name }}</span>
                  </template>
                  <el-menu-item v-for="(secondeChild, seIndex) in child.children" :key="seIndex"
                                :index="secondeChild.menu_code"
                                @click="showpage(secondeChild)">
                    <i :class="secondeChild.icon"></i> {{ secondeChild.menu_name }}
                  </el-menu-item>
                </el-submenu>
                <el-menu-item v-else :key="c" :index="child.menu_code"
                              @click="showpage(child)">
                  <i :class="child.icon"></i> {{ child.menu_name }}
                </el-menu-item>
              </template>
            </el-submenu>
            <el-menu-item @click="showpage(item)" v-else :key="i" :index="item.menu_code">
              <template slot="title">
                {{ item.menu_name }}
              </template>
            </el-menu-item>
          </template>
        </el-menu>
      </aside>
      <section class="content-container">
        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="breadcrumb-container" style="margin-bottom: 20px">
            <strong class="title">{{ $route.name }}</strong>
            <el-breadcrumb separator="/" class="breadcrumb-inner">
              <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                {{ item.name }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </el-col>
          <el-col :span="24" class="content-wrapper">
            <!--						<transition name="fade" mode="out-in">-->
            <!--							&lt;!&ndash; <keep-alive include="operateRecord"> &ndash;&gt;-->
            <!--								<router-view :key="$route.fullPath"></router-view>-->
            <!--							&lt;!&ndash; </keep-alive> &ndash;&gt;-->
            <!--						</transition>-->
            <keep-alive :max="1">
              <router-view :key="$route.fullPath" v-if="$route.meta.keepAlive"></router-view>
            </keep-alive>
            <router-view :key="$route.fullPath" v-if="!$route.meta.keepAlive"></router-view>
          </el-col>
        </div>
      </section>
    </el-col>

    <el-dialog
        :title="'修改密码'"
        :visible.sync="visible"
        width="45%"
        :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="原密码" prop="old_pwd" style="width: 360px">
          <el-input v-model="form.old_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="new_pwd" style="width: 360px">
          <el-input v-model="form.new_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="重复新密码" prop="confirm_pwd" style="width: 360px">
          <el-input v-model="form.confirm_pwd" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="onSubmit"
        >确 定
        </el-button
        >
      </div>
    </el-dialog>

  </el-row>
</template>

<script>
import {get} from "../api/request";
import {getUserList} from "../api/api2";

export default {
  name: "home",
  data() {
    return {
      sysName: "惠农管理系统",
      collapsed: false,
      currentChoose: "",
      branchName: "",
      roleName: "",
      sysUserName: "",
      sysUserAvatar: "",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      rules: {
        old_pwd: [
          {required: true, message: "请输入原密码", trigger: "blur"},
        ],
        new_pwd: [
          {required: true, message: "请输入新密码", trigger: "blur"},
          {
            min: 6,
            max: 20,
            message: "密码在 6 到 20 个字符",
            trigger: "blur",
          },
        ],
        confirm_pwd: [
          {required: true, message: "请确认新密码", trigger: "blur"},
          {
            min: 6,
            max: 20,
            message: "密码在 6 到 20 个字符",
            trigger: "blur",
          },
        ],
      },
      routerList: [],
      dav: [],
      menuIndex: '',
      visible: false,
    };
  },
  computed: {},
  mounted() {
    var user = sessionStorage.getItem("user");
    if (user) {
      user = JSON.parse(user);
      console.log(user)
      this.branchName = user.branch_name || "";
      this.roleName = user.role_name || "";
      this.sysUserName = user.name || "";
      this.sysUserAvatar = user.avatar || "";
    }

    let muneu = JSON.parse(sessionStorage.menuList);
    muneu.unshift({
      create_time: "2020-09-07 15:28:25",
      creator: "anonymousUser",
      icon: "el-icon-s-cooperation",
      menu_code: "1",
      menu_name: "首页",
      menu_path: "index",
      menu_type: "1",
      super_code: null
    })
    this.menu = muneu
    this.dealwithMenu(muneu)

  },
  watch: {
    $route: {
      handler(obj) {
        if (!obj.meta.flag) {
          this.currentChoose = obj.path;
        }
        this.handleMenu()
        this.checkSessionStorageItem()
      },
      immediate: true
    },
  },
  methods: {
    checkSessionStorageItem() {
      const user = JSON.parse(sessionStorage.getItem("user"));
      if (user === null) {
        this.$router.push("/login");
      }
    },
    //退出登录
    handleMenu() {
      let menu = JSON.parse(sessionStorage.menuList);
      let result = menu.some(item => this.$route.path == ('/' + item.menu_path) && item.menu_path !== '/')
      let res = menu.find(item => this.$route.path == ('/' + item.menu_path) && item.menu_path !== '/')
      if (result) {
        this.dav = [res.super_code]
        this.dav.push(menu.filter(item => res.super_code == item.menu_code)[0].super_code)
        this.menuIndex = res.menu_code
        console.log(this.dav, this.menuIndex)
      }
    },
    showpage(child) {
      this.$router.push({
        path: child.menu_path
      });
    },

    dealwithMenu(menus) {
      let temp = [];
      for (let i = 0; i < menus.length; i++) {
        let children = [];
        if (menus[i].super_code == null) {
          for (let j = 0; j < menus.length; j++) {
            let secondChildren = []
            if (menus[j].super_code == menus[i].menu_code) {
              for (let k = 0; k < menus.length; k++) {
                if (menus[k].super_code == menus[j].menu_code) {
                  secondChildren.push(menus[k])
                  menus[j].children = secondChildren
                }
              }
              children.push(menus[j]);
            }
          }

          menus[i].children = children;
          temp.push(menus[i]);
        }
      }
      this.routerList = temp;
      // this.dav = [this.routerList[0].menu_code];
    },

    updatePassWord: function () {
      this.visible = true;
    },
    //修改密码
    onSubmit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.new_pwd === this.form.confirm_pwd) {
            this.$api.updatePwd(this.form).then((res) => {
              console.log(res.data);
              this.handleClose();
              this.$message.success("修改密码成功");
            });
          } else {
            this.$message({
              message: "两次密码输入不一致",
              type: "error",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleClose() {
      this.visible = false;
      this.form = {};
    },

    logout: function () {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {}).then(() => {
        sessionStorage.removeItem("user");
        sessionStorage.removeItem("route");
        // sessionStorage.clear();
        _this.$router.push("/login");
        // location.reload();
      });
    },
    //折叠导航栏
    collapse: function () {
      this.collapsed = !this.collapsed;
    },
    showMenu(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
          "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    },
  },

  // destroyed() {
  //   window.removeEventListener("beforeunload");
  // },
};
</script>

<style scoped lang="scss">
@import "~scss_vars";

.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;

  .grid-content {
    height: 100%;
  }

  .header {
    height: 60px;
    line-height: 60px;
    background: $color-primary;
    color: #fff;

    .userinfo {
      text-align: right;
      padding-right: 15px;
      float: right;

      .userinfo-inner {
        cursor: pointer;
        color: #fff;

        img {
          width: 40px;
          height: 40px;
          border-radius: 20px;
          margin: 10px 0px 10px 10px;
          float: right;
        }
      }
    }

    .logo {
      //width:230px;
      height: 60px;
      font-size: 22px;
      padding-left: 20px;
      padding-right: 20px;
      border-color: rgba(238, 241, 146, 0.3);
      border-right-width: 1px;
      border-right-style: solid;

      img {
        width: 40px;
        float: left;
        margin: 10px 10px 10px 18px;
      }

      .txt {
        color: #fff;
      }
    }

    .logo-width {
      width: 230px;
    }

    .logo-collapse-width {
      width: 60px;
    }

    .tools {
      padding: 0px 23px;
      width: 14px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
    }
  }

  .main {
    display: flex;
    // background: #324057;
    position: absolute;
    top: 60px;
    bottom: 0px;
    overflow: hidden;

    aside {
      flex: 0 0 230px;
      width: 230px;
      overflow: auto;
      // position: absolute;
      // top: 0px;
      // bottom: 0px;
      .el-menu {
        height: 100%;
      }

      .collapsed {
        width: 60px;

        .item {
          position: relative;
        }

        .submenu {
          position: absolute;
          top: 0px;
          left: 60px;
          z-index: 99999;
          height: auto;
          display: none;
        }
      }
    }

    .menu-collapsed {
      flex: 0 0 60px;
      width: 60px;
    }

    .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
    }

    .content-container {
      // background: #f1f2f7;
      flex: 1;
      // position: absolute;
      // right: 0px;
      // top: 0px;
      // bottom: 0px;
      // left: 230px;
      overflow-y: scroll;
      padding: 20px;
      box-sizing: border-box;

      .breadcrumb-container {

        //margin-bottom: 15px;
        .title {
          width: 200px;
          float: left;
          color: #475669;
        }

        .breadcrumb-inner {
          float: right;
        }
      }

      .content-wrapper {
        height: calc(100% - 20px);
        background-color: #fff;
        box-sizing: border-box;
      }
    }
  }
}
</style>



