<template>
  <div class="index">
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card shadow="hover">
          <div
            slot="header"
            class="clearfix"
          >
            <span>业绩展示</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="check">查看</el-button>
          </div>
          <div class="img-container">
            <img :src="animateJson.achievment"></img>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card shadow="hover">
          <div
            slot="header"
            class="clearfix"
          >
            <span>消息推送</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="check">查看</el-button>
          </div>
          <div class="img-container">
            <img :src="animateJson.messageJson"></img>
          </div>
        </el-card>
      </el-col>
      
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card shadow="hover">
          <div
            slot="header"
            class="clearfix"
          >
            <span>政策文件</span>
            <el-button style="float: right; padding: 3px 0" type="text"  @click="check">查看</el-button>
          </div>
          <div class="img-container">
            <img :src="animateJson.fileJson"></img>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card shadow="hover">
          <div
            slot="header"
            class="clearfix"
          >
            <span>重要通知</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="check">查看</el-button>
          </div>
          <div class="img-container">
            <img :src="animateJson.noticeJson"></img>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import * as noticeJson from '@/assets/notice.png'
import * as achievment from '@/assets/achievment.png'
import * as messageJson from '@/assets/message.png'
import * as fileJson from '@/assets/file.png'
export default {
  
  data() {
    return {
      animateJson:{
        noticeJson,
        achievment,
        messageJson,
        fileJson
      },
    }
  },
  methods: {
    check() {
      this.$message.info('功能暂未开放，敬请期待')
    }
  },
  
};
</script>

<style scoped>
.el-row{
  margin-bottom: 20px;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
}
img{
  width: 200px;
  height: 200px;
  
}
.img-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-around;
}
</style>



