<template>
	<el-form :model="form" :rules="rules2" ref="form" label-position="left" label-width="0px" class="demo-ruleForm login-container">
		<h3 class="title">惠农管理系统</h3>
		<div class="loginTitle">
			<h2>登录</h2>
		</div>
		<template>
			<el-form-item prop="userid">
				<el-input type="text" v-model="form.userid" prefix-icon="el-icon-s-custom" placeholder="请输入员工编号"></el-input>
			</el-form-item>
			<el-form-item prop="entry_pwd">
				<el-input type="password" v-model="form.entry_pwd" auto-complete="off" show-password prefix-icon="el-icon-lock"
				 placeholder="请输入密码"></el-input>
			</el-form-item>
			<el-form-item prop="img_flowid" v-if="count > 2">
				<el-row style="width: 100%">
					<el-col :span="15">
						<el-input type="text" v-model="form.img_flowid" auto-complete="off" placeholder="请输入验证码"></el-input>
					</el-col>
					<el-col :span="8" :offset="1"><img id="img" :src="this.baseURL + 'code?txcode=code001'" @click="getCode" /></el-col>
				</el-row>
			</el-form-item>
		</template>
		<div style="text-align: right">
			<el-button type="text" @click="forgetPass">{{
        form.flag == "1" ? "忘记密码" : "收不到验证码"
      }}</el-button>
		</div>
		<el-form-item style="width: 100%">
			<el-button type="primary" style="width: 100%" @click.native.prevent="submit" :loading="logining">登录</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
	import Home from "./Home.vue";
	import Main from "./Main.vue";
	//import NProgress from 'nprogress'
	export default {
		data() {
			var checkPhone = (rule, value, callback) => {
				if (!value) {
					return callback(new Error("联系电话不能为空"));
				}
				var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
				if (phoneReg.test(value)) {
					callback();
				} else {
					return callback(new Error("手机号码非法"));
				}
			};
			return {
				logining: false,
				form: {
					flag: "1",
				},
				rules2: {
					userid: [{
						required: true,
						message: "请输入账号",
						trigger: "blur"
					}],
					entry_pwd: [{
						required: true,
						message: "请输入密码",
						trigger: "blur"
					}],
					phone: [{
							required: true,
							message: "请输入手机号码",
							trigger: "blur"
						},
						{
							validator: checkPhone,
							trigger: "blur"
						},
					],
					phone_pass: [{
						required: true,
						message: "请输入手机验证码",
						trigger: "blur"
					}, ],
				},
				count: 0,
				src: "",
				sendFlag: false,
				time: 60,
			};
		},
		watch: {
			count(num) {
				if (num >= 3) {
					this.getCode();
				}
			},
		},
		methods: {
			// 获取图形验证码
			getCode() {
				var date = new Date().getTime(); // 防止缓存
				var change = document.getElementById("img");
				change.src = this.baseURL + `code?txcode=code001&${date}`;
			},
			getPhoneCode() {
				console.log(this.form.phone);
				if (this.sendFlag) return;
				var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
				if (!phoneReg.test(this.form.phone)) {
					return;
				}
				this.$api
					.getPhoneCode({
						mobile: this.form.phone,
					})
					.then((res) => {
						this.sendFlag = true;
						var loop = setInterval(() => {
							this.time--;
							if (this.time == 0) {
								this.time = 59;
								this.sendFlag = false;
								clearInterval(loop);
							}
						}, 1000);
					});
			},
			// 提交
			submit() {
				this.$refs.form.validate((valid) => {
					if (valid) {
						this.logining = true;
						setTimeout(() => {
							this.logining = false;
						}, 2000);
						if (this.form.flag == "2") {
							this.form.userid = this.form.phone;
							this.form.entry_pwd = this.form.phone_pass;
						}
						this.$api
							.login(this.form)
							.then((res) => {
								this.count = 0;
								this.logining = false;
								sessionStorage.setItem("user", JSON.stringify(res.userMap));
								sessionStorage.setItem("role", res.userRole);
                                sessionStorage.setItem("role_level", res.role_level);
								sessionStorage.setItem("role_id", res.role_id);
								sessionStorage.setItem("menuList", JSON.stringify(res.menuList));
                                sessionStorage.setItem("funcList", JSON.stringify(res.funcList));

								this.$store.commit("changeName", "1");
								for(let i=0;i<res.menuList.length;i++){
									if(res.menuList[i].menu_path!="/"){
										this.$router.push({
											path: '/index'
										});
										break;
									}
								}
							})
							.catch((err) => {
								console.log(err);

							});
					} else {
						console.log("error submit!!");
						return false;
					}
				});
			},
			// 忘记密码/收不到验证码
			forgetPass() {
				this.$alert("请联系管理员重置密码", "忘记密码", {
					confirmButtonText: "确定",
					center: true,
					callback: (action) => {
						console.log("弹出");
					},
				});
			},
		},
	};
</script>

<style lang="scss" scoped>
	.login-container {
		/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
		-webkit-border-radius: 5px;
		border-radius: 5px;
		-moz-border-radius: 5px;
		background-clip: padding-box;
		margin: 180px auto;
		width: 350px;
		padding: 35px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		box-shadow: 0 0 25px #cac6c6;

		.title {
			margin: 0px auto 40px auto;
			text-align: center;
			color: #505458;
		}

		.loginTitle {
			text-align: center;
			color: #505458;
		}

		.remember {
			margin: 0px 0px 35px 0px;
		}
	}

	img {
		height: 40px;
		// margin-left: 10%;
	}
</style>



