<template>
    <div>
        <el-form :inline="true" :model="form">
            <el-button type="warning" size="small" @click="exportStaffInfos" v-if="isStaffDetail" :loading="loading">导出全部查询数据</el-button>
            <div style="margin-top: 15px">
                <el-form-item label="所属机构" prop="bankid">
                    <div style="display: flex;">
                        <el-input readonly="" size="small" v-model="bankname"></el-input>
                        <BranchTreePicker @nodeselect="nodeselectForSerch"></BranchTreePicker>
                    </div>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input
                            v-model="form.mgr_name"
                            clearable
                            placeholder="请输入姓名"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="role_id">
                    <el-select v-model="form.role_id" filterable clearable size="small">
                        <el-option
                                v-for="item in roleList"
                                :key="item.role_id"
                                :label="item.role_name"
                                :value="item.role_id"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="工号">
                    <el-input
                            v-model="form.mgr_id"
                            clearable
                            placeholder="请输入工号"
                            size="small"
                    ></el-input>
                </el-form-item>
                <el-form-item label="手机号码">
                    <el-input
                            v-model="form.phone"
                            clearable
                            placeholder="请输入手机号码"
                            size="small"
                    ></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button
                            type="primary"
                            @click="getStaffList"
                            size="small"
                            icon="el-icon-search"
                    >搜索
                    </el-button>
                </el-form-item>
            </div>
        </el-form>
        <base-table :list="dataList" :data="userList">
            <el-table-column fixed="right" label="操作" align="center" width="240">
                <template slot-scope="scope">
                    <template>
                        <el-dropdown placement="bottom" v-if="!isStaffDetail">
                            <el-button
                                    type="text"
                                    icon="el-icon-edit-outline"
                                    @click="beforeEdit(scope.row.approval_status, scope.row.staff_show)"
                            >操作
                            </el-button
                            >
                            <i v-if="!isStaffDetail" class="el-icon-arrow-down el-icon--right"></i>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item @click.native="editStaff(scope.row.id,false,type003)" v-if="scope.row.approval_status=='2' && scope.row.staff_show=='0' "
                                >人员基本信息调整
                                </el-dropdown-item
                                >
                                <el-dropdown-item @click.native="editStaff(scope.row.id,false,type014)" v-if="scope.row.approval_status=='2' && scope.row.staff_show=='0' "
                                >人员角色调整
                                </el-dropdown-item
                                >
                                <el-dropdown-item @click.native="editStaff(scope.row.id,false,type015)" v-if="scope.row.approval_status=='2' && scope.row.staff_show=='0' "
                                >人员所属机构调整
                                </el-dropdown-item
                                >
                                <el-dropdown-item @click.native="deleteStaff(scope.row.id,false,type021)" v-if="scope.row.approval_status=='2' && scope.row.staff_show=='0' "
                                >停用
                                </el-dropdown-item
                                >
                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>
                    <el-button type="text" icon="el-icon-edit-outline"
                               @click="editStaff(scope.row.id,true)" v-if="isStaffDetail">查看
                    </el-button>
                </template>
            </el-table-column>
        </base-table>
        <base-page
                :page="pageNum"
                :size="pageSize"
                :total="total"
                @size="changeSize"
                @page="changePage"
        ></base-page>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";
    import BranchTreePicker from "../../components/BranchTreePicker";
    import XLSX from "xlsx";

    export default {
        name: "staff",
        components: {
            baseTable,
            basePage,
            BranchTreePicker
        },
        data() {
            return {
                user: JSON.parse(sessionStorage.getItem("user")),
                role_id: sessionStorage.getItem("role_id"),
                role: sessionStorage.getItem("role"),
                form: {},
                bankid: "",
                bankname: "",
                dataList: [
                    {
                        label: "工号",
                        prop: "mgr_id",
                        sort: true,
                    },
                    {
                        label: "姓名",
                        prop: "mgr_name",
                    },
                    {
                        label: "角色",
                        prop: "role_name",
                    },
                    {
                        label: "所属机构",
                        prop: "branch_name",
                    },
                    {
                        label: "资产余额（元）",
                        prop: "amt1",
                    },
                    {
                        label: "资产年日均（元）",
                        prop: "amt2",
                    },
                    {
                        label: "存款余额（元）",
                        prop: "amt3",
                    },
                    {
                        label: "存款年日均（元）",
                        prop: "amt4",
                    },
                    {
                        label: "试营业服务点数",
                        prop: "site_num1",
                    },
                    {
                        label: "开业服务点数",
                        prop: "site_num2",
                    },
                    {
                        label: "客户数",
                        prop: "cus_num1",
                    },
                ],
                userList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                flag: "",
                type003: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "003").code,
                type014: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "014").code,
                type015: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "015").code,
                type021: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "021").code,
                roleList: [],
                loading: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
            isStaffDetail() {
                return this.$route.path == '/staffDetail'
            },
        },
        mounted() {
            this.getRoleList();
        },
        methods: {
            // 机构列表
            nodeselectForSerch(node) {
                console.log(node);
                this.bankname = node.branch_name;
                this.bankid = node.branch_id;
            },
            // 角色类型
            getRoleList() {
                this.$api.getStaffRoleList().then((res) => {
                    this.roleList = res.data;
                });
            },
            // 导出
            exportStaffInfos() {
                this.loading = true;
                this.$api.exportStaffInfos({
                    bankid: this.bankid,
                    ...this.form
                }).then((res) => {
                    this.loading = false;
                    var fileName = res.data.fileName;
                    var filePath = res.data.filePath;
                    if (null == fileName || null == filePath) {
                        this.$message({
                            message: "导出失败，系统异常",
                            type: "error",
                        });
                        return false;
                    }

                    var objSelector = document.createElement("form");
                    document.body.appendChild(objSelector);
                    objSelector.method = "post";
                    objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
                    //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
                    objSelector.enctype = "multipart/form-data";
                    var input01 = document.createElement("input");
                    var input02 = document.createElement("input");
                    var input03 = document.createElement("input");
                    var input04 = document.createElement("input");
                    input01.setAttribute("name", "filePath");
                    input01.setAttribute("type", "hidden");
                    input01.setAttribute("value", filePath);
                    input02.setAttribute("name", "fileName");
                    input02.setAttribute("type", "hidden");
                    input02.setAttribute("value", fileName);
                    input03.setAttribute("name", "isDownload");
                    input03.setAttribute("type", "hidden");
                    input03.setAttribute("value", "1");
                    input04.setAttribute("name", "isDelete");
                    input04.setAttribute("type", "hidden");
                    input04.setAttribute("value", "1");
                    objSelector.appendChild(input01);
                    objSelector.appendChild(input02);
                    objSelector.appendChild(input03);
                    objSelector.appendChild(input04);
                    objSelector.submit();

                    this.$message({
                        message: "导出成功",
                        type: "success",
                    });
                });
            },

            beforeEdit(approval_status, is_show){
                if(approval_status !== '2' || is_show !== '0'){
                    this.$message({
                        message: "暂无可做操作",
                        type: "warning",
                    });
                }
            },
            // 编辑人员
            editStaff(id, flag ,approval_type) {
                this.$router.push({
                    path: "/addStaffDetail",
                    // name: "人员信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 删除人员
            deleteStaff(id, flag ,approval_type) {
                this.$router.push({
                    path: "/addStaffDetail",
                    // name: "人员信息",
                    query: {
                        id: id,
                        role: this.role,
                        check: flag, // 查看不可编辑
                        approval_type: approval_type,
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },

            // 获取人员列表
            getStaffList() {
                this.$api
                    .getStaffList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        bankid: this.bankid,
                        ...this.form,
                    })
                    .then((res) => {
                        this.userList = res.data.userList;
                        this.total = res.data.total;
                        // this.form = {};
                    });
            },
            // 修改页面大小
            changeSize(size) {
                this.pageNum = 1;
                this.pageSize = size;
                this.getStaffList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getStaffList();
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getStaffList();
            });
        },
    };
</script>



