<template>
	<div style="margin-left: 1px;">
		<el-button size="small" type="primary" @click="geBankList()">选择</el-button>
		<el-dialog title="机构选择" :visible.sync="dialogVisible" :append-to-body="appendToBody">
			<el-tree :data="showlist" node-key="branch_id" :props="defaultProps" :default-expanded-keys="dfks" >

				     <span class="custom-tree-node" slot-scope="{ node, data }">
				        <span>{{ node.label }}</span>
				        <span>
				          <el-button
				            type="text"
				            size="mini"
				            @click="() => nodec(data)">
				            选择
				          </el-button>

				        </span>
				      </span>

			</el-tree>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "basePage",
		props: {
			dfk: {
				type: String,
				default: "",
			},
            appendToBody:false,
		},
		data() {
			return {
				dialogVisible: false,
				showlist: [],
				defaultProps: {
					children: 'children',
					label: 'branch_name',

				},
				dfks:[]
			}
		},
		methods: {
			nodec(node) {
				console.log(node)
				this.$emit("nodeselect", node);
				this.dialogVisible=false;
			},

			geBankList() {
				// if (this.bankList.length != 0) return;

				if(this.showlist.length>0){
					this.dialogVisible = true;
					return;
				}

				this.$api.getBranchByLevel({bran_level:'1'}).then((res) => {
					this.dealwithBranch(res.data);
					this.dialogVisible = true;
				});
			},

			dealwithBranch(branchList) {
                this.showlist = branchList;
                this.dfks=[this.dfk];

			},
			findchildren(all, theone) {
				let res = [];
				console.log(theone,11)
				for (let i = 0; i < all.length; i++) {
					if (all[i].porgid == theone.branch_id ) {
						let temp = this.findchildren(all, all[i]);
						if (temp.length > 0) {
							all[i].children = temp;
						}
						res.push(all[i])
					}
				}
				return res;
			},
		},
	};
</script>



