import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

const listApprovalType = [
    { code: "002", value:"新增人员" },
    { code: "003", value:"人员基本信息调整" },
    { code: "004", value: "新增站点(试营业)" },
    { code: "005", value: "站点基本信息调整" },
    { code: "006", value: "助农POS申请" },
    { code: "007", value: "助农POS申请和申请开业" },
    { code: "008", value: "退出申请" },
    { code: "009", value: "新增团队" },
    { code: "010", value: "团队基本信息调整" },
    { code: "011", value:"团队类型调整" },
    { code: "012", value:"团队所属机构调整" },
    { code: "013", value: "团队负责人调整" },
    { code: "014", value: "人员角色调整" },
    { code: "015", value: "人员所属机构调整" },
    { code: "016", value: "站点站长信息调整" },
    { code: "017", value: "站点POS信息调整" },
    { code: "018", value: "站点管辖客户经理调整" },
    { code: "019", value: "站点落地支行调整" },
    { code: "020", value: "删除团队" },
    { code: "021", value: "删除人员" },
    { code: "022", value: "转试营业申请" },
    { code: "023", value: "风险处置整改" },
];

const listRole = [
    { code: "super_manager", value:"2021061500000001" },
    { code: "head_manager", value:"2021061500000002" },
    { code: "head_science", value:"2021061500000003" },
    { code: "sub_manager", value:"2021061500000004" },
    { code: "sub_leader", value:"2021061500000005" },
    { code: "area_leader", value:"2021061500000006" },
    { code: "team_leader", value:"2021061500000007" },
    { code: "customer", value:"2021061500000008" },
];

/*
* 取出枚举
*/

// 根据值获取枚举项
function value_GetEnums(enumsItem, value) {
    for (var i = 0; i < enumsItem.length; i++) {
        if (enumsItem[i].value == value) return enumsItem[i];
    }
    return {
        "code": "",
        "value": ""
    };
};

// 根据编号获取枚举项
function code_GetEnums(enumsItem, code) {
    for (var i = 0; i < enumsItem.length; i++) {
        if (enumsItem[i].code == code) return enumsItem[i];
    }
    return {
        "code": "",
        "value": ""
    };
};

export default {
    listApprovalType,
    listRole,
    value_GetEnums,
    code_GetEnums,
}





