<template>
  <div>
    <el-button type="primary" @click="addRiskCustomer" size="small">新增风险客户</el-button>
    <!--    <el-button type="primary" size="small" icon="el-icon-folder-add" @click="uploadFile">批量导入</el-button>-->
    <!--    <input type="file" id="file" @change="chooseFile" style="display: none"/>-->
    <!--    <el-button type="warning" size="small" @click="downModal">下载风险客户导入模版</el-button>-->
    <!--    <el-button type="warning" size="small" @click="exportRiskCustomerInfos">导出数据</el-button>-->
    <el-button-group>
      <measure></measure>
    </el-button-group>
    <el-form :model="info" :inline="true" style="margin-top: 15px" label-position="left">

      <el-form-item label="风险等级" style="width: 300px" clearable>
        <el-select v-model="info.risk_level" clearable size="small">
          <el-option v-for="item in items2" :label="item.label" :value="item.value" :key="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="客户姓名" style="width: 300px">
        <el-input v-model="info.cust_name" size="small" clearable></el-input>
      </el-form-item>
      <el-form-item label="证件类型" style="width: 300px">
        <el-select v-model="info.cert_type_cd" size="small" clearable>
          <el-option v-for="item in items" :label="item.dict_name" :value="item.dict_value" :key="item.dict_value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="备注" style="width: 300px" clearable>
        <el-select v-model="info.type_cd" clearable size="small">
          <el-option v-for="item in item3" :label="item.label" :value="item.value" :key="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="证件号码" style="width: 300px">
        <el-input v-model="info.cert_num" size="small" clearable></el-input>
      </el-form-item>
      <el-form-item label="账号" style="width: 300px">
        <el-input v-model="info.acct_id" size="small" clearable></el-input>
      </el-form-item>
      <el-form-item label="入库时间" style="width: 300px">
        <el-date-picker v-model="info.create_time" size="small" value-format="yyyy-MM-dd" clearable></el-date-picker>
      </el-form-item>
      <el-form-item label="所属服务点" style="width: 300px">
        <el-select v-model="info.site_no" clearable filterable size="small">
          <el-option v-for="item in siteList" :label="item.site_name" :value="item.site_no" :key="item.site_no"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getRiskCustomer(true)"
            size="small"
            icon="el-icon-search"
        >搜索
        </el-button
        >
      </el-form-item>
    </el-form>
    <el-table :data="showData" border>
      <el-table-column label="客户姓名" prop="cust_name" align="center"></el-table-column>
<!--      <el-table-column label="证件类型" prop="cert_type_cd" align="center" sortable>-->
<!--        <template slot-scope="scope" v-if="items">-->
<!--          <span>-->
<!--            {{ computedCertType(scope.row.cert_type_cd) }}-->
<!--          </span>-->
<!--        </template>-->
<!--      </el-table-column>-->
      <el-table-column label="所属服务点" prop="site_name" align="center" sortable></el-table-column>
      <el-table-column label="证件号码" prop="cert_num" align="center" sortable></el-table-column>
      <el-table-column label="账号" prop="acct_id" align="center" sortable></el-table-column>
      <el-table-column label="风险等级" prop="risk_level" align="center" sortable>
        <template slot-scope="scope" v-if="items2">
          <span>
            {{ computedRisklevel(scope.row.risk_level) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="来源" prop="src" align="center" sortable>
        <template slot-scope="scope" v-if="items1">
          <span>
            {{ computedSrc(scope.row.src) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="入库时间" prop="create_time" align="center" sortable></el-table-column>
      <el-table-column label="控制开始日期" prop="set_ctrl_dt" align="center" sortable></el-table-column>
      <el-table-column label="控制截至日期" prop="closing_dt" align="center" sortable></el-table-column>
      <el-table-column label="备注" prop="blklisttype" align="center" sortable></el-table-column>
      <el-table-column label="操作" style="width: 180px" align="center">
        <template slot-scope="scope">
          <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editRiskCustomer(scope.row)"
          >编辑
          </el-button
          >
          <el-button
              type="text"
              icon="el-icon-delete"
              style="color: red"
              @click="deleteRiskCustomer(scope.row.id)"
          >删除
          </el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog :title="form.id ? '修改' : '新增'" :visible.sync="disabled" width="45%"
               :before-close="handleClose" :close-on-click-modal="false">
      <el-form ref="form" :model="form" label-width="100px" label-position="right" :rules="rules">
        <el-form-item
            prop="cust_name"
            label="客户姓名"
            style="width: 360px">
          <el-input type="text" size="small"
                    v-model="form.cust_name">
          </el-input>
        </el-form-item>
        <el-form-item
            prop="cert_type_cd"
            label="证件类型"
            style="width: 360px">
          <el-select
              v-model="form.cert_type_cd" clearable>
            <el-option
                v-for="item in items"
                :label="item.dict_name"
                :value="item.dict_value"
                :key="item.dict_value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
            prop="cert_num"
            label="证件号码"
            style="width: 360px"
        >
          <el-input type="text" size="small" v-model="form.cert_num">
          </el-input>
        </el-form-item>
        <el-form-item
            prop="acct_id"
            label="账号"
            style="width: 360px">
          <el-input type="text" size="small"
                    v-model="form.acct_id">
          </el-input>
        </el-form-item>
        <el-form-item prop="risk_level" label="风险等级" style="width: 360px">
          <el-select
              v-model="form.risk_level" clearable>
            <el-option
                v-for="item in items2"
                :label="item.label"
                :value="item.value"
                :key="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
            prop="set_ctrl_dt"
            label="控制开始日期"
            style="width: 500px">
          <el-date-picker size="small" value-format="yyyy-MM-dd"
                          v-model="form.set_ctrl_dt" type="date"
                          placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item
            prop="closing_dt"
            label="控制截至日期"
            style="width: 400px">
          <el-date-picker size="small" value-format="yyyy-MM-dd"
                          v-model="form.closing_dt" type="date"
                          placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addOrEditRiskCustomer">确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import measure from "../../pages/patrol/measure"

export default {
  components: {
    baseTable,
    basePage,
    measure,
  },
  name: "risk_customer",

  data() {
    return {
      rules: {
        cust_name: [{
          pattern: /^[\u4E00-\u9FA5A-Za-z•]{2,12}$/,
          required: true,
          message: "客户姓名非法",
          trigger: "blur"
        },],
        cert_type_cd: [{
          message: "请选择证件类型",
          trigger: "change"
        },],
        cert_num: [{
          pattern: /^[A-Za-z0-9]{6,20}$/,
          message: "证件号码非法",
          trigger: "blur"
        },
          {
            validator: (rule, value, callback) => {
              let cert_num = this.form.acct_id
              if (cert_num) {
                callback()
              } else {
                if (value) {
                  callback()
                } else {
                  callback(new Error())
                }
              }
            },
            message: '证件号码和账号不能同时为空',
            trigger: 'blur'
          }],
        acct_id: [
          {
            pattern: /^\d+$/,
            message: "账号非法",
            trigger: 'blur'
          },
          {
            validator: (rule, value, callback) => {
              let cert_num = this.form.cert_num
              if (cert_num) {
                callback()
              } else {
                if (value) {
                  callback()
                } else {
                  callback(new Error())
                }
              }
            },
            message: '证件号码和账号不能同时为空',
            trigger: 'blur'
          }], risk_level: [{
          required: true,
          message: "请选择风险等级",
          trigger: "change"
        }], set_ctrl_dt: [{
          required: true,
          message: "请选择控制开始日期",
          trigger: "blur"
        }], closing_dt: [{
          required: true,
          message: "请选择控制截至日期",
          trigger: "blur"
        },],
      },
      siteList:[],
      items: [],
      items1: [
        {
          label: '公安',
          value: '200501'
        }, {
          label: '核心系统',
          value: '999999'
        }, {
          label: '惠农系统',
          value: '100001'
        },
      ],
      items2: [
        {
          label: '高',
          value: '0'
        },
        {
          label: '中',
          value: '1',
        },
        {
          label: '低',
          value: '2',
        },
      ],
      item3: [{
        label: '手动添加',
        value: '0'
      }, {
        label: '电信诈骗（黑名单）',
        value: '1'
      }, {
        label: '电信诈骗（灰名单）',
        value: '2'
      }, {
        label: '7、8类账户',
        value: '3'
      }, {
        label: '反洗钱黑名单',
        value: '6'
      }],
      disabled: false,
      form: {},
      info: {},
      showData: [],
      cust_name: "",
      cert_no: "",
      acct_no: "",
      pageNum: 1,
      pageSize: 10,
      total: 0,
      defaultDate: "2999-12-31"
    }
  },

  methods: {
    getSiteSelectList() {
      this.$api.getSiteSelectList().then((res) => {
        this.siteList = res.data.siteList;
        if (this.siteList != null && this.siteList.length > 0) {
          let defaultProps = {};
          defaultProps.site_name = '全部';
          let site_nos = '';
          this.siteList.map((item) => {

            site_nos += "'" + item.site_no + "',";
          })
          site_nos = site_nos.substr(0,site_nos.length-1)
          defaultProps.site_no = site_nos;
          this.siteList.push(defaultProps)

        }
      })
    },
    computedCertType(params) {

      if (!params) {
        params = '000000'
      }
      let index = this.items.findIndex(function (item) {
        return item.dict_value === params
      })
      return this.items[index].dict_name

    },
    computedSrc(params) {
      if (!params) {
        params = '999999'
      }
      let index = this.items1.findIndex(function (item) {
        return item.value === params
      })

      return this.items1[index].label
    },
    computedRisklevel(params) {

      return this.items2[params].label
    },
    deleteRiskCustomer(id) {
      this.$confirm("确定删除选中的数据吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      }).then(() => {
        this.$api.deleteRiskCustomer({id: id}).then(() => {
          this.getRiskCustomer()
        })
      })

    },
    addOrEditRiskCustomer() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.cert_num && !this.form.cert_type_cd) {
            this.$message.error("请选择证件类型")
            return;
          }
          //修改
          if (this.form.id) {

            this.$api.editRiskCustomer(this.form).then(() => {
              this.form = {}
              this.getRiskCustomer()
              this.handleClose();
            })
            //新增
          } else {
            this.$api.addRiskCustomer(this.form).then(() => {
              this.form = {}
              this.getRiskCustomer()
              this.handleClose();
            })
          }
        }
      })

    },
    editRiskCustomer(obj) {
      this.disabled = true;

      this.form = JSON.parse(JSON.stringify(obj))
    },
    addRiskCustomer() {
      this.disabled = true;
      this.form = {};
    },
    handleClose() {
      this.disabled = false;
      this.form = {};
    },
    getRiskCustomer(flag) {

      let arr = [];
      if (flag) {
        this.pageNum = 1;
      }

      this.$api.getRiskCustomer({
        page: this.pageNum - 1,
        number: this.pageSize,
        ...this.info,
      }).then((res => {
        this.total = res.data.total
        this.items = res.data.certtype
        this.showData = res.data.returnList

      }))
    },
    // 下载模版
    downModal() {
      var aTag = document.createElement("a");
      aTag.download = "风险客户导入模版.xls";
      aTag.href = this.baseURL + "download/risk_customer_import.xls";
      aTag.click();
    },
    // 批量导入Excel
    uploadFile() {
      let input = document.getElementById("file");
      input.value = "";
      input.click();
    },
    chooseFile() {

      let input = document.getElementById("file");
      let file = input.files[0];
      input.value = "";
      this.$api
          .uploadRiskCustomer({
            file: file,
          })
          .then((res) => {
            if (res.fail_count === 0) {
              this.$notify.success(`成功导入${res.success_count}条数据`);
            } else {
              let arr = "";
              arr = arr.substring(0, arr.length - 2);
              this.$notify.info({
                message: `共导入${res.total_count}条数据，${res.success_count}条成功，
              ${res.fail_count}条失败。`,
              });
            }
            this.getRiskCustomer();
          });
    },
    // 修改页面大小
    changeSize(size) {
      this.pageNum = 1;
      this.pageSize = size;
      this.getRiskCustomer();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getRiskCustomer();
    },
    // 导出
    exportRiskCustomerInfos() {

      delete this.form.page;
      delete this.form.number;

      this.$api.exportRiskCustomerInfos(this.form).then((res) => {

        let fileName = res.data.fileName;
        let filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "导出失败，系统异常",
            type: "error",
          });
          return false;
        }

        let objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        let input01 = document.createElement("input");
        let input02 = document.createElement("input");
        let input03 = document.createElement("input");
        let input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "导出成功",
          type: "success",
        });
      });
    },
  },
  mounted() {
    this.getSiteSelectList();
    this.getRiskCustomer();
  }
}
</script>



