<template>
    <div>
        <el-button
                type="primary"
                size="small"
                @click="visible = true"
                style="margin-bottom: 10px"
                v-if="activeName == 'approvalConnect1'"
        >提交沟通
        </el-button
        >
        <el-collapse v-model="activeNames" @change="handleChange">

            <el-collapse-item title="审批历史(点击展开)" name="approvalHis">
                <approval-his
                ></approval-his>
            </el-collapse-item>

            <el-collapse-item title="修改信息(点击展开)" name="updateInfo" v-if="approval_type == type003 || approval_type == type005 || approval_type == type006 || approval_type == type007 || approval_type == type008 || approval_type == type010
                || approval_type == type011 || approval_type == type012 || approval_type == type013 || approval_type == type014 || approval_type == type015 || approval_type == type016 || approval_type == type017 || approval_type == type018 || approval_type == type019">
                <div>
                    <label style="color: red">{{this.approvalApplyDetail.update_items}}</label>
                </div>
            </el-collapse-item>

            <el-collapse-item title="申请详情(点击展开)" name="baseInfo">
                <base-info-staff v-if="approval_type == type002 || approval_type == type003 || approval_type == type014 || approval_type == type015 || approval_type == type021"
                ></base-info-staff>
                <base-info-site v-if="approval_type == type004 || approval_type == type005 || approval_type == type006 || approval_type == type007 || approval_type == type008 || approval_type == type016 || approval_type == type017 || approval_type == type018 || approval_type == type019 || approval_type == type022"
                ></base-info-site>
                <base-info-team v-if="approval_type == type009 || approval_type == type010 || approval_type == type011 || approval_type == type012 || approval_type == type013 || approval_type == type020"
                ></base-info-team>
              <base-info-risk v-if="approval_type == type023">
              </base-info-risk>
            </el-collapse-item>
        </el-collapse>

        <el-dialog
                :title="'提交'"
                :visible.sync="visible"
                width="45%"
                :before-close="handleClose"
        >
            <el-form ref="form" :model="form" :rules="rules" label-width="90px">
                <el-form-item label="沟通意见" prop="approval_desc">
                    <el-input
                            type="textarea"
                            maxlength="300"
                            show-word-limit
                            :autosize="{ minRows: 2, maxRows: 4 }"
                            placeholder="请输入内容，最多300字"
                            v-model="form.approval_desc"
                    ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" style="text-align: center">
                <el-button size="small" @click="handleClose">取 消</el-button>
                <el-button type="primary" size="small" @click="submitApprovalConnect"
                >确 定
                </el-button
                >
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import baseInfoStaff from "../bussiness/addStaff";
    import baseInfoSite from "../bussiness/addSite";
    import baseInfoTeam from "../bussiness/addTeam";
    import approvalHis from "./approvalHis";
    import baseInfoRisk from "../patrol/riskDetail";

    export default {
        components: {
            baseInfoStaff,
            baseInfoSite,
            baseInfoTeam,
            approvalHis,
            baseInfoRisk,
        },
        name: "approvalConnectDetail",
        data() {
            return {
                form: {},
                rules: {
                    approval_desc: [
                        {required: true, message: "请输入沟通意见", trigger: "blur"},
                    ],
                },
                activeNames: ['baseInfo'],
                approval_type: this.$route.query.approval_type,
                apply_id: this.$route.query.apply_id,
                activeName: this.$route.query.activeName,
                relation_id: this.$route.query.id,
                visible: false,
                approvalApplyDetail:{},
                type002: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"002").code,
                type003: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"003").code,
                type004: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"004").code,
                type005: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"005").code,
                type006: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"006").code,
                type007: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"007").code,
                type008: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"008").code,
                type009: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"009").code,
                type010: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"010").code,
                type011: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"011").code,
                type012: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"012").code,
                type013: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"013").code,
                type014: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"014").code,
                type015: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"015").code,
                type016: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"016").code,
                type017: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"017").code,
                type018: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"018").code,
                type019: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"019").code,
                type020: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"020").code,
                type021: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"021").code,
                type022: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType,"022").code,
                type023: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "023").code,
            };
        },
        mounted() {
            this.getUpdateInfo();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            //查询修改项目
            getUpdateInfo() {
                this.$api.getUpdateInfo({
                    apply_id:this.apply_id,
                }).then((res) => {
                    this.approvalApplyDetail = res.data;
                });
            },
            handleChange(val) {
                console.log(val);
            },
            handleClose() {
                this.visible = false;
                this.form = {};
            },

            //提交
            submitApprovalConnect() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$api.submitApprovalConnect({
                            apply_id: this.apply_id,
                            relation_id: this.relation_id,
                            ...this.form,
                        }).then((res) => {
                            this.$router.back(-1);
                        });
                    }
                });
            },
        },
    };
</script>



