<template>
    <div>
        <el-button type="primary" size="small" @click="addOrEditView" style="margin-bottom: 10px">
            活动录入
        </el-button>
        <el-button type="warning" size="small" @click="exportEventInfos">
            导出全部查询数据
        </el-button>
        <el-form :inline="true" :model="form" style="margin-top: 15px">

            <el-form-item label="活动日期" prop="event_start_date">
                <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                                v-model="form.event_start_date" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="-" prop="event_end_date">
                <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                                v-model="form.event_end_date" style="width: 100%;"></el-date-picker>
            </el-form-item>

            <el-form-item label="服务点" prop="site_id">
                <el-select v-model="form.site_id" filterable clearable size="small">
                    <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                               :value="item.site_no"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="客户经理" prop="account_id">
                <el-select v-model="form.account_id" filterable clearable size="small">
                    <el-option v-for="item in sysUserList" :key="item.account_id" :label="item.name"
                               :value="item.account_id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="getEventList" size="small" icon="el-icon-search">搜索
                </el-button>
            </el-form-item>
        </el-form>
        <base-table :list="dataList" :data="eventList">
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button type="text" icon="el-icon-edit-outline" @click="editEvent(scope.row.event_id)">
                        编辑
                    </el-button>
                    <el-button type="text" icon="el-icon-delete" style="color: red"
                               @click="deleteEvent(scope.row.event_id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </base-table>
        <base-page :page="pageNum" :size="pageSize" :total="total" @size="changeSize" @page="changePage"></base-page>

        <el-dialog :title="form.event_id ? '修改' : '新增'" :visible.sync="visible" width="45%"
                   :before-close="handleClose">
            <el-form ref="form" :model="form" :rules="rules" label-width="90px">

                <el-form-item label="主键" prop="event_id" v-show="false">
                    <el-select v-model="form.event_id" filterable clearable size="small">
                        <el-input v-model="form.event_id"></el-input>
                    </el-select>
                </el-form-item>

                <el-form-item label="服务点" prop="site_id" style="width: 360px">
                    <el-select v-model="form.site_id" filterable clearable size="small"
                               :multiple="form.event_id ? false : true">
                        <el-option v-for="item in siteList" :key="item.site_no" :label="item.site_name"
                                   :value="item.site_no"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="活动主题" prop="event_name" style="width: 360px">
                    <el-input v-model="form.event_name"></el-input>
                </el-form-item>

                <el-form-item label="活动日期" prop="event_date" style="width: 360px">
                    <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期"
                                    v-model="form.event_date" style="width: 100%;"></el-date-picker>
                </el-form-item>

                <el-form-item label="活动方式" prop="event_way">
                    <el-radio-group v-model="form.event_way">
                        <el-radio label=0>线上活动</el-radio>
                        <el-radio label=1>线下活动</el-radio>
                        <el-radio label=2>其他</el-radio>
                    </el-radio-group>
                </el-form-item>


                <el-form-item label="现场是否开卡" prop="is_card">
                    <el-radio-group v-model="form.is_card">
                        <el-radio label=0>是</el-radio>
                        <el-radio label=1>否</el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="活动参与人数" prop="event_staff" style="width: 360px">
                    <el-input v-model="form.event_staff"></el-input>
                </el-form-item>

                <el-form-item label="活动效果" prop="event_effect" style="width: 360px">
                    <el-input v-model="form.event_effect"></el-input>
                </el-form-item>

                <el-form-item label="其他" prop="event_other" style="width: 360px">
                    <el-input v-model="form.event_other"></el-input>
                </el-form-item>

                <el-form-item label="活动照片" prop="event_photo">
                    <el-upload
                            action="/hnm/txCtrl?txcode=upload001"
                            list-type="picture-card"
                            ref="upload"
                            :before-upload="beforeAvatarUpload"
                            :file-list="this.fileList"
                            :data="{business: 'event',name: '123'}"
                            :headers='{Accept:"application/json"}'
                            :on-success="uploadReturn"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>

                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>

            </el-form>
            <div slot="footer" style="text-align: center">
                <el-button size="small" @click="handleClose">取 消</el-button>
                <el-button type="primary" size="small" @click="addEvent">确 定
                </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";

    export default {
        components: {
            baseTable,
            basePage,
        },
        name: "event",
        data() {
            return {
                role: sessionStorage.getItem("role"),
                form: {site_id: []},
                dialogImageUrl: '',
                dialogVisible: false,
                fileList: [],
                siteList: [],
                dataList: [{
                    label: "服务点名称",
                    prop: "site_name",
                    sort: true
                },
                    {
                        label: "客户经理",
                        prop: "account_name",
                        sort: true,
                    },
                    {
                        label: "活动日期",
                        prop: "event_date",
                        sort: true,
                    },
                    {
                        label: "活动方式",
                        prop: "event_way",
                        sort: true,
                    },
                    {
                        label: "活动效果",
                        prop: "event_effect",
                    },
                    {
                        label: "活动主题",
                        prop: "event_name",
                    },
                ],
                rules: {
                    site_id: [{
                        required: true,
                        message: "请选择服务点",
                        trigger: "change"
                    },],
                    account_id: [{
                        required: true,
                        message: "请选择客户经理",
                        trigger: "change"
                    },],
                    event_way: [{
                        required: true,
                        message: "请选择活动方式",
                        trigger: "change"
                    },],
                    is_card: [{
                        required: true,
                        message: "请选择现场是否开卡",
                        trigger: "change"
                    },],
                    event_date: [{
                        required: true,
                        message: "请选择活动日期",
                        trigger: "blur"
                    },],
                },
                eventList: [],
                sysUserList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        computed: {
            bankList: {
                get: function () {
                    return this.$store.state.bankList;
                },
                set: function (newVal) {
                    this.$store.state.bankList = newVal;
                },
            },
        },
        mounted() {
            //人员列表
            this.getAccountList();
            //站点列表
            this.getSiteSelectList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            beforeAvatarUpload(file) {
                if(file.type !=='image/png' && file.type !== 'image/jpg' && file.type !== 'image/jpeg') {
                    this.$message.error('不支持png、jpg、jpeg以外格式的图片')
                    return false
                }
                return true
                /*const isLt2M = file.size / 1024  < 300;
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 300KB');
                }
                return isLt2M;*/
            },
            //图片上传成功回调
            uploadReturn(response, file, fileList) {
                console.log(file)
                this.form.event_photo = '';
                fileList.map((item) => {
                    this.form.event_photo += "'" + item.response.data[0].id + "',";
                })
                this.form.event_photo = this.form.event_photo.substr(0, this.form.event_photo.length - 1);
                console.log(this.form.event_photo);
            },
            //删除上传图片
            handleRemove(file, fileList) {
                this.form.event_photo = '';
                fileList.map((item) => {
                    this.form.event_photo += "'" + item.response.data[0].id + "',";
                })
                this.form.event_photo = this.form.event_photo.substr(0, this.form.event_photo.length - 1);
                console.log(this.form.event_photo);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            // 机构下用户列表
            getAccountList() {
                if (this.sysUserList.length != 0) return;
                this.$api.getAccountList({}).then((res) => {
                    this.sysUserList = res.data;
                });
            },
            //站点列表
            getSiteSelectList() {
                this.$api.getSiteSelectList().then((res) => {
                    this.siteList = res.data.siteList;
                });

            },
            // 获取活动列表
            getEventList() {
                this.$api
                    .getEventList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        bankid: this.bankid,
                        site_id: this.form.site_id,
                        account_id: this.form.account_id,
                        event_start_date: this.form.event_start_date,
                        event_end_date: this.form.event_end_date,
                    })
                    .then((res) => {
                        this.eventList = res.data.eventList;
                        this.total = res.data.total;
                        this.eventList.map((item) => {
                            if (item.is_card == "1") {
                                item.is_card = "否";
                            } else if (item.is_card == "0") {
                                item.is_card = "是";
                            }
                            ;

                            if (item.event_way == "0") {
                                return item.event_way = "线上活动";
                            } else if (item.event_way == "1") {
                                return item.event_way = "线下活动";
                            } else if (item.event_way == "2") {
                                return item.event_way = "其他";
                            }
                        });
                        //this.form = {};
                    });
            },
            //打开新增、更新窗口
            addOrEditView() {
                this.visible = true;
                this.form = {};
                this.fileList = [];
            },
            //新增/更新
            addEvent() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.form.event_id) {
                            this.$api.updateEvent(this.form).then((res) => {
                                this.handleClose();
                                this.getEventList();
                            });
                        } else {
                            this.$api.addEvent(this.form).then((res) => {
                                this.handleClose();
                                this.getEventList();
                            });
                        }
                    }
                });
            },
            // 编辑
            editEvent(event_id) {
                this.form = {};
                this.fileList = [];
                this.$api
                    .getEventDetail({
                        event_id,
                    })
                    .then((res) => {
                        this.visible = true;
                        console.log(res);
                        this.form = res.data.msg[0];
                        //图片集合
                        if (this.form.event_photo) {
                            this.$api.getPhotoList({"ids": this.form.event_photo}).then((res) => {
                                this.fileList = res.data.photoList;
                                this.form.event_photo = '';
                                this.fileList.map((item) => {
                                    item.response = {data: [{id: item.name}]};
                                    this.form.event_photo += "'" + item.response.data[0].id + "',";
                                })
                                this.form.event_photo = this.form.event_photo.substr(0, this.form.event_photo.length - 1);
                                console.log(this.form.event_photo);
                            });
                        }

                    });
            },
            // 删除机构
            deleteEvent(event_id) {
                this.$confirm("确定删除吗？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .deleteEvent({
                                event_id: event_id,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getEventList();
                            });
                    })
                    .catch(() => {
                    });
            },
            // 导出
            exportEventInfos() {
                this.$api.exportEventInfos({
                    ...this.form
                }).then((res) => {
                    var fileName = res.data.fileName;
                    var filePath = res.data.filePath;
                    if (null == fileName || null == filePath) {
                        this.$message({
                            message: "导出失败，系统异常",
                            type: "error",
                        });
                        return false;
                    }

                    var objSelector = document.createElement("form");
                    document.body.appendChild(objSelector);
                    objSelector.method = "post";
                    objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
                    //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
                    objSelector.enctype = "multipart/form-data";
                    var input01 = document.createElement("input");
                    var input02 = document.createElement("input");
                    var input03 = document.createElement("input");
                    var input04 = document.createElement("input");
                    input01.setAttribute("name", "filePath");
                    input01.setAttribute("type", "hidden");
                    input01.setAttribute("value", filePath);
                    input02.setAttribute("name", "fileName");
                    input02.setAttribute("type", "hidden");
                    input02.setAttribute("value", fileName);
                    input03.setAttribute("name", "isDownload");
                    input03.setAttribute("type", "hidden");
                    input03.setAttribute("value", "1");
                    input04.setAttribute("name", "isDelete");
                    input04.setAttribute("type", "hidden");
                    input04.setAttribute("value", "1");
                    objSelector.appendChild(input01);
                    objSelector.appendChild(input02);
                    objSelector.appendChild(input03);
                    objSelector.appendChild(input04);
                    objSelector.submit();

                    this.$message({
                        message: "导出成功",
                        type: "success",
                    });
                });
            },


            // 修改页面大小
            changeSize(size) {
                this.pageSize = size;
                this.getEventList();
            },
            // 跳转页码
            changePage(page) {
                this.pageNum = page;
                this.getEventList();
            },

            handleClose() {
                this.visible = false;
                this.form = {};
            },
        },
        // 跳转保存数据
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getEventList();
            });
        },
    };
</script>



