<template>
  <div>
    <el-row :gutter="50">
      <el-col :span="12">
        <el-divider
            content-position="left"
        >团队基本信息
        </el-divider
        >
        <el-form
            ref="form"
            status-icon
            :rules="rules"
            :model="form"
            label-width="120px"
        >
          <el-form-item
              label="团队/片区编号"
              prop="branch_id"
              v-if="
              this.form.id
                ? true
                : false
            "
          >
            <el-input
                v-model="form.branch_id"
                :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="团队/片区名称"
              prop="branch_name"
          >
            <el-input
                v-model="form.branch_name"
                :disabled="check || (approval_type !== type010 && approval_type !== type009)"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="团队/片区类型"
              prop="team_flag"
          >
            <el-radio-group
                v-model="form.team_flag"
                :disabled="check || (approval_type !== type011 && approval_type !== type009)"
            >
              <el-radio label="1"
              >团队
              </el-radio
              >
              <el-radio label="2"
              >片区
              </el-radio
              >
            </el-radio-group>
          </el-form-item>
          <el-form-item
              label="所属机构"
              prop="porgid"
          >
            <div style="display: flex">
              <el-input
                  :disabled="true"
                  v-model="
                  this.form.porgname
                "
              ></el-input>
              <BranchTreePicker
                  @nodeselect="
                  nodeselectForShow
                "
                  :dfks="this.form.porgid"
                  v-show="!check && (approval_type === type012 || approval_type === type009)"
                  :appendToBody="true"
              ></BranchTreePicker>
            </div>
          </el-form-item>
          <el-form-item
              label="成立日期"
              prop="generate_date"
          >
            <el-date-picker
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期"
                v-model="
                form.generate_date
              "
                style="width: 100%"
                :disabled="check || (approval_type !== type010 && approval_type !== type009)"
            ></el-date-picker>
          </el-form-item>
          <el-form-item
              label="团队/片区负责人"
              prop="team_leader"
          >
            <el-select
                style="width: 100%"
                :disabled="check || (approval_type !== type013 && approval_type !== type009)"
                v-model="form.team_leader"
                clearable
                filterable
            >
              <el-option
                  v-for="item in sysUserList"
                  :key="item.account_id"
                  :label="
                  item.account_id +
                  '-' +
                  item.name
                "
                  :value="item.account_id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
              label="团队人数"
              prop="team_num"
              v-if="form.team_flag == '1'"
          >
            <el-input
                v-model="form.team_num"
                :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="团队数"
              prop="team_count"
              v-if="form.team_flag == '2'"
          >
            <el-input
                v-model="form.team_count"
                :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="片区人数"
              prop="area_num"
              v-if="form.team_flag == '2'"
          >
            <el-input
                v-model="form.area_num"
                :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="其他"
              prop="remarks"
          >
            <el-input
                :disabled="check || (approval_type !== type010 && approval_type !== type009)"
                type="textarea"
                :autosize="{
                minRows: 2,
                maxRows: 4,
              }"
                placeholder="请输入内容"
                v-model="form.remarks"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="审批人"
              v-if="
              !check &&
              this.use_up === '1' && req_place !=='1'
            "
              prop="approval_user"
          >
            <el-select
                style="width: 100%"
                v-model="
                form.approval_user
              "
                :disabled="check"
                clearable
                filterable
                @click.native="getApprovalAccountList"
            >
              <el-option
                  v-for="item in accountList"
                  :key="item.account_id"
                  :label="
                  item.approval_name
                "
                  :value="item.account_id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button
                id="tempButton"
                v-if="!check && approval_type === type009 && req_place !=='1'"
                type="primary"
                size="medium"
                @click="tempSubmit"
            >{{ "暂存" }}
            </el-button>
            <el-button
                id="editButton"
                v-if="!check"
                type="primary"
                size="medium"
                @click="open"
            >{{ "提交" }}
            </el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider
            content-position="left"
        >团队业绩数据
        </el-divider
        >
        <el-row class="table-item" v-for="(item, index) in depositList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({branch_id:form.branch_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider
            content-position="left"
        >团队客户数据
        </el-divider
        >
        <el-row class="table-item" v-for="(item, index) in teamCusList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({branch_id:form.branch_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
      <el-col v-if="check && req_place ==='0'" :span="12">
        <el-divider
            content-position="left"
        >团队拓展数据
        </el-divider
        >
        <el-row class="table-item" v-for="(item, index) in teamExpandList" :key="item.key">
          <el-col :span="8">{{ item.label }}</el-col>
          <el-col :span="8">{{ form[item.key] }}</el-col>
          <el-col :span="8">
            <el-button type="text" @click="showChart({branch_id:form.branch_id,query_item:item.key})">查看视图
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <show-chart @get-data="childSearch" :option="option" ref="chartShow"></show-chart>

    <el-row v-if="check && req_place ==='0'" :gutter="50">
      <el-col :span="24">
        <el-divider direction="horizontal" content-position="center">
          审批记录
        </el-divider>
        <base-table :list="hisLogList" :data="approvalHisLogList">
          <el-table-column fixed="right" label="操作" align="center" width="240">
            <template slot-scope="scope">
              <el-button type="text" @click.native="showHisDetail(scope.row.id)">详情</el-button>
            </template>
          </el-table-column>
        </base-table>
      </el-col>
    </el-row>

    <el-dialog
        :title="'审批历史'"
        :visible.sync="his_visible"
        width="80%"
        :before-close="handleHisClose"
    >

      <base-table :list="hisList" :data="approvalHisList">
      </base-table>

      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleHisClose">关 闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import BranchTreePicker from "../../components/BranchTreePicker";
import addMixin from "@/mixins/addMixin.js";
import showChart from './components/showChart.vue'
import baseTable from "../../components/Table";

export default {
  mixins: [addMixin],
  components: {
    BranchTreePicker,
    showChart,
    baseTable
  },
  name: "addTeam",
  data() {
    return {
      depositList: [ //团队业绩数据
        {label: '资产余额', key: 'amt1'},
        {label: '资产年日均', key: 'amt2'},
        {label: '存款余额', key: 'amt3'},
        {label: '存款年日均', key: 'amt4'},
      ],
      teamCusList: [// 团队客户数据
        {label: '所有客户数', key: 'cus_num1'},
        {label: '高净值客户数', key: 'cus_num2'},
        {label: '价值客户数', key: 'cus_num3'},
        {label: '有效客户数', key: 'cus_num4'},
      ],
      teamExpandList: [ // 团队拓展数据
        {label: '试营业服务点数', key: 'site_num1'},
        {label: '试营业（已申请POS）服务点数', key: 'site_num2'},
        {label: '已开业服务点数', key: 'site_num3'},
        {label: '已退出服务点数', key: 'site_num4'},
      ],
      form: {
        suggestion: "",
      },
      teamList: [],
      check: false,
      bankList: [],
      //leaderList: [],
      accountList: [],
      sysUserList: [],
      type009: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "009").code,
      type010: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "010").code,
      type011: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "011").code,
      type012: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "012").code,
      type013: this.$loacalData.code_GetEnums(this.$loacalData.listApprovalType, "013").code,
      use_up: "",
      req_place: "",
      parms: {},

      hisLogList: [
        {
          label: "申请日期",
          prop: "create_time",
          sort: true,
        },
        {
          label: "申请编号",
          prop: "id",
        },
        {
          label: "审批类型",
          prop: "approval_type_name",
        },
        {
          label: "审批状态",
          prop: "approval_status_name",
        },
      ],
      approvalHisLogList: [],

      hisList: [
        {
          label: "时间",
          prop: "create_time",
          sort: true,
        },
        {
          label: "节点名称",
          prop: "role_name",
        },
        {
          label: "操作人员编号",
          prop: "approval_user",
        },
        {
          label: "操作人员姓名",
          prop: "name",
        },
        {
          label: "操作",
          prop: "approval_opinion",
        },
        {
          label: "处理意见",
          prop: "approval_desc",
        },
      ],
      approvalHisList: [],
      his_visible: false,

    };
  },
  mounted() {
    this.check =
        this.$route.query.check ===
        "false"
            ? false
            : Boolean(
            this.$route.query.check
            );
    this.id = this.$route.query.id;
    this.req_place = this.$route.query.req_place || '0';
    if (this.id) {
      this.$api
          .getTeamDetail({
            id: this.id,
          })
          .then((res) => {
            this.form =
                res.data.teamDetail;
            console.log(this.form);
            this.baseForm = JSON.parse(
                JSON.stringify(this.form)
            );
          });
    } else {
      //查询是否有暂存
      this.$api
          .getTempTeamDetail({})
          .then((res) => {
            if (res.data.teamDetail) {
              this.form = res.data.teamDetail;
            }
          });
    }
    //人员列表
    this.getAccountList();
    //保存（首次提交）时查询审批角色对应审批人员
    // if (!this.check) {
    //   this.getApprovalAccountList();
    // }

      //查询是否启用
      if (!this.check) {
          this.getApprovalQY();
      }

    if (this.check && this.req_place === '0') {
      this.getApprovalHisLogList(this.id);
    }
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = {};
      }
    },
  },
  computed: {
    approval_type() {
      return (
          this.$route.query
              .approval_type || this.type009
      );
    },
    rules() {
      return {
        branch_name: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type009 || this.$route.query.approval_type == this.type010,
            message:
                "请输入团队/片区名称",
            trigger: "blur",
          },
        ],
        team_flag: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type009 || this.$route.query.approval_type == this.type011,
            message:
                "请选择团队/片区类型",
            trigger: "change",
          },
        ],
        porgid: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type009 || this.$route.query.approval_type == this.type012,
            message: "请选择所属机构",
            trigger: "change",
          },
        ],
        generate_date: [
          {
            required: this.$route.query.approval_type == undefined || this.$route.query.approval_type == this.type009 || this.$route.query.approval_type == this.type010,
            message: "请选择成立日期",
            trigger: "blur",
          },
        ],
        approval_user: [
          {
            required: true,
            message: "请选择审批人",
            trigger: "change",
          },
        ],
      }
    },
  },
  methods: {

    // 审批记录列表
    getApprovalHisLogList(id) {
      this.$api.getApprovalHisLogList({
        "req_type": "1",
        "id": id,
      }).then((res) => {
        this.approvalHisLogList = res.data;
      });
    },
    showHisDetail(id) {
      this.approvalHisList = [];
      this.$api.getApprovalHisList({
        apply_id: id
      }).then((res) => {
        this.his_visible = true;
        this.approvalHisList = res.data;
      });
    },
    handleHisClose() {
      this.his_visible = false;
    },

    open() {
      this.$prompt('', '申请说明', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputType: 'textarea',
        inputPattern: /^(.|\n){1,300}$/,
        inputErrorMessage: '申请说明不能为空且不能超过300字',
        inputPlaceholder: '输入内容不能超过300字',
      }).then(({value}) => {
        this.form.suggestion = value
        this.onSubmit()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消提交'
        });
      });

    },


    getThanData(params) {
      this.parms.query_date = params.name;
      let parms = this.parms;
      let query_item = this.parms.query_item;
      if ("amt1" === query_item || "amt2" === query_item || "amt3" === query_item || "amt4" === query_item) {
        this.$api
            .getReport001List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      } else if ("cus_num1" === query_item || "cus_num2" === query_item || "cus_num3" === query_item || "cus_num4" === query_item) {
        this.$api
            .getReport002List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      } else if ("site_num1" === query_item || "site_num2" === query_item || "site_num3" === query_item || "site_num4" === query_item) {
        this.$api
            .getReport003List(parms)
            .then((res) => {
              this.tooltip = `<div style="padding: 0 10px">
                                        <h5><span>时点值:</span><span>${res.data[query_item]}</span></h5>
                                        <h5><span>比上日:</span><span>${res.data.than_last_day}</span></h5>
                                        <h5><span>比上月:</span><span>${res.data.than_last_month}</span></h5>
                                        <h5><span>比年初:</span><span>${res.data.than_last_year}</span></h5>
                                      </div>
                                      `
            });
      }
    },
    childSearch(params) {
      this.parms.query_date = params.query_date;
      this.getData(this.parms)
    },
    getData(parms) {
      this.parms = parms;
      this.$api
          .getReport004List(parms)
          .then((res) => {
            this.option.xAxis.data = res.data.dateList;
            this.option.series[0].data = res.data.dataList;
          });
    },
    // 机构列表
    nodeselectForShow(node) {
      console.log(node);
      this.$set(
          this.form,
          "porgname",
          node.branch_name
      );
      this.$set(
          this.form,
          "porgid",
          node.branch_id
      );
    },
    // 机构下用户列表
    getAccountList() {
      if (this.sysUserList.length != 0)
        return;
      this.$api
          .getAccountList({})
          .then((res) => {
            this.sysUserList = res.data;
          });
    },
    // 保存（首次提交）时查询审批角色对应审批人员
    getApprovalAccountList() {
      this.$api
          .getApprovalAccountList({
            approval_type: this.approval_type  || this.type009,
            ...this.form,
          })
          .then((res) => {
            this.accountList =
                res.data.dataList;
            //this.use_up = res.data.use_up;
            // if (
            //     res.data.dataList.length ==
            //     1
            // ) {
            //     this.$set(
            //         this.form,
            //         "approval_user",
            //         res.data.dataList[0]
            //             .account_id
            //     );
            // }
          });
    },

      // 查询是否启用
      getApprovalQY() {
          this.$api
              .getApprovalQY({
                  approval_type: this.$route.query.approval_type || this.type009,
              })
              .then((res) => {
                  this.use_up = res.data.use_up;
              });
      },

    //暂存
    tempSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$api
          .tempaddTeam(this.form)
          .then((res) => {
            //this.handleClose();
            this.$message.success(
                "暂存成功"
            );
            //this.$router.back(-1);
          });
    },

    onSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$refs.form.validate(
          (valid) => {
            if (valid) {
              this.form.approval_type = this.approval_type;
              this.form.req_place = this.req_place;
              this.form.apply_id = this.$route.query.apply_id || "";
              this.form.use_up = this.use_up;
              let update_items = [];
              for (let i in this
                  .baseForm) {
                this.baseForm[i] !=
                this.form[i] &&
                update_items.push(
                    this.labelList[i]
                );
              }
              if (
                  update_items.length > 0
              ) {
                this.form.update_items = update_items.join(
                    ","
                );
              }
              this.$api
                  .addTeam(this.form)
                  .then((res) => {
                    this.handleClose();
                    this.$message.success(
                        "操作成功，你已跳转至我的申请页面"
                    );
                    this.$router.push({
                      path:
                          "/approvalApply",
                      name: "我的申请",
                    });
                    //this.$router.back(-1);
                  });
            }
          }
      );
    },

    handleClose() {
      this.visible = false;
      this.form = {};
    },
  },
  // 跳转保存数据
  beforeRouteLeave(to, from, next) {
    to.params.searchKey = this.$route.params.searchKey;
    next();
  },
};
</script>



